import { Injectable } from '@angular/core';
import { OnboardingStateMachineService } from '../onboarding/onboarding-state-machine.service';
import { StateDataService } from '../api/state-data.service';
import { ApiService } from '../api/api.service';
import { ActivationService } from './activation.service';
import { PluginService } from '../api/plugin.service';

@Injectable()
export class LoanService {

    private currentOffer: any = null;

    constructor(
        private stateMachine: OnboardingStateMachineService,
        private stateData: StateDataService,
        private api: ApiService,
        private plugin: PluginService,
        private activation: ActivationService
    ) {
    }

    public async returnToOffers() {
        await this.cancelLoanApplication();
        this.stateMachine.incrementPage();
    }

    public async clearCurrentOffer() {
        await this.stateData.set(StateDataService.KEYS.CURRENT_OFFER, null);
        this.currentOffer = null;
    }

    public async goToOffer() {
        await this.retrieveOffer();
        const isActivated = await this.activation.isActivated();
        this.goToLoanOnboarding();
        if (!isActivated) {            
            if (this.stateMachine.getCurrentPage() === this.stateMachine.states.LOAN_APPLICATION_PROCESS) {
                const status = await this.activation.activate();
                if (status === ActivationService.STATUS.ACTIVATED) {
                    // During device activation the Activity stack gets reordered
                    // due to the Device Administrator, KLMS, and PJB screens. So
                    // we need to relaunch the in-app browser upon activation
                    this.stateData.setAppMode('lock');
                } else if (status === ActivationService.STATUS.REJECTED) {
                    await this.cancelLoanApplication();
                    this.stateMachine.closeApp();
                }
            }
        }
    }

    public async getOfferData(field?: string) {
        await this.retrieveOffer();
        if (!field) {
            return this.currentOffer;
        }
        if (this.currentOffer && this.currentOffer[field] !== undefined) {
            return this.currentOffer[field];
        }
        return null;
    }

    public async getPageContentUrl() {
        await this.retrieveOffer();
        if (!this.currentOffer) {
            return null;
        }
        const userData = {
            userId: await this.stateData.get(StateDataService.KEYS.USER_ID),
            deviceTag: await this.stateData.get(StateDataService.KEYS.DEVICE_TAG),
            locale: await this.stateData.get(StateDataService.KEYS.LOCALE),
            offerKey: await this.getOfferData('partnerOfferKey'),
            product: await this.stateData.get(StateDataService.KEYS.APP_MODE),
            channelType: await this.stateData.get(StateDataService.KEYS.CHANNEL_TYPE)
        };

        const onboardingUrl = await this.getOfferData('onboardingUrl');
        return onboardingUrl + '?userData=' + JSON.stringify(userData);
    }

    public async setOffer(offer) {
        this.currentOffer = offer;
        await this.stateData.set(StateDataService.KEYS.CURRENT_OFFER, JSON.stringify(offer));
    }

    public async goToLoanOnboarding() {
        this.stateMachine.updateCurrentPage(this.stateMachine.states.LOAN_APPLICATION_PROCESS);
        // ToDo: Uncomment incrementPage() when Loan Application pages are added server-side state-machine
        // this.stateMachine.incrementPage()
    }

    public async cancelLoanApplication() {
        const payload = { userId: await this.stateData.get(StateDataService.KEYS.USER_ID) };
        try {
            const response = await this.api.post('/loans/v2/api/cancel-loan-application.php', payload);
            const success = response['valid'];
            if (success) {
                this.clearCurrentOffer();
                return true;
            } else {
                this.stateMachine.setError(
                    'Error canceling loan application: ' + response['message'],
                    StateDataService.ERROR_CODES.CANCEL_LOAN_APPLICATION);
            }
        } catch (error) {
            this.stateMachine.setError(
                'Error occurred on post cancel-loan-application: ' + error,
                StateDataService.ERROR_CODES.POST_CANCEL_LOAN_APPLICATION);
        }
    }

    private async retrieveOffer() {
        if (!this.currentOffer) {
            const jsonOffer = await this.stateData.get(StateDataService.KEYS.CURRENT_OFFER);
            if (jsonOffer) {
                this.currentOffer = JSON.parse(jsonOffer);
            }
        }
    }


}
