import { Injectable, NgZone } from '@angular/core';
import { Events, Platform } from '@ionic/angular';

declare var cordova: any;

@Injectable({
    providedIn: 'root'
})

export class DeviceBridgeService {

    public static readonly EVENT_KEYS = {};

    constructor(
        private ngZone: NgZone,
        private platform: Platform,
        public events: Events,) {
    }

    public subscribe(event, handler) {
        return this.platform.ready().then(() => {
            console.log('DeviceBridge subscribed to: ' + event);
            this.events.subscribe(event, handler);
            cordova.plugins.DeviceBridge.subscribe(result => {
                this.ngZone.run(() => {
                    const data = JSON.parse(result);
                    this.events.publish(data.event, { status: 'success', data });
                });
            }, error => {
                this.ngZone.run(() => {
                    console.log('DeviceBridge error: ', error);
                    this.events.publish(error.event, { status: 'error', message: error })
                });
            }, event);
        });
    }

    public unsubscribe(event) {
        return this.platform.ready().then(() => {
            console.log('DeviceBridge unsubscribe');
            this.events.unsubscribe(event);
            return new Promise<string>((resolve, reject) => {
                cordova.plugins.DeviceBridge.unsubscribe(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    });
            });
        });
    }
}
