import { NgModule } from '@angular/core';
import { TranslateLoader } from '@ngx-translate/core';
import { ComponentsModule } from '@app/components/components.module';
import { SelfieComponent } from '../onboarding/selfie/selfie.component';
import { PhoneVerifyComponent } from '../onboarding/phone-verify/phone-verify.component';
import { ConfirmDetailsComponent } from '../onboarding/confirm-details/confirm-details.component';
import { IdPhotosComponent } from '../onboarding/id-photos/id-photos.component';
import { ImageInputComponent } from './image-input/image-input.component';
import { AndroidPermissionsComponent } from '../onboarding/android-permissions/android-permissions.component';
import { DeclineComponent } from '../onboarding/decline/decline.component';
import { TransitionComponent } from '../onboarding/transition/transition.component';
import { AddressComponent } from '../onboarding/address/address.component';
import { HelpComponent } from '../help/help.component';
import { AwaitManualReviewComponent } from '../await-manual-review/await-manual-review.component';
import { BrowserModule } from '@angular/platform-browser';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

export function createTranslateLoader(http: HttpClient) {
    return new TranslateHttpLoader(http, 'assets/i18n/', '.json');
}

@NgModule({
    declarations: [
        SelfieComponent,
        PhoneVerifyComponent,
        ConfirmDetailsComponent,
        IdPhotosComponent,
        ImageInputComponent,
        AndroidPermissionsComponent,
        DeclineComponent,
        TransitionComponent,
        HelpComponent,
        AwaitManualReviewComponent,
        AddressComponent
    ],
    imports: [
        BrowserModule,
        FormsModule,
        IonicModule,
        HttpClientModule,
        TranslateModule.forRoot({
                loader: {
                    provide: TranslateLoader,
                    useFactory: (createTranslateLoader),
                    deps: [HttpClient]
                }
            }
        ),
        CommonModule,
        ComponentsModule,
    ],
    exports: [
        SelfieComponent,
        PhoneVerifyComponent,
        ConfirmDetailsComponent,
        IdPhotosComponent,
        ImageInputComponent,
        AndroidPermissionsComponent,
        DeclineComponent,
        TransitionComponent,
        HelpComponent,
        AwaitManualReviewComponent
    ],
    providers: [],
    bootstrap: [],
})
export class OnboardingModule {
    constructor() {
    }

}