import { ApiService } from '../api/api.service';
import { Injectable } from '@angular/core';
import { LoadingController, Platform } from '@ionic/angular';
import { PluginService } from '../api/plugin.service';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StateDataService } from '../api/state-data.service';
import { StateMachineService } from '../api/state-machine.service';
import { from, of , Subject } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { delay, mergeMap, repeat } from 'rxjs/operators';
import { Network } from '@ionic-native/network/ngx';
import { environment } from '@app/env';

@Injectable()
export abstract class OnboardingStateMachineService extends StateMachineService {

    public static AVAILABILITY_STATUS_TYPES = {
        AVAILABLE: 'loansAvailable',
        OVER_DAILY_LIMIT: 'overDailyLoanLimit',
        COUNTRY_NOT_SUPPORTED: 'loansNotAvailableInCountry'
    };
    public states;
    public subStates;
    public tasks;
    private nextButton = new Subject < number > ();
    public $nextButton = this.nextButton.asObservable();
    private counter = 0;
    private clearButton = new Subject < number > ();
    public $clearButton = this.clearButton.asObservable();
    private clearCounter = 0;
    private nextButtonEnabled;
    private displayReturnToLock = false;
    // Initial availability checks
    private quotaReached = false;
    private marketSupported = true;
    private offersFound = false;
    public serviceConfig = null;
    private readonly BUNDLE_ROUTER_URL = `${environment.API_BASE_URL}/cash-app-bundle/`;


    constructor(
        protected api: ApiService,
        protected loadingController: LoadingController,
        protected network: Network,
        protected platform: Platform,
        protected plugin: PluginService,
        protected splashscreen: SplashScreen,
        protected stateData: StateDataService,
        protected translate: TranslateService
    ) {
        super(api, loadingController, network, platform, plugin, stateData, translate);
        this.nextButtonEnabled = false;
        this.states = {
            HOME: 'home',
            PHONE: 'phone',
            ID: 'id',
            ID_VERIFY: 'id-verify',
            ID_REJECTED: 'id-rejected',
            SELFIE: 'selfie',
            ANDROID_PERMISSIONS: 'permissions',
            AWAIT_MANUAL_REVIEW: 'await-manual-review',
            LOAN_APPLICATION_IN_PROGRESS: 'loan-application-in-progress',
            LOAN_APPLICATION_PROCESS: 'loan-application-process',
            DECLINE: 'decline',
            POST_MERGE_DECLINE: 'post-merge-decline',
            OFFER: 'offer',
            ERROR: 'error',
            COMPLETE: 'complete',
        };
        // SUB_STATES are needed because some of our states actually represent
        // 2 or more distinct states
        this.subStates = {
            PERMISSIONS_GIVEN: 'permissions-given',
            REQUESTING_MANUAL_REVIEW: 'loanApplicationRequesting-manual-review',
            LOAN_APPLICATION_INTRO: 'loanApplicationIntro',
            LOAN_APPLICATION_TRANSFER: 'loanApplicationTransfer',
            LOAN_APPLICATION_WINDOW_PICKUP: 'loanApplicationWindowPickup',
            LOAN_APPLICATION_QUESTIONS: 'loanApplicationQuestions',
            LOAN_APPLICATION_OFFER: 'loanApplicationOffer',
            LOAN_APPLICATION_TRANSITION: 'loanApplicationTransition',
            LOAN_APPLICATION_CONTRACT: 'loanApplicationContract',
            LOAN_APPLICATION_WALMART_DISBURSEMENT: 'loanApplicationWalmartDisbursement',
        };
        this.tasks = {};
    }

    public async initialize() {
        super.initialize();

        if (await this.stateData.get(StateDataService.KEYS.CURRENT_STATE) &&
            await this.isLoggedIn()) {
            await this.incrementPage();
        } else {
            this.goToBeginning();
        }
    }

    abstract goToBeginning();

    abstract userAwaitingAction();

    abstract explainPermissions();

    abstract isCashLoan(): boolean;

    public addPadding() {
        return false;
    }

    public async isLoggedIn() {
        return true;
    }

    public isScoringNeeded() {
        return true;
    }

    public isQuotaReached() {
        return this.quotaReached;
    }

    public isMarketSupported() {
        return this.marketSupported;
    }

    public isInIdUploadState() {
        return (
            this.getCurrentPage() === this.states.ID ||
            this.getCurrentPage() === this.states.ID_REJECTED
        );
    }

    public isHomeScreen() {
        return this.getCurrentPage() === this.states.HOME;
    }


    public isOnboardingComplete() {
        return this.getCurrentPage() === this.states.COMPLETE;
    }

    public displayingFoundOffers() {
        return (this.onOffersComponent() && this.offersFound && !this.isTransitioning());
    }

    public onOffersComponent() {
        return (this.getCurrentPage() === this.states.OFFERS);
    }


    public foundOffers() {
        this.offersFound = true;
        this.endTransition();
    }

    public async getServiceConfig() {
        const uuid = await this.stateData.get(StateDataService.KEYS.DEVICE_UUID);
        const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);

        const parameters = {
            uuid: uuid,
            deviceTag: deviceTag
        };

        const response = await this.api.get(
            '/loans/v2/api/get-service-config.php', parameters);
        const success = response['valid'];
        const status = response['message'];
        if (!success) {
            this.api.logError(StateDataService.ERROR_CODES.GET_SERVICE_CONFIG_ERROR, 'error getting service config status: ' + status);
            return false;
        }
        this.serviceConfig = response['data'].serviceConfig;
        return true;
    }

    public async checkLoanAvailability() {
        const parameters = {
            country: await this.stateData.get(StateDataService.KEYS.MARKET), 
            product: await this.stateData.get(StateDataService.KEYS.APP_MODE),
        };

        const response = await this.api.get(
            '/loans/v2/api/loan-availability.php', parameters);

        const success = response['valid'];
        try {
            const status = response['message'];

            if (success) {
                this.quotaReached = false;
                this.marketSupported = true;
            } else {
                if (status === OnboardingStateMachineService.AVAILABILITY_STATUS_TYPES.OVER_DAILY_LIMIT) {
                    this.quotaReached = true;
                } else if (status === OnboardingStateMachineService.AVAILABILITY_STATUS_TYPES.COUNTRY_NOT_SUPPORTED) {
                    this.marketSupported = false;
                } else {
                    this.setError(
                        'checkLoanAvailability Server returned unsupported type ' + status,
                        StateDataService.ERROR_CODES.CHECK_LOAN_AVAILABILITY_UNSUPPORTED_TYPE);
                }
            }
        } catch (error) {
            this.setError(error, StateDataService.ERROR_CODES.CHECK_LOAN_AVAILABILITY);
        }
    }


    public async incrementPage() {
        if (await this.isLoggedIn() || this.isConnectWithoutFacebookButtonOn()) {
            return this.getNextState();
        } else {
            return this.goToBeginning();
        }
    }

    public pollIncrementPage(pollDelay = 1000) {
        // of({}): create an empty observable
        // pipe(): a sequential stream of operators performed one after the other
        // mergeMap(): not sure... I think in this context it will call observerIncrementPage()
        //             and subscribe to the observable, which, in this case triggers a single
        //             call to next-state.php
        // delay(): after the mergeMap operators have emitted (i.e. after next-state.php returns), wait 1 second
        // repeat(): repeat the whole pipe of operators
        return of({}).pipe(
            mergeMap(() => this.observeIncrementPage()),
            delay(pollDelay),
            repeat()
        );
    }

    public async goToLock() {
        const userId = await this.stateData.get(StateDataService.KEYS.USER_ID);
        let success = true;

        try {
            if (userId) {
                const response = await this.api.post('/loans/v2/api/close-onboarding-profile.php', { userId: String(userId) });
                success = response['valid'];

                if (!success) {
                    this.setError(
                        'error occured closing profile: ' + response['message'],
                        StateDataService.ERROR_CODES.CLOSE_ONBOARDING_PROFILE);
                    return null;
                }
            }

            this.api.logEvent('AwaitingLock', { currentPage: this.getCurrentPage() });
            await this.stateData.setAppMode('lock');

        } catch (err) {
            this.setError(
                'Error occurred on post close-onboarding-profile: ' + err,
                StateDataService.ERROR_CODES.POST_CLOSE_ONBOARDING_PROFILE);
        }
    }

    public isDeclinedState() {
        return this.getCurrentPage() === this.states.DECLINE ||
            this.getCurrentPage() === this.states.POST_MERGE_DECLINE ||
            this.isQuotaReached() ||
            !this.isDeviceSupported() ||
            !this.isMarketSupported();
    }

    public isReturnToLockDisplayed() {
        return this.displayReturnToLock;
    }

    public setDisplayReturnToLock(val) {
        this.displayReturnToLock = val;
    }

    public isNextButtonEnabled() {
        return this.nextButtonEnabled;
    }

    public enableNext() {
        this.nextButtonEnabled = true;
    }

    public disableNext() {
        this.nextButtonEnabled = false;
    }

    public fireNext() {
        this.counter += 1;
        this.nextButton.next(this.counter);
    }

    public fireClear() {
        this.clearCounter += 1;
        this.clearButton.next(this.clearCounter);
    }

    public isFooterHidden() {
        if (!this.hasInternetConnection()) {
            return true;
        } else if (this.hasError()) {
            return false;
        } else {
            return (this.isHelpNeeded() ||
                this.isTransitioning() ||
                this.isLoading());
        }
    }

    public isConnectWithoutFacebookButtonOn() {
        if (this.serviceConfig) {
            return this.serviceConfig.showConnectWithoutFacebookButton;
        }
        return false;
    }

    public isFacebookLoginButtonOn() {
        if (this.serviceConfig) {
            return this.serviceConfig.showFacebookLoginButton;
        }
        return false;
    }

    public getTasks(status) {
        const tasks = [];
        for (const task in this.tasks) {
            if (this.tasks[task] === status) {
                tasks.push(task);
            }
        }
        return tasks;
    }

    public markTaskComplete(task) {
        this.tasks[task] = 1;
        this.stateData.set(task, 1);
    }

    public requestingManualReview() {
        return (
            this.getCurrentPage() === this.states.ID_VERIFY &&
            this.getCurrentSubPage() === this.subStates.REQUESTING_MANUAL_REVIEW)
    }

    protected addStates(states) {
        this.states = {
            ...this.states,
            ...states
        };
    }

    protected addSubStates(subStates) {
        this.subStates = {
            ...this.subStates,
            ...subStates
        };
    }

    protected initializeTasks(taskNames) {
        this.tasks = {};
        for (const task of taskNames) {
            this.tasks[task] = 0;
        }
    }

    protected resetTasks() {
        this.initializeTasks(Object.keys(this.tasks));
    }

    protected async updateTasks() {
        for (const task in this.tasks) {
            if (await this.stateData.get(task)) {
                this.markTaskComplete(task);
            }
        }
    }

    private async validateBundleVersion() {
        if (!environment.production) {
            return;
        }

        const statePacket = await this.stateData.getStatePacket();
        const expectedBundleVersion = statePacket['expectedBundleVersion'];
        const currentBundleVersion = await this.stateData.get(StateDataService.KEYS.APP_BUNDLE_VERSION);

        if (expectedBundleVersion && expectedBundleVersion !== currentBundleVersion) {
            const versionCode = await this.stateData.get(StateDataService.KEYS.APP_VERSION_CODE);
            const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);
            const targetUrl = `${this.BUNDLE_ROUTER_URL}?versionCode=${versionCode}`;
            const logData = {
                deviceTag,
                versionCode,
                currentBundleVersion,
                expectedBundleVersion,
            };
            this.api.logEvent('Onboarding forced bundle upgrade', logData);
            window.location.replace(targetUrl);
        }
    }

    protected observeIncrementPage() {
        return from(this.incrementPage());
    }

    private async getNextState() {
        const hasPermissions = await this.plugin.hasPermissions();
        if (hasPermissions === '0') {
            await this.stateData.set(StateDataService.KEYS.PERMISSIONS_GIVEN, 0);
        }
        await this.stateData.set(StateDataService.KEYS.CURRENT_STATE, this.getCurrentPage());

        try {
            const statePacket = await this.api.getStatePacket();
            await this.stateData.setStatePacket(statePacket);
            this.setDisplayReturnToLock(statePacket['allowReturnToLock']);
            this.updateCurrentPage(statePacket['nextState']);
            this.closeLoading();

            if (this.isOnboardingComplete()) {
                this.goToLock();
            } else {
                this.validateBundleVersion();
            }
        } catch (error) {
            this.setError(
                'Error occurred on post getNextState: ' + error,
                StateDataService.ERROR_CODES.POST_NEXT_STATE);
        }
    }

}