import { ApiService } from './api.service';
import { Injectable } from '@angular/core';
import { LoadingController, Platform } from '@ionic/angular';
import { PluginService } from './plugin.service';
import { StateDataService } from './state-data.service';
import { TranslateService } from '@ngx-translate/core';
import { Network } from '@ionic-native/network/ngx';

@Injectable()
export class StateMachineService {
    private currentPage = null;
    private currentSubPage = null;
    private loading;
    private menuOpen = false;

    private error = false;
    private currentErrorCode: any = 1000;
    private transitioning = false;

    private deviceSupported = null;
    private helpNeeded = false;

    private cashPlatformProfileId = null;

    private isOnline = navigator.onLine;
    private market = null; 


    constructor(
        protected api: ApiService,
        protected loadingController: LoadingController,
        protected network: Network,
        protected platform: Platform,
        protected plugin: PluginService,
        protected stateData: StateDataService,
        protected translate: TranslateService

    ) {
        this.loading = false;
    }

    static parseMessage(response) {
        return response.message ? response.message : 'no error message in response'
    }

    public initialize() {
        this.platform.ready().then(async () => {
            this.api.registerAppInstall();

            this.network.onDisconnect().subscribe(() => {
                this.onNetworkDisconnected();
            });
            this.network.onConnect().subscribe(() => {
                this.onNetworkConnected();
            });

            this.endTransition();
        });
    }


    public updateCurrentPage(newState): void {
        console.log('update current page: ' + newState);
        this.stateData.set(StateDataService.KEYS.CURRENT_STATE, newState);
        this.currentPage = newState;
        this.clearCurrentSubPage();
    }

    public clearCurrentSubPage(): void {
        this.updateCurrentSubPage(null);
    }

    public updateCurrentSubPage(newSubState): void {
        console.log('update current subpage: ' + newSubState);
        this.currentSubPage = newSubState;
    }

    public getCurrentPage(): string {
        return this.currentPage;
    }

    public getCurrentSubPage(): string {
        return this.currentSubPage;
    }

    public isLoading(): boolean {
        return this.loading;
    }

    public async presentLoading() {
        this.loading = true;
        this.loadingController.create({
            message: this.translate.instant('MESSAGE.WAIT_LOADING'),
            cssClass: 'loading-controller'
        }).then((res) => {
            res.present();
            res.onDidDismiss().then((dis) => {
                console.log('Loading dismissed!');
            });
        });
    }

    public async closeLoading() {
        this.loading = false;
        if (await this.loadingController.getTop()) {
            this.loadingController.dismiss();
        }
    }

    public openMenu(): void {
        this.menuOpen = true;
    }

    public closeMenu(): void {
        this.menuOpen = false;
    }

    public isMenuOpen(): boolean {
        return this.menuOpen;
    }

    public getHeaderClass(): string {
        return 'generic-background';
    }

    public getPageClass(): string {
        return 'generic-background';
    }

    public hasError(): boolean {
        return this.error;
    }

    public setError(err, code): void {
        console.error(err);
        this.api.logError(code, err);

        this.endTransition();
        this.currentErrorCode = code;
        this.error = true;
    }

    public clearError(): void {
        this.error = false;
        this.endTransition();
        this.currentErrorCode = '';
    }

    public getCurrentErrorCode(): any {
        return this.currentErrorCode;
    }

    public beginTransition() {
        this.transitioning = true;
    }

    public endTransition() {
        this.transitioning = false;
    }

    public isTransitioning() {
        return this.transitioning;
    }

    public closeApp() {
        this.api.logEvent('ExitApp');
        this.clearError();
        navigator['app'].exitApp();
    }

    public hasInternetConnection() {
        return this.isOnline;
    }

    public isDeviceSupported() {
        if (this.deviceSupported === null) {
            return true;
        } else {
            return this.deviceSupported;
        }
    }

    public isDeviceCompatilityChecked() {
        return this.deviceSupported !== null;
    }

    public async haveProfileOnDevice() { // #todo change text if we already have the profile id
        return await this.stateData.get(StateDataService.KEYS.PROFILE_ID) !== null;
    }

    public async checkDeviceSupported() {
        this.deviceSupported = await this.api.checkDeviceSupported();
    }

    public async determineAndSetMarket() {
        this.market = await this.api.determineAndSetMarket();
    }

    public getMarket() {
        return this.market;
    }

    public isHelpNeeded(): boolean {
        return this.helpNeeded;
    }

    public setHelpNeeded(val): void {
        this.helpNeeded = val;
    }

    protected onNetworkDisconnected(): void {
        if (this.isOnline) {
            this.stateData.captureTelemetry({online: 'off'});
        }
        this.isOnline = false;
    }

    protected onNetworkConnected(): void {
        if (!this.isOnline) {
            this.stateData.captureTelemetry({online: 'on'});
            this.api.logEvent('NetworkReconnected');
        }
        this.isOnline = true;
    }


    public async isEcuador() {
        if (this.getMarket()) {
            return this.getMarket().toUpperCase() === 'EC';
        }
        const countryFromDevice = await this.stateData.get(StateDataService.KEYS.DEVICE_ISO_COUNTRY_CODE);
        if (countryFromDevice) {
            return countryFromDevice === 'EC';
        }
        return false;
    }

    public processEcString(stringToTranslate): string {
        if (this.isEcuador()) {
            return stringToTranslate + '_EC';
        } else {
            return stringToTranslate;
        }
    }
}