import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { SegmentChangeEventDetail } from '@ionic/core/dist/types/interface';
import { OfferData } from '../get-offers.types';

@Component({
  selector: 'app-offer-slider',
  templateUrl: './offer-slider.component.html',
  styleUrls: ['./offer-slider.component.scss'],
})
export class OfferSliderComponent implements OnInit {
  @Input() title: string;
  @Input() initialSliderPosition: number;
  @Input() options: OfferData[] = [];
  @Input() actionButtonText: string;
  @Output() onSelectAction = new EventEmitter<OfferData>();
  @Output() onOfferChange = new EventEmitter<OfferData>();

  public sliderPosition: number;
  public selectedOffer: OfferData;
  public animatingText = false;
  public readonly textAnimationTime = 500; // Miliseconds
  public readonly rangeMultiplayer = 10;
  public browserLanguage = 'es-MX';

  constructor() {
    this.browserLanguage = window.navigator.language;
  }

  ngOnInit() {
    this.sliderPosition = this.initialSliderPosition * this.rangeMultiplayer || this.rangeMultiplayer;
    this.selectedOffer = this.options[this.initialSliderPosition];
    this.onOfferChange.emit(this.selectedOffer);
  }

  getMaxRange(): number {
    return (this.options.length - 1) * this.rangeMultiplayer;
  }

  handleSliderChange(e: CustomEvent<SegmentChangeEventDetail>): void {
    const selectedValue = Number(e.detail.value);
    const offerIndex = Math.floor(selectedValue / this.rangeMultiplayer);
    const newSelectedOffer = this.options[offerIndex]

    if (this.selectedOffer !== newSelectedOffer && !this.animatingText) {
      this.triggerTextAnimation();
    }

    this.sliderPosition = selectedValue;
    this.selectedOffer = newSelectedOffer;

    this.onOfferChange.emit(this.selectedOffer);
  }

  handlerSelectAction() {
    this.onSelectAction.emit(this.selectedOffer);
  }

  triggerTextAnimation(): void {
    this.animatingText = true;
    setTimeout(() => {
      this.animatingText = false;
    }, this.textAnimationTime);
  }
}
