import { Component, OnInit } from '@angular/core';
import { environment } from '@app/env';
import { StateService } from './state.service';
import { StateDataService } from '../api/state-data.service';
import { CashStateMachineService } from '../cash/cash-state-machine.service';

@Component({
    selector: 'loan-application-root',
    templateUrl: './loan-application.component.html',
    styleUrls: ['./loan-application.component.scss'],
})
export class LoanApplicationComponent implements OnInit {

    screen = StateService.STATES.ONBOARDING;
    hideHelp = false;
    disbursementInfo = {
        availableMethods: null,
    };
    stateSetupoDone = false;
    constructor(
        private state: StateService,
        private stateData: StateDataService,
        public stateMachine: CashStateMachineService,
    ) { }

    async ngOnInit() {
        const currentOffer = JSON.parse(await this.stateData.get(StateDataService.KEYS.CURRENT_OFFER));
        const offerProblem = !currentOffer || !currentOffer['amount'];

        if (offerProblem) {
            // TODO!! Get copy from translate JSON
            this.stateMachine.setError('Lo sentimos, encontramos un error.', StateDataService.ERROR_CODES.OFFER_DATA_MISSING);
        }

        const channelType = await this.stateData.get(StateDataService.KEYS.CHANNEL_TYPE);
        this.disbursementInfo.availableMethods = this.stateData.getAvailableDisbursmentMethods(channelType);

        this.state.setAvailableDisbursementMethods(this.disbursementInfo.availableMethods);

        const loanContractIsSigned = await this.stateData.get(StateDataService.KEYS.LOAN_CONTRACT_IS_SIGNED);
        if (!loanContractIsSigned) {
            const currentSubPage = await this.stateMachine.getCurrentSubPage();
            this.stateMachine.updateCurrentSubPage(currentSubPage || this.stateMachine.subStates.LOAN_APPLICATION_INTRO);
        } else {
            this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_TRANSITION);
        }

        this.stateSetupoDone = true;
    }

}
