import { Injectable } from '@angular/core';
import { environment } from '@app/env';
import { StateDataService } from './state-data.service';
import { HttpClient } from '@angular/common/http';
import CryptoJS from 'crypto-js';

declare var cordova: any;

@Injectable({
    providedIn: 'root'
})
export class ApiService {

    constructor(
        private http: HttpClient,
        private stateData: StateDataService,
    ) {}

    private static adjustUrlParts(urlBase, endpoint) {
        const API_PATH_PREFIX = '/api/';

        if (endpoint.startsWith(API_PATH_PREFIX) && urlBase.startsWith('https://payjoy.com')) {
            let logMessage = 'converting url for prod from ' + urlBase + endpoint;
            endpoint = endpoint.slice(API_PATH_PREFIX.length - 1, );
            urlBase = 'https://api.payjoy.com';
            logMessage += ' to ' + urlBase + endpoint;
        }

        return { urlBase, endpoint };
    }

    public async postImages(endpoint, data, images, names, doAuth = true) {
        console.log('postImages', images);
        const additionalData = { images, names };
        return this.post(endpoint, { ...data, ...additionalData }, doAuth);
    }

    public async submitSurvey(endpoint, data) {
        console.log('submitSurvey', data);
        return this.post(endpoint, data);
    }

    public async logError(errorCode, errorMessage) {
        const payload = await this._buildBasePayload();
        payload['errorCode'] = errorCode;
        payload['errorMessage'] = errorMessage;

        this.stateData.captureTelemetry(payload);
        return this.post('/loans/v2/api/cash-error.php', payload);
    }

    public async logEvent(event, details = null) {
        const payload = await this._buildBasePayload();
        payload['event'] = event;
        payload['details'] = details;

        this.stateData.captureTelemetry(payload);
        return this.post('/loans/v2/api/cash-event.php', payload);
    }

    public async getUserInfo() {
        const userId = await this.stateData.get(StateDataService.KEYS.USER_ID);
        const response = await this.get(
            '/loans/v2/api/get-user-info.php', { userId: String(userId) });
        if (response['valid'] && !userId && response['data'].userId) {
            console.log('Updating userId from get-user-info: ', response['data'].userId);
            this.stateData.set(StateDataService.KEYS.USER_ID, response['data'].userId);
        }
        return response;
    }

    public async authorizeDataCollection() {
        const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);
        if (!deviceTag) {
            throw new Error('Device tag has not been set');
        }
        const payload = {
            deviceTag,
            dataCollectionPermission: 'true',
            isSMSCollectionAuthorized: '1'
        };
        const response = await this.get('/api/update-preferences.php', payload);
        if (!response['success']) {
            throw new Error('authorizeDataCollection failed: ' + response['message']);
        }
        return response;
    }

    public async checkDeviceSupported() {
        const parameters = {
            product: await this.stateData.get(StateDataService.KEYS.APP_PRODUCT),
            manufacturer: await this.stateData.get(StateDataService.KEYS.MANUFACTURER),
            sdkInt: await this.stateData.get(StateDataService.KEYS.SDK_INT),
            model: await this.stateData.get(StateDataService.KEYS.MODEL)
        }
        const response = await this.get('/loans/v2/api/device-supported.php', parameters);
        return (response && response['valid']);
    }

    public async determineAndSetMarket() {

        const marketFromStorage = await this.stateData.get(StateDataService.KEYS.MARKET);
        this.logEvent('DetermineAndSetMarket 1');
        if (marketFromStorage){
            return marketFromStorage;
        } else {
            let parameters = {
                deviceTag: await this.stateData.get(StateDataService.KEYS.DEVICE_TAG),
                uuid: await this.stateData.get(StateDataService.KEYS.DEVICE_UUID),
                countryFromDevice: await this.stateData.get(StateDataService.KEYS.DEVICE_ISO_COUNTRY_CODE)
            }
            this.logEvent('DetermineAndSetMarket 2');
            try {
                const response = await this.post('/loans/v2/api/get-country.php', parameters);
                this.logEvent('DetermineAndSetMarket 3');

                if (response && response['data'] && response['data']['country']) {
                    const market =  response['data']['country'].toUpperCase();
                    this.stateData.set(StateDataService.KEYS.MARKET, market);
                    parameters['market'] = market;
                    this.logEvent('DetermineAndSetMarket', parameters);
                    return market;
                }
            } catch (error) {
                this.logError(
                StateDataService.ERROR_CODES.DETERMINE_AND_SET_MARKET,
                'Error occurred in determineAndSetMarket: ' + error);
            }
        }
        return null;
    }

    public async registerAppInstall() {
        const uuid = await this.stateData.get(StateDataService.KEYS.DEVICE_UUID);

        if (!uuid) {
            this.logError(StateDataService.ERROR_CODES.UUID_GET_ERROR, 'uuid is null after get');
            /* should we error out as an unexpected state? or allow them to go on since this is just for tracking? */
            return;
        }

        const payload = {
            uuid,
            versionCode: await this.stateData.get(StateDataService.KEYS.APP_VERSION_CODE),
            product: await this.stateData.get(StateDataService.KEYS.APP_PRODUCT),
            manufacturer: await this.stateData.get(StateDataService.KEYS.MANUFACTURER),
            model: await this.stateData.get(StateDataService.KEYS.MODEL),
            osVersion: await this.stateData.get(StateDataService.KEYS.OS_VERSION),
            sdkInt: await this.stateData.get(StateDataService.KEYS.SDK_INT),
            buildFingerprint: await this.stateData.get(StateDataService.KEYS.BUILD_FINGERPRINT),
            deviceTag: await this.stateData.get(StateDataService.KEYS.DEVICE_TAG),
            appsFlyerUID: await this.stateData.get(StateDataService.KEYS.APPSFLYER_UID),
            deviceIsoCountryCode: await this.stateData.get(StateDataService.KEYS.DEVICE_ISO_COUNTRY_CODE)
        }
        try {
            const res = await this.post('/loans/v2/api/register-app-install.php', payload);
            if (res['valid']) {
                this.logEvent('RegisterAppInstall', payload);
            } else {
                this.logError(
                    StateDataService.ERROR_CODES.REGISTER_APP_INSTALL,
                    'Error occurred in post register-app-install: ' + JSON.stringify(res));
            }
        } catch (error) {
            this.logError(
                StateDataService.ERROR_CODES.POST_REGISTER_APP_INSTALL,
                'Error occurred in post register-app-install: ' + error);
        }
    }

    public async post(endpoint, data = {}, doAuth = true) {
        const urlParts = ApiService.adjustUrlParts(environment.API_BASE_URL, endpoint);
        const url = urlParts.urlBase + urlParts.endpoint;

        const appMode = this.stateData.getAppMode();
        const product = await this.stateData.get(StateDataService.KEYS.APP_PRODUCT);
        const appVersionCode = await this.stateData.get(StateDataService.KEYS.APP_VERSION_CODE);

        data['appMode'] = appMode;
        data['product'] = product;
        data['appVersionCode'] = appVersionCode;

        let headers = {};
        if (doAuth) {
            headers = await this._buildAuthHeader(
                'POST',
                urlParts.endpoint,
                JSON.stringify(data));
        }

        return new Promise((resolve, reject) => {
            cordova.plugins.NativeHttp.post(response => {
                if (response) {
                    if (response['error']) {
                        this.processResponseError(urlParts.endpoint, response['error']);
                    } else {
                        console.log('success', urlParts.endpoint);
                        resolve(JSON.parse(response));
                    }
                } else {
                    console.error('empty response', url, headers, data);
                    reject('Empty response for ' + url + ' with payload: ' + JSON.stringify(data));
                }
            }, error => {
                console.error(error);
                reject(error);
            }, url, headers, data);
        });
    }

    public async get<T>(endpoint, parameters = {}, doAuth = true): Promise<T> {
        const urlParts = ApiService.adjustUrlParts(environment.API_BASE_URL, endpoint);
        const url = urlParts.urlBase + urlParts.endpoint;

        const appMode = this.stateData.getAppMode();
        const product = await this.stateData.get(StateDataService.KEYS.APP_PRODUCT);
        const appVersionCode = await this.stateData.get(StateDataService.KEYS.APP_VERSION_CODE);

        parameters['appMode'] = appMode;
        parameters['product'] = product;
        parameters['appVersionCode'] = appVersionCode;

        let headers = {};
        if (doAuth) {
            headers = await this._buildAuthHeader(
                'GET',
                urlParts.endpoint + '?' + this._buildQueryString(parameters),
                '');
        }
        console.log('GET ' + url, parameters, headers);
        return new Promise((resolve, reject) => {
            if (urlParts.endpoint.startsWith('assets')) {
                this.http.get(urlParts.endpoint, { responseType: 'text' })
                    .subscribe((data: unknown) => {
                        resolve(data as T);
                    });
            } else {
                cordova.plugins.NativeHttp.get(response => {
                    if (response) {
                        if (response['error']) {
                            this.processResponseError(urlParts.endpoint, response['error']);
                        } else {
                            console.log('success', urlParts.endpoint, response);
                            resolve(JSON.parse(response));
                        }
                    } else {
                        console.error('empty response', urlParts.endpoint);
                        reject('Empty response');
                    }
                }, error => {
                    console.log('error', error);
                    reject(error);
                }, url, headers, parameters);
            }
        });
    }

    public async externalGet(url, parameters = {}) {
        console.log('GET ' + url, parameters);

        return new Promise((resolve, reject) => {
            cordova.plugins.NativeHttp.get(response => {
                if (response) {
                    resolve(JSON.parse(response));
                } else {
                    console.error('empty response', url);
                    reject('Empty response');
                }
            }, error => {
                console.log('error', error);
                reject(error);
            }, url, {}, parameters);
        });
    }

    public async getStatePacket() {
        let statePacket = await this.stateData.getStatePacket();
        const response = await this.post(
            '/loans/v2/api/next-state.php',
            statePacket);
        return response['data']['statePacket'];

    }

    private async processResponseError(endpoint, error) {
        switch (error) {
            case ('http_error_401'):
                console.log('authentication error', endpoint, error);
                this.logError(StateDataService.ERROR_CODES.AUTHENTICATION_FAILED, 'Error occured with authentication' + error);
                break;
            case ('http_error_500'):
                console.log('internal server error', endpoint, error);
                this.logError(StateDataService.ERROR_CODES.INTERNAL_SERVER, 'Error occured within internal server' + error);
                break;
            default:
                console.log('http error', endpoint, error);
                this.logError(StateDataService.ERROR_CODES.GENERAL_HTTP, 'Error occured with http response' + error);
                break;
        }
    }

    private _buildQueryString(parameters) {
        const tokens = [];
        for (const d in parameters) {
            tokens.push(encodeURIComponent(d) + '=' + encodeURIComponent(parameters[d]));
        }
        return tokens.join('&');
    }

    private async _buildAuthHeader(method, endpoint, body) {
        const key = await this.stateData.get(StateDataService.KEYS.DEVICE_SECRET);
        const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);
        if (!key || !deviceTag) {
            return {}
        }
        const message = this._buildRequestMessage(method, endpoint, body);
        const signature = CryptoJS.enc.Base64.stringify(CryptoJS.HmacSHA256(message, key));
        const header = 'Signature algorithm="hmac-sha256",keyId="' + deviceTag + '",signature="' + signature + '"';
        return {
            Authorization: header
        };
    }

    private _buildRequestMessage(method, endpoint, body) {
        let message = method + endpoint;
        if (body) {
            message = message + body;
        }
        return message;
    }

    private async _buildBasePayload() {
        const appVersionCode = await this.stateData.get(StateDataService.KEYS.APP_VERSION_CODE);
        const userId = await this.stateData.get(StateDataService.KEYS.USER_ID);
        const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);
        const uuid = await this.stateData.get(StateDataService.KEYS.DEVICE_UUID);
        const appMode = this.stateData.getAppMode();
        const payload = {
            appVersionCode,
            userId,
            deviceTag,
            uuid,
            appMode
        };
        return payload;
    }
}