import { Component } from '@angular/core';
import { LockStateMachineService } from '../../lock/lock-state-machine.service';
import { StateDataService } from '../../api/state-data.service';
import { TranslateService } from '@ngx-translate/core';
import { LanguageService } from '../../services/language.service'
import { ApiService } from '../../api/api.service';

@Component({
    selector: 'app-feedback-form',
    templateUrl: './feedback-form.component.html',
    styleUrls: ['./feedback-form.component.scss'],
})
export class FeedbackFormComponent {
    private rating: number;
    private textInput: string;

    constructor(
        protected api: ApiService,
        public stateMachine: LockStateMachineService,
        private stateData: StateDataService,
        public translate: TranslateService,
        public language: LanguageService
    ) {}

    public feedbackOptions = [{
            id: 'loan-process',
            text: this.translate.instant('HELP.BUBBLES.APPLICATION_PROCESS'),
        },
        {
            id: 'loan-amount',
            text: this.translate.instant('HELP.BUBBLES.LOAN_AMOUNT'),
        },
        {
            id: 'loan-pricing',
            text: this.translate.instant('HELP.BUBBLES.LOAN_PRICING'),
        },
        {
            id: 'loan-repayment',
            text: this.translate.instant('HELP.BUBBLES.LOAN_REPAYMENT'),
        },
        {
            id: 'issues-or-bugs',
            text: this.translate.instant('HELP.BUBBLES.ISSUES_OR_BUGS'),
        }
    ];

    private toggleButton(option) {
        const feedback = document.querySelector('#' + option)
        if (feedback.classList.contains('active')) {
            feedback.classList.remove('active');
        } else {
            feedback.classList.add('active');
        }
    }

    private getSelectedAnswers() {
        const selectedOptions = [];
        const options = [...document.getElementsByClassName('options')];
        for (let i = 0; i < options.length; i++) {
            if (options[i].classList.contains("active")) {
                selectedOptions.push(options[i].id)
            }
        }
        return selectedOptions;
    }

    private getTextResponse() {
        let inputValue = (<HTMLInputElement>document.getElementById('input')).value;
        return inputValue;
    }

    private async submitFeedbackForm() {
        await this.postSurvey(this.stateMachine.getRating(), this.getSelectedAnswers(), this.getTextResponse());
        setTimeout(function() {
            this.stateMachine.setRating();
        }.bind(this), 3000);
    }

    private returnToPaymentPage() {
        this.stateMachine.updateCurrentPage(this.stateMachine.states.MAIN_HOME);
        this.stateMachine.setRating(null);
    }

    private async postSurvey(rating, bubbles, textResponse) {
        console.log(this.textInput);
        const data = {
            userId: String(await this.stateData.get(StateDataService.KEYS.USER_ID)),
            financeOrderId: String(this.stateMachine.loanInfo.financeOrderId),
            rating: String(rating),
            bubbles: String(bubbles), 
            textResponse: String(textResponse),
        };

        try {
            const response = await this.api.submitSurvey(
                '/loans/v2/api/feedback-survey.php', data
            );
            const success = response['valid'];
            if (success) {
                this.api.logEvent('user submitted feedback survey');
                // update flag to completed survey
                this.stateMachine.setFeedbackSurveyCompleted(true);
            } else {
                this.stateMachine.setError(
                    'error occured on feedback survey: ' + response['message'],
                    StateDataService.ERROR_CODES.FEEDBACK_SURVEY);
            }
        } catch (error) {
            this.stateMachine.setError(
                'Error occurred on post feedback survey: ' + error,
                StateDataService.ERROR_CODES.POST_FEEDBACK_SURVEY);
        }
    }
}