import { ApiService } from '../api/api.service';
import { DeviceBridgeService } from '../services/device-bridge.service';
import { Injectable, OnDestroy } from '@angular/core';
import { LoadingController, Platform } from '@ionic/angular';
import { LoanInfo } from '../models/loan.model';
import * as lodash from 'lodash';
import { PluginService } from '../api/plugin.service';
import { Profile } from '../models/profile.model';
import { StateDataService } from '../api/state-data.service';
import { StateMachineService } from '../api/state-machine.service';
import { timer } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { Network } from '@ionic-native/network/ngx';
import { environment } from '@app/env';
import { GetBundleVersionResponse } from './lock-state-machine.service.types';

class CooldownTimeLeft {
    public seconds;
    public days;
    public hours;
    public minutes;
    public endTime;
    public finished = false;
    private tickInterval = 2000;
    private timeTicker = timer(1000, this.tickInterval);
    private timerSubscription;
    private stateMachine;


    constructor(endTime, stateMachine) {
        this.stateMachine = stateMachine;
        this.setTime(endTime);
        this.timerSubscription = this.timeTicker.subscribe(
            val => this.update()
        );
    }

    setTime(endTime) {
        this.endTime = endTime;
        const now = new Date().getTime();
        if (now > this.endTime) {
            this.finishUp();
        } else {
            // add buffer so button changes when minutes turns to 0
            const seconds = Math.floor((this.endTime - (now) + this.tickInterval) / 1000);
            const minutes = Math.ceil(seconds / 60);
            const hours = Math.floor(minutes / 60);
            this.days = Math.floor(hours / 24);
            this.hours = hours - (this.days * 24);
            this.minutes = minutes - (this.days * 24 * 60) - (this.hours * 60);
            this.seconds = seconds - (this.days * 24 * 60 * 60) - (this.hours * 60 * 60) - (this.minutes * 60);
        }
    }

    finishUp() {
        this.seconds = 0;
        this.days = 0;
        this.hours = 0;
        this.minutes = 0;
        if (this.timerSubscription) {
            this.timerSubscription.unsubscribe();
        }
        this.stateMachine.loanInfo.inCooldown = false;
        this.finished = true;
    }

    update() {
        this.setTime(this.endTime);
    }

    shutdown() {
        if (this.timerSubscription) {
            this.timerSubscription.unsubscribe();
        }
    }
}

@Injectable({
    providedIn: 'root',
})

export class LockStateMachineService extends StateMachineService implements OnDestroy {
    public states;
    public cooldownTimer;
    public locked = false;
    public fullyUnlocked = true;
    public removable = true;
    public lockPacket;
    public loanInfoFetched = false;
    // #todo set these on/off by api call?
    public FEATURES_CONTROL = {
        OFFERS_ON: true,
        REFERRAL_ON: true,
    };
    private countPubSubUpdates = 0;
    private currentlyLoadingUserInfo = false;
    private currentlyLoadingLoanInfo = false;
    private rating;
    private feedbackSurveyComplete = false;
    private readonly BUNDLE_ROUTER_URL = `${environment.API_BASE_URL}/cash-app-bundle/`;

    constructor(
        public userProfile: Profile,
        public loanInfo: LoanInfo,
        public deviceBridge: DeviceBridgeService,
        protected api: ApiService,
        protected loadingController: LoadingController,
        protected network: Network,
        protected platform: Platform,
        protected plugin: PluginService,
        protected stateData: StateDataService,
        protected translate: TranslateService,
    ) {
        super(api, loadingController, network, platform, plugin, stateData, translate);

        this.states = {
            CASH_ONBOARDING: 'cashOnboarding',
            MAIN_HOME: 'lockMain',
            MAIN_PROFILE: 'lockProfile',
            MAIN_PAY: 'lockPay',
            MAIN_MENU: 'lockMenu',
            MAIN_HELP: 'lockHelp',
            MAIN_LOAN: 'lockLoanDetail',
            MAIN_DISBURSEMENT: 'lockWithdrawal'
        };
    }

    public async initialize() {
        this.platform.ready().then(async () => {
            super.initialize();

            // if starting fresh, go to home; otherwise, call increment page
            this.updateCurrentPage(this.states.MAIN_HOME);

            this.initializeData();
            this.deviceBridge.subscribe('lockStatus', (packet: any) => {
                if (!lodash.isEqual(packet, this.lockPacket)) {
                    this.lockPacket = packet;
                    this.locked = packet.data.locked;
                    this.fullyUnlocked = packet.data.fullyUnlocked;
                    this.removable = packet.data.removable;
                    if (this.loanInfo.validThrough != packet.data.validThrough) {
                        this.fetchAndUpdateLoanDetail(packet.data.validThrough);
                    }
                    if (this.countPubSubUpdates) {
                        this.api.logEvent('LockPubSubUpdate', {
                            currentPage: this.getCurrentPage(),
                            lockPacket: packet
                        });
                    }
                    this.countPubSubUpdates += 1;
                }
            });
        });
    }

    public isSecured() {
        /*    #todo this should check whether or not the device isAuthorized - but that value hasn't yet been added to the deviceBridge. Update this function once the devicebridge reports isAuthorized */

        return !this.removable;
    }

    public ngOnDestroy() {
        if (this.cooldownTimer) {
            this.cooldownTimer.shutdown();
        }
        this.deviceBridge.unsubscribe('lockStatus');
    }

    private async reopenCashPlatformProfile() {
        const userId = await this.stateData.get(StateDataService.KEYS.USER_ID);
        let success = true;

        try {
            if (userId) {
                const response = await this.api.post('/loans/v2/api/reopen-onboarding-profile.php', { userId: String(userId) });
                success = response['valid'];
                if (!success) {
                    this.setError(
                        'error occured reopening profile: ' + response['message'],
                        StateDataService.ERROR_CODES.REOPEN_ONBOARDING_PROFILE);
                    return null;
                }
                return response['data'].nextState;
            }

        } catch (err) {
            this.setError(
                'Error occurred on post open-onboarding-profile: ' + err,
                StateDataService.ERROR_CODES.REOPEN_ONBOARDING_PROFILE);
        }
    }

    public async setupForOnboardingFlow() {
        this.api.logEvent('LocksReturnToOffers', {
            currentPage: this.getCurrentPage(),
        });
        // must be called before cash init for transition screens
        if (this.isSecured) {
            await this.stateData.resetDeviceStorage(); 
        } else {
            await this.stateData.resetDeviceStorageUnsecured(); 
        }
        console.log('switch to cash');

        this.stateData.setAppMode('cash');
    }

    public async setupForOffersReapplyFlow() {
        console.log('switch to cash offers');
        const nextState = await this.reopenCashPlatformProfile();
        if (nextState !== StateDataService.USER_PROGRESS_KEYS.OFFERS) {
            await this.setupForOnboardingFlow();
            return;
        }
        await this.stateData.resetDeviceStorageReapply();
        this.stateData.setAppMode('cash');
    }


    public loanInfoInitialized() {
        if (this.loanInfoFetched && this.lockPacket) {
            return true;
        }
        return false;
    }

    public showTel() {
        return this.loanInfo.isPastDue()
    }

    public showWhatsApp() {
        return !this.loanInfo.isPastDue()
    }

    protected onNetworkConnected() {
        super.onNetworkConnected();
        this.initializeData();
    }

    private async initializeData() {
        if (!this.hasInternetConnection()) {
            return;
        }
        if (this.userProfile && this.loanInfoInitialized()) {
            return false;
        }

        if (this.currentlyLoadingUserInfo) {
            return false;
        }


        this.currentlyLoadingUserInfo = true;

        try {
            const response = await this.api.getUserInfo();
            const success = response['valid'];
            if (success) {
                const data = response['data'];
                if (data) {
                    this.userProfile.userDisplayName = data.name;
                    this.userProfile.telephoneNumber = data.phone;
                    this.userProfile.emailAddress = data.email;
                }
            } else {
                this.setError(
                    'Invalid response from getUserInfo: ' + LockStateMachineService.parseMessage(response),
                    StateDataService.ERROR_CODES.LOCK_SCREEN_GET_USER_INFO);
            }
        } catch (error) {
            this.setError(
                'Error occurred on post getUserInfo: ' + error,
                StateDataService.ERROR_CODES.LOCK_SCREEN_GET_USER_INFO);
        } finally {
            this.currentlyLoadingUserInfo = false;
        }

        await this.stateData.refreshDeviceTag();
        this.fetchAndUpdateLoanDetail();
    }

    private updateLoanDetail(loanStatusResponse, validThrough = null) {
        const newInfo = new LoanInfo();

        newInfo.financeStatus = loanStatusResponse.financeStatus;

        if (validThrough === null) {
            validThrough = this.loanInfo.validThrough;
        }
        newInfo.initialize(loanStatusResponse, validThrough);

        if (newInfo.cooldownEndDatetime) {
            this.cooldownTimer = new CooldownTimeLeft(newInfo.cooldownEndDatetime, this);
        } else if (!newInfo.inCooldown) {
            if (this.cooldownTimer) {
                this.cooldownTimer.shutdown();
                this.cooldownTimer = null;
            }
        }

        this.api.logEvent('LockLoanInfoUpdated', {
            currentPage: this.getCurrentPage(),
            loanInfo: newInfo
        });

        this.setFeedbackSurveyCompleted(newInfo.feedbackSurveyCompleted);

        this.loanInfo = newInfo;
    }

    private async fetchAndUpdateLoanDetail(validThrough = null) {
        const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);

        const parameters = { deviceTag };

        if (!this.currentlyLoadingLoanInfo) {
            try {
                this.currentlyLoadingLoanInfo = true;

                const loanStatusResponse = await this.api.get(
                    '/api/get-payment-progress.php', parameters
                );
                if (loanStatusResponse) {
                    // send validThrough so that loan status updates at one time
                    this.updateLoanDetail(loanStatusResponse, validThrough);
                    this.loanInfoFetched = true;
                    this.validateBundleVersion();
                } else {
                    this.setError(
                        'Error occurred in get-payment-progress: ' + LockStateMachineService.parseMessage(loanStatusResponse),
                        StateDataService.ERROR_CODES.LOCK_SCREEN_GET_LOAN_STATUS);
                }
            } catch (error) {
                this.setError(
                    'Error occurred in post get-payment-progress: ' + error,
                    StateDataService.ERROR_CODES.LOCK_SCREEN_GET_LOAN_STATUS);
            } finally {
                this.currentlyLoadingLoanInfo = false;
            }
        }

        //if endpoint fails, at least update validThrough
        this.loanInfo.updateValidThrough(validThrough);
    }

    public getRating() {
        return this.rating;
    }

    public setRating(val): void {
        this.rating = val;
    }

    public showFeedbackForm(): boolean {
        return (!this.feedbackSurveyComplete && this.loanInfo.financeOrderId !== null);
    }

    public isFeedbackSurveyCompleted(): boolean {
        return this.feedbackSurveyComplete;
    }

    public setFeedbackSurveyCompleted(val): void {
        this.feedbackSurveyComplete = val;
    }

    private async validateBundleVersion() {
        if (!environment.production) {
            return;
        }
        const versionCode = await this.stateData.get(StateDataService.KEYS.APP_VERSION_CODE);
        const currentBundleVersion = await this.stateData.get(StateDataService.KEYS.APP_BUNDLE_VERSION);

        const parameters = { versionCode };

        const bundleVersionResponse = await this.api.get < GetBundleVersionResponse > (
            '/loans/v2/api/get-bundle-version.php', parameters
        );

        if (bundleVersionResponse.data && bundleVersionResponse.valid) {
            const expectedBundleVersion = bundleVersionResponse.data.bundleVersion;
            if (expectedBundleVersion != currentBundleVersion) {
                const targetUrl = `${this.BUNDLE_ROUTER_URL}?versionCode=${versionCode}`;
                const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);
                const logData = {
                    deviceTag,
                    versionCode,
                    currentBundleVersion,
                    expectedBundleVersion,
                };
                this.api.logEvent('LockScreen forced bundle upgrade', logData);
                window.location.replace(targetUrl);
            }
        }
    }
}