import { Component, HostBinding, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';
import { NavFooterService } from './nav-footer.service';

@Component({
    selector: 'app-nav-footer',
    templateUrl: './nav-footer.component.html',
    styleUrls: ['./nav-footer.component.scss'],
})
export class NavFooterComponent implements OnInit, OnDestroy {
    @HostBinding('style.pointer-events') public pointerEvents: string;
    public footerStyle: string;
    private footerStyleSubscription: Subscription;
    public states;
    public returnToLockEnabled = true;

    constructor(
        public navFooter: NavFooterService,
        public stateMachine: OnboardingStateMachineService
    ) { }

    ngOnInit() {
        this.footerStyleSubscription = this.navFooter.footerStyleChange.subscribe(val => this.onFooterStyleChange(val));
        this.onFooterStyleChange(this.navFooter.footerStyle);
    }

    ngOnDestroy() {
        this.footerStyleSubscription.unsubscribe();
    }

    private onFooterStyleChange(newStyle: string) {
        if (newStyle === NavFooterService.STYLES.HIDDEN) {
            this.pointerEvents = 'none';
        } else {
            this.pointerEvents = 'all';
        }
        this.footerStyle = newStyle;
    }

    public buttonClicked(destination = 'next') {
        this.stateMachine.disableNext();
        this.navFooter.fireButton(destination);
    }

    public nextClicked() {
        this.returnToLockEnabled = true;
        this.navFooter.setSubmitButtonEnabled(false);
        this.stateMachine.fireNext();
    }

    public isConfirmDetailsButtonEnabled() {
        return this.navFooter.isConfirmDetailsButtonEnabled() && this.stateMachine.isNextButtonEnabled();
    }

    public checkboxClicked(e) {
        this.navFooter.fireCheckbox(e);
    }

    public returnToLockClicked() {
        this.returnToLockEnabled = false;
        this.stateMachine.goToLock();
    }
}