import { Component, Input, OnInit } from '@angular/core';
import { LockStateMachineService } from '../../lock/lock-state-machine.service';
import { TranslateService } from '@ngx-translate/core';
import { LanguageService } from '../../services/language.service'

@Component({
    selector: 'app-rating-stars',
    templateUrl: './rating-stars.component.html',
    styleUrls: ['./rating-stars.component.scss'],
})
export class RatingStarsComponent {
    @Input() placeholder = false;
    public rating: number;

    constructor(
        public stateMachine: LockStateMachineService,
        public translate: TranslateService,
        public language: LanguageService
    ) {
    }

    public async ngOnInit() {
        this.loadStars(this.stateMachine.getRating());
    }

    public loadStars(rating) {
        this.rating = rating;
        const stars = [...document.getElementsByClassName('ratingStar')];
        const starsLength = stars.length - 1;
        let i = rating - 1;
        if (stars.length > 5) {
            i += 5;
        }
        let star = stars[i];
        if (star && star.classList.contains("active")) {
            for (i; i < starsLength; i++) stars[i + 1].classList.remove("active");
        } else {
            for (i; i >= 0; i--) stars[i].classList.add("active");
        }

        this.stateMachine.setRating(rating);
    }
}