import { Component, ElementRef, NgZone, OnDestroy, OnInit } from '@angular/core';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';
import { StateDataService } from '../../api/state-data.service';
import { PluginService } from '../../api/plugin.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { HelpService } from '../../services/help.service';

@Component({
    selector: 'app-phone-verify',
    templateUrl: './phone-verify.component.html',
    styleUrls: ['./phone-verify.component.scss'],
})
export class PhoneVerifyComponent extends PageComponent implements OnInit, OnDestroy {
    public phoneNumber = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.PHONE_INPUT);
    public waitingForVerification = false;
    public showDelayedComponents = false;
    public enteredInvalidNumber = false;
    public enteredNewNumber = false;
    public progress = 0;

    public readonly MIN_DIGITS = 10;
    public readonly MAX_DIGITS = 10;

    constructor(
        public stateMachine: OnboardingStateMachineService,
        protected elementRef: ElementRef,
        private help: HelpService,
        private ngZone: NgZone,
        private plugin: PluginService
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'PhoneVerifyComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.stateMachine.endTransition();
        this.navFooter.setNextStyle('NAVIGATION.VERIFY');
        this.subscription.add(
            this.stateMachine.$nextButton.subscribe(val => {
                this.formSubmit()
            })
        );

        this.inputCheck(this.phoneNumber);
    }

    public ngOnDestroy() {
        super.ngOnDestroy();
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.PHONE_INPUT, this.phoneNumber);
    }

    public goToContact() {
        this.help.setHelpBackButton(this.stateMachine.getCurrentPage(), this.stateMachine.getCurrentSubPage());
        this.stateMachine.setHelpNeeded(true);
    }

    public async formSubmit() {
        this.stateMachine.disableNext();
        this.enteredNewNumber = false;
        this.waitingForVerification = true;
        setTimeout(function () {
            this.showDelayedComponents = this.waitingForVerification;
        }.bind(this), 1000);
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.PHONE_INPUT, this.phoneNumber);

        try {
            const result = await this.plugin.verifyNumber(this.phoneNumber);
            console.log('verifyNumber', result);
            if (result === 'cancelled') {
                return;
            }
            this.stateMachine.markTaskComplete(StateDataService.KEYS.TASK_PHONE_VERIFICATION);
            this.stateMachine.beginTransition();
            setTimeout(function () {
                this.stateMachine.incrementPage();
            }.bind(this), 2000);
        } catch (error) {
            console.error('verifyNumber error: ' + error);
            this.retryPhoneVerification(true);
        }
    }

    public incrementProgress(newVal) {
        this.inputCheck(newVal);
        this.progress = (newVal.toString().length) / 10;
    }

    public enterNewNumber() {
        this.enteredNewNumber = true;
        this.retryPhoneVerification(false);
    }

    private inputCheck(newVal: number) {
        this.phoneNumber = Number.parseInt(newVal.toString().replace('.', ''));
        const valLength = this.phoneNumber.toString().length
        if (valLength > this.MAX_DIGITS || valLength < this.MIN_DIGITS) {
            this.stateMachine.disableNext();
        } else {
            this.stateMachine.enableNext();
        }
    }

    private retryPhoneVerification(showMessage = true) {
        this.enteredInvalidNumber = showMessage;
        this.phoneNumber = '';
        this.progress = 0;
        this.waitingForVerification = false;
        this.showDelayedComponents = false;
        this.stateMachine.disableNext();
    }
}