import { Component, OnDestroy, OnInit, ElementRef } from '@angular/core';
import { StateService } from '../../loan-application/state.service';
import { Observable } from 'rxjs';
import { timer } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import { NgxSpinnerService } from "ngx-spinner";
import { PageComponent } from '@app/pages/_page/page.component';
import { ActivationService } from '../../services/activation.service';
import { LoanService } from '../../services/loan.service';
import { StateDataService } from '../../api/state-data.service';
import { CashStateMachineService } from '../../cash/cash-state-machine.service';


@Component({
    selector: 'app-loan-application-transition',
    templateUrl: './transition.component.html',
    styleUrls: ['./transition.component.scss']
})

export class TransitionComponent extends PageComponent implements OnInit, OnDestroy {
    private polling$: Observable < number > = timer(0, 1000);
    private offerKey = '';
    private deviceTag: string;

    constructor(
        protected elementRef: ElementRef,
        private state: StateService,
        private spinner: NgxSpinnerService,
        private activation: ActivationService,
        private loan: LoanService,
        private stateMachine: CashStateMachineService,
    ) {
        super(elementRef);
    }

    async ngOnInit() {
        console.log('transition');
        super.ngOnInit();
        this.spinner.show();
        await this.activation.activate();
        this.offerKey = await this.loan.getOfferData('partnerOfferKey');
        this.deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);
        this.navFooter.setHiddenStyle();
        this.recursiveDeviceIsActivePolling();
    }

    public getName(): string {
        return 'LoanApplicationTransitionComponent';
    }

    async recursiveDeviceIsActivePolling() {
        const isActive = await this.activation.isActivated();
        if (!isActive) {
            await setTimeout(null, 1000);
            this.recursiveDeviceIsActivePolling()
        } else {
            this.startLoanStatusPolling();
        }
    }

    startLoanStatusPolling() {
        let params = {
            offerKey: this.offerKey,
            deviceTag: this.deviceTag,
        }
        this.subscription.add(
            this.polling$.subscribe(second => {
                this.state.apiGet("/check-loan-status", params).subscribe(
                    res => {
                        console.log('transition');
                        console.log(res);
                        if (res['deviceCreationStatus'] === "SECURED" && res['loanDetails'] && res['loanDetails']['amount'] && (( ['CashTransfer', 'WalmartGiftCard'].includes(res['loanDetails']['paymentType'])) || ((res['disbursementInfo'] != null) && (res['disbursementInfo'].pin != null)))) {
                            this.state.setOutsideBusinessHours(res['isOffHours']);
                            this.state.setOnHolidayOrWeekend(res['isWeekendHoliday']);
                            
                            this.stateData.setAppMode('lock');
                        }
                    },
                    (err: HttpErrorResponse) => {
                        this.stateMachine.setError(err.message, StateDataService.ERROR_CODES.CHECK_LOAN_STATUS);
                        console.error('Error occurred on post check-loan-status: ', err.message);
                    }
                );
            })
        );
    }

    ngOnDestroy() {
        console.log("ngOnDestroy()");
        this.subscription.unsubscribe();
    }
}
