import { Component, OnInit } from '@angular/core';
import { LockStateMachineService } from '../lock-state-machine.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
    selector: 'app-withdrawal',
    templateUrl: './withdrawal.component.html',
    styleUrls: ['./withdrawal.component.scss'],
})
export class WithdrawalComponent implements OnInit {

    constructor(
        public stateMachine: LockStateMachineService,
        public translate: TranslateService,
    ) {}

    ngOnInit() {}

    public async returnToMain() {
        this.stateMachine.updateCurrentPage(this.stateMachine.states.MAIN_HOME);
    }

}