import { Component, Input, OnInit } from '@angular/core';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';

@Component({
    selector: 'app-next-button',
    templateUrl: './next-button.component.html',
    styleUrls: ['./next-button.component.scss'],
})
export class NextButtonComponent implements OnInit {
    @Input() nextText = '';
    @Input() nextColor = '';

    constructor(
        public stateMachine: OnboardingStateMachineService,
    ) {
    }

    public async ngOnInit() {
    }

    public nextClicked() {
        this.stateMachine.fireNext();
        this.stateMachine.disableNext();
    }
}