import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { CashStateMachineService } from '../../cash/cash-state-machine.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { IonSlides } from '@ionic/angular';
import { FacebookService } from '../../services/facebook.service';
import { StateDataService } from '../../api/state-data.service';
import { HelpService } from '../../services/help.service';

@Component({
    selector: 'app-connect',
    templateUrl: './connect.component.html',
    styleUrls: ['./connect.component.scss'],
})
export class ConnectComponent extends PageComponent implements OnInit, OnDestroy {
    slideOpts = {
        initialSlide: 0,
        speed: 400,
        autoplay: false
    };
    @ViewChild(IonSlides, { static: true }) slides: IonSlides;
    private displayReturnToLock = this.stateMachine.isReturnToLockDisplayed();

    constructor(
        public fb: FacebookService,
        protected elementRef: ElementRef,
        private help: HelpService,
        public stateMachine: CashStateMachineService
    ) {
        super(elementRef);
        this.stateMachine.enableNext();
    }

    public getName(): string {
        return 'ConnectComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();

        const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);

        if (await this.stateMachine.getServiceConfig()) {
            this.navFooter.setOnboardingLoadingStyle();
            this.stateMachine.setDisplayReturnToLock(false);

            if (this.stateMachine.isFacebookLoginButtonOn()) {
                this.enableConnect();
                this.fb.setReadyToConnect(false);
                this.stateMachine.setDisplayReturnToLock(false);
                this.subscription.add(
                    this.navFooter.$button.subscribe(async (val) => {
                        if (val === 'facebook') {
                            this.api.logEvent('FacebookLoginScreen');
                            await this.stateMachine.presentLoading();
                            let credentials = null;
                            try {
                                credentials = await this.fb.login();
                            } catch (error) {
                                // error happened while logging into facebook, let the user retry
                                console.error(error);
                                this.stateMachine.goToBeginning();
                                this.stateMachine.closeLoading();
                                return;
                            }
                            try {
                                await this.fb.registerPayjoyFacebook(credentials);
                            } catch (error) {
                                // error happened while registering the user with
                                // PayJoy, this is unrecoverable
                                this.stateMachine.setError(
                                    'Error occurred logging into Facebook: ' + error,
                                    StateDataService.ERROR_CODES.REGISTER_PAYJOY_FACEBOOK);
                                this.stateMachine.closeLoading();
                                return;
                            }
                            this.stateMachine.incrementPage();
                        }
                    })
                );
            } else if (this.stateMachine.isConnectWithoutFacebookButtonOn()) {

                this.enableConnectWithoutFacebook();
                console.log('enableConnectWithoutFacebook');
                this.subscription.add(
                    this.navFooter.$button.subscribe(async (val) => {
                        console.log('buttonlistener', val);
                        if (val === 'permissions') {
                            console.log('connect-without-facebook listener');
                            if (await this.registerUser()) {
                                this.stateMachine.incrementPage();
                            }
                        }
                    })
                );
            } else {
                this.turnOffOnboardingConnectButtons();
                console.log('turnOffOnboardingConnectButtons');
            }
        }
    }

    public async registerUser() {
        await this.stateData.set(StateDataService.KEYS.IS_FACEBOOK_ENABLED, 0);
        const userId = await this.stateData.get(StateDataService.KEYS.USER_ID);
        const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);
        const cashPlatformProfileId = await this.stateData.get(StateDataService.KEYS.PROFILE_ID);
        const market = await this.stateData.get(StateDataService.KEYS.MARKET);

        const response = await this.api.post(
            '/loans/v2/api/register-user-cash.php', {
                userId,
                deviceTag,
                cashPlatformProfileId,
                market
            }
        );
        if (response.hasOwnProperty('data')) {
            const data = response['data'];
            await this.stateData.set(StateDataService.KEYS.PROFILE_ID, data.cashPlatformProfileId);
            if (data.userId) {
                await this.stateData.set(StateDataService.KEYS.USER_ID, data.userId);
            }
            return data.cashPlatformProfileId;
        } else {
            throw new Error(JSON.stringify(response));
        }
    }

    public getHelpWhyDoesPayjoyUseLock() {
        this.help.openHelp('WHY_DOES_PAYJOY_LOCK_PHONE');
    }

    public async logSlideEvent(currentIndex) {
        // const currentIndex = await this.slides.getActiveIndex();
        this.api.logEvent('IntroSlide' + (currentIndex + 1));
    }

    private async enableConnect() {
        try {
            this.fb.setReadyToConnect(true);
            this.navFooter.setFacebookStyle();
            this.stateMachine.setDisplayReturnToLock(this.displayReturnToLock);
        } catch (err) {
            console.log(err);
        }
    }

    private async enableConnectWithoutFacebook() {
        try {
            this.navFooter.setConnectWithoutFacebookStyle();
            this.stateMachine.setDisplayReturnToLock(this.displayReturnToLock);
        } catch (err) {
            console.log(err);
        }
    }

    private async turnOffOnboardingConnectButtons() {
        try {
            this.navFooter.setStartOnboardingOff();
            this.stateMachine.setDisplayReturnToLock(this.displayReturnToLock);
        } catch (err) {
            console.log(err);
        }
    }
}