import { Component, ElementRef, OnInit } from '@angular/core';
import { PageComponent } from '@app/pages/_page/page.component';
import { OnboardingStateMachineService } from '../onboarding/onboarding-state-machine.service';

@Component({
    selector: 'app-await-manual-review',
    templateUrl: './await-manual-review.component.html',
    styleUrls: ['./await-manual-review.component.scss'],
})
export class AwaitManualReviewComponent extends PageComponent implements OnInit {

    constructor(
        protected elementRef: ElementRef,
        private stateMachine: OnboardingStateMachineService
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'AwaitManualReviewComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.navFooter.setHiddenStyle();
        this.subscription.add(this.stateMachine.pollIncrementPage(10000).subscribe());
    }
}