import { AfterViewInit, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { SignaturePad } from 'angular2-signaturepad/signature-pad';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';
import { StateDataService } from '../../api/state-data.service';
import { PageComponent } from '@app/pages/_page/page.component';

@Component({
    selector: 'app-sign-circulo',
    templateUrl: './sign-circulo.component.html',
    styleUrls: ['./sign-circulo.component.scss'],
    providers: [SignaturePad],
})

export class SignCirculoComponent extends PageComponent implements OnInit, AfterViewInit {
    @ViewChild(SignaturePad, { static: false }) signaturePad: SignaturePad;
    public signaturePadOptions: Object = {
        'minWidth': 5,
        'canvasWidth': 400, // takes up whole screen, this should be modified to fit design
        'canvasHeight': 100
    };

    private enableClear = true;
    private contractRejected = false;

    constructor(
        public stateMachine: OnboardingStateMachineService,
        protected elementRef: ElementRef,
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'SignCirculoComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.stateMachine.endTransition();
        this.navFooter.setCancelAcceptStyle();
        this.subscription.add(this.stateMachine.$nextButton.subscribe(val => {
            this.drawComplete()
        }));
        this.subscription.add(this.stateMachine.$clearButton.subscribe(val => {
            this.rejectContract()
        }));
    }

    public ngAfterViewInit() {
        super.ngAfterViewInit();
        // this.signaturePad is now available
        this.signaturePad.on();
        this.signaturePad.set('minWidth', 2); // indicates thickness of pen, can be modified
        this.signaturePad.clear(); // invoke functions from szimek/signature_pad API
    }

    public drawStart() {
        this.stateMachine.enableNext();
        this.navFooter.setSubmitButtonEnabled(true) //enable submit
    }

    public drawContinue() {
        this.navFooter.setSubmitButtonEnabled(true) //enable submit
    }

    public clearAndDisableSubmit() {
        if (this.enableClear) {
            this.signaturePad.clear();
            this.navFooter.setSubmitButtonEnabled(false) //disable submit
        }
    }

    public returnToContract() {
        this.contractRejected = false;
        this.stateMachine.enableNext();
    }

    public showPopup() {
        return this.contractRejected;
    }

    private async drawComplete() {
        // call this when continue button is hit (right now there's a submit button but we'll want to change that)
        // trim base64 image header (data:image/png;base64,) for ease of server side decoding
        this.postSignature(this.signaturePad.toDataURL(), 'CIRCULO_CREDIT_PULL_AUTH');
    }


    private rejectContract() {
        this.stateMachine.disableNext();
        this.contractRejected = true;
    }

    private async postSignature(signatureData: any, signatureType: any) {
        this.signaturePad.off();
        this.enableClear = false;
        const data = {
            userId: String(await this.stateData.get(StateDataService.KEYS.USER_ID)),
        };

        try {
            const response = await this.api.postImages(
                '/loans/v2/api/save-signatures.php', data, [signatureData.slice(22)], [signatureType]
            );
            const success = response['valid'];
            if (success) {
                //everything is good! move on from here
                this.stateMachine.markTaskComplete(StateDataService.KEYS.TASK_ACCEPT_CIRCULO_CONTRACT);
                this.api.logEvent('user accepted circulo contract');
                this.stateMachine.beginTransition();
                setTimeout(function () {
                    this.stateMachine.incrementPage();
                }.bind(this), 2000);
            } else {
                this.stateMachine.setError(
                    'error occured on signature upload: ' + response['message'],
                    StateDataService.ERROR_CODES.SIGNATURE_UPLOAD);
            }
        } catch (error) {
            this.stateMachine.setError(
                'Error occurred on post save-signatures: ' + error,
                StateDataService.ERROR_CODES.POST_SAVE_SIGNATURES);
        }
    }
}