import { Platform } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { Injectable } from '@angular/core';

const LNG_KEY = 'SELECTED_LANGUAGE';
const DEFAULT_LANG = 'es';
const SUPPORTED_LANG: string[] = ['es', 'en'];

// this is temporary and can be replaced once the logic is pushed serverside
function normalizeLanguageCode(code) {
    const detectedLang = code.slice(0, 2).toLowerCase();
    console.log('detectedLang:', detectedLang);
    if (SUPPORTED_LANG.includes(detectedLang)) {
        return detectedLang;
    } else {
        return DEFAULT_LANG;
    }
}

@Injectable({
    providedIn: 'root'
})

export class LanguageService {

    static LONG_DATE_OPTIONS = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
    static NUMERIC_DATE_OPTIONS = { day: '2-digit', month: '2-digit', year: 'numeric' };

    public languageCode = 'es';

    constructor(
        private translate: TranslateService,
        private plt: Platform,
    ) {
        this.languageCode = normalizeLanguageCode(navigator.language);
        this.setLang();
    }

    public setLang() {
        this.translate.setDefaultLang(this.languageCode);
        this.translate.use(this.languageCode);
    }
}