import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { OfferData } from '../get-offers.types';

@Component({
  selector: 'app-branded-gift-card',
  templateUrl: './branded-gift-card.component.html',
  styleUrls: ['./branded-gift-card.component.scss'],
})
export class BrandedGiftCardComponent implements OnInit {
  @Input() description: string;
  @Input() bgColor: string = 'white';
  @Input() selectedOffer: OfferData;
  @Output() onCardClick = new EventEmitter();

  public browserLanguage = 'es-MX';

  constructor() {
    this.browserLanguage = window.navigator.language;
  }

  ngOnInit() {}

  public handleCardClick(): void {
    this.onCardClick.emit();
  }

}
