import { Component, NgZone } from '@angular/core';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';
import { HelpService } from '../../services/help.service';
import { TranslateService } from '@ngx-translate/core';
import { LanguageService } from '../../services/language.service'


@Component({
    selector: 'app-nav-header',
    templateUrl: './nav-header.component.html',
    styleUrls: ['./nav-header.component.scss', './nav-header-tooltip.component.scss'],
})
export class NavHeaderComponent {

    constructor(
        public help: HelpService,
        public translate: TranslateService,
        private stateMachine: OnboardingStateMachineService,
        private zone: NgZone,
        public language: LanguageService
    ) {
        console.log('language', this.language.languageCode);
    }

    ngAfterViewInit() {
        var helpTip = document.getElementById('helpTip');

        var _removeTooltip = function() {
            helpTip.classList.remove('showTooltip');
            console.log('removed tooltip!');
            document.removeEventListener("click", _removeTooltip);
        }
        document.addEventListener("click", _removeTooltip);

        if (this.stateMachine.isHomeScreen()) {
            if (this.language.languageCode !== 'en') {
                // #to figure out why this.translate.instant('HELP.TOOLTIP') only works sometimes
                helpTip.dataset.tooltip = "Ests a un click de recibir ayuda personalizada de nuestro equipo. Estamos aqu para ayudarte!";
            } else {
                helpTip.dataset.tooltip = "You are just a click away from personalized help from our team. We are always here to help.";
            }

            helpTip.classList.add('showTooltip');
            setTimeout(function() {
                helpTip.classList.remove('showTooltip');
            }, 3000);
        }
    }

    public getLogoContrastColor() {
        return (this.stateMachine.getHeaderClass() === 'transition-background') ? '#fff' : '#656363';
    }

    public getHelpIconColor() {
        return (this.stateMachine.getHeaderClass() === 'transition-background') ? '#fff' : 'primary';
    }

    public getHelpColor() {
        return (this.stateMachine.getHeaderClass() === 'transition-background') ? 'light' : 'primary';
    }

    public showHelp() {
        if (this.stateMachine.isDeclinedState() || this.stateMachine.hasError()) {
            return true;
        } else if (!this.stateMachine.hasInternetConnection()) {
            return true;
        } else if (this.stateMachine.isTransitioning()) {
            return false;
        }
        return true;
    }

    public showHeader() {
        if (this.stateMachine.isTransitioning() || this.stateMachine.userAwaitingAction()) {
            return false;
        }
        if (this.stateMachine.requestingManualReview()) {
            return false;
        }
        return true;
    }
}