import { Injectable } from '@angular/core';
import { OnboardingStateMachineService } from '../onboarding/onboarding-state-machine.service';
import { LoadingController, Platform } from '@ionic/angular';
import { ApiService } from '../api/api.service';
import { AppsFlyerService } from '../api/appsflyer.service';
import { FacebookService } from '../services/facebook.service';
import { StateDataService } from '../api/state-data.service';
import { TranslateService } from '@ngx-translate/core';
import { PluginService } from '../api/plugin.service';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { Network } from '@ionic-native/network/ngx';

// *not* `providedIn: root` since we need to control which subclass of the
// OnboardingStateMachineService is used
@Injectable()
export class CashStateMachineService extends OnboardingStateMachineService {

    // SUB_STATES are needed because some of our states actually represent
    // 2 or more distinct states
    public static readonly SUB_STATES = {
        PERMISSIONS_GIVEN: 'permissions-given',
        REQUESTING_MANUAL_REVIEW: 'requesting-manual-review'
    };

    public static TASKS = {
        ACCEPT_TERMS_AND_CONDITIONS: 0,
        ID_VERIFICATION: 0,
        PHONE_VERIFICATION: 0,
        ACCEPT_CIRCULO_CONTRACT: 0,
    };

    constructor(
        protected api: ApiService,
        protected appsFlyer: AppsFlyerService,
        protected fb: FacebookService,
        protected loadingController: LoadingController,
        protected network: Network,
        protected platform: Platform,
        protected plugin: PluginService,
        protected splashscreen: SplashScreen,
        protected stateData: StateDataService,
        protected translate: TranslateService
    ) {
        super(api, loadingController, network, platform, plugin, splashscreen, stateData, translate);
        this.addStates({
            OFFERS: 'offers',
            TRANSITION: 'transition',
            CIRCULO: 'circulo',
        });

        if (!this.isEcuador()) {
            this.initializeTasks([
                StateDataService.KEYS.TASK_ACCEPT_TERMS_AND_CONDITIONS,
                StateDataService.KEYS.TASK_ID_VERIFICATION,
                StateDataService.KEYS.TASK_PHONE_VERIFICATION,
                StateDataService.KEYS.TASK_ACCEPT_CIRCULO_CONTRACT,
                ]);
        } else {
            this.initializeTasks([
            StateDataService.KEYS.TASK_ID_VERIFICATION,
            StateDataService.KEYS.TASK_PHONE_VERIFICATION,
            StateDataService.KEYS.TASK_ACCEPT_CIRCULO_CONTRACT,
        ]);
        }
    }

    public async initialize() {
        await this.appsFlyer.initialize();
        super.initialize();

        this.resetTasks();
        this.updateTasks();
    }


    public getPageClass() {
        if (!(this.isHelpNeeded() ||
            this.isDeclinedState() ||
            !this.hasInternetConnection())) {
            if (this.isTransitioning() ||
                this.userAwaitingAction() ||
                this.requestingManualReview()) {
                return 'transition-background';
            }

            if (this.displayingFoundOffers() || this.getCurrentPage() === this.states.OFFER) {
                return 'offers-background';
            }

            if (this.getCurrentPage() === this.states.HOME ||
                this.getCurrentPage() === this.states.SPLASH ||
                this.getCurrentPage() === this.states.COMPLETE) {
                return 'connect-background';
            }

            if (this.getCurrentPage() === this.states.LOAN_APPLICATION_PROCESS) {
                return 'loan-application-background';
            }
        }
        return super.getPageClass();
    }

    public async goToBeginning() {
        this.updateCurrentPage(this.states.HOME);
    }

    public isCashLoan() {
        return true;
    }

    public async isLoggedIn() {
        return this.fb.isLoggedIn();
    }

    public explainPermissions() {
        return true; // cash collects sms/contacts/apps
    }

    public userAwaitingAction() {
        return this.getCurrentPage() === this.states.AWAIT_MANUAL_REVIEW ||
            this.getCurrentPage() === this.states.LOAN_APPLICATION_IN_PROGRESS;
    }

    public showTel() {
        return false;
    }

    public showWhatsApp() {
        return true;
    }

    public isFooterHidden() {
        return (super.isFooterHidden() ||
            this.isDeclinedState());
    }
}