import { Component, OnInit } from '@angular/core';

import { Platform } from '@ionic/angular';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { preLoadImage, getTrailingUrlString } from '@shared/util';
import * as serviceWorkerConfig from '../../ngsw-config.json';
import * as angularConfig from '../../angular.json';
import { StateDataService } from './api/state-data.service';
import { ApiService } from './api/api.service';
import { environment } from '@app/env';


@Component({
    selector: 'app-root',
    templateUrl: 'app.component.html',
})

export class AppComponent implements OnInit {

    constructor(
        public stateData: StateDataService,
        private platform: Platform,
        private statusBar: StatusBar,
        private api: ApiService,
    ) {}

    async ngOnInit() {
      this.platform.ready().then(async () => {
        await this.stateData.initialize();
        await this.checkProceedToLockScreen();
        this.statusBar.styleDefault();
        this.preloadOfflineImages();
        this.storeBundleVersion();
      });
    }

    async checkProceedToLockScreen(){
      const statePacket = await this.api.getStatePacket();
      if (statePacket['nextState'] === 'complete') {
        this.stateData.setAppMode('lock');
      }
    }

    preloadOfflineImages(): void {
        const offlineImagesList = serviceWorkerConfig.assetGroups.find(item => item.name === 'offline-essential-images');
        if (offlineImagesList && offlineImagesList.resources) {
            offlineImagesList.resources.files.forEach(path => {
                const baseHref = angularConfig.projects.app.architect.build.options['baseHref'] || '';
                preLoadImage(baseHref + path);
            });
        }
    }

    storeBundleVersion(): void {
        const currentHref = window.location.href;
        const bundleVersion = environment.production ? getTrailingUrlString(currentHref) : '1.0.0';
        if (bundleVersion) {
            this.stateData.set(StateDataService.KEYS.APP_BUNDLE_VERSION, bundleVersion);
        }
    }
}