import { Component, OnDestroy, OnInit } from '@angular/core';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';

@Component({
    selector: 'app-transition',
    templateUrl: './transition.component.html',
    styleUrls: ['./transition.component.scss']
})
export class TransitionComponent implements OnInit, OnDestroy {
    public completedTasks: any = [];
    public uncompletedTasks: any = [];
    public tasksLoaded = false;

    constructor(
        private stateMachine: OnboardingStateMachineService
    ) {
    }


    public ngOnInit() {
        this.completedTasks = this.stateMachine.getTasks(1);
        this.uncompletedTasks = this.stateMachine.getTasks(0);
        this.tasksLoaded = true;
    }

    public ngOnDestroy() {
        this.stateMachine.endTransition();
    }
}