import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { SignaturePad } from 'angular2-signaturepad/signature-pad';
import { HttpErrorResponse } from '@angular/common/http';
import { PageComponent } from '@app/pages/_page/page.component';
import { StateService } from '../../loan-application/state.service';
import { HelpService } from '../../services/help.service';
import { StateMachineService } from '../../api/state-machine.service';
import { CashStateMachineService } from '../../cash/cash-state-machine.service';
import { LoanService } from '../../services/loan.service';
import { LanguageService } from '../../services/language.service'
import { StateDataService } from 'src/app/api/state-data.service';
import { Subscription } from 'src/app/api/state-machine.imports';


@Component({
    selector: 'app-contract',
    templateUrl: './contract.component.html',
    styleUrls: ['./contract.component.scss'],
    providers: [
        SignaturePad,
        { provide: StateMachineService, useExisting: CashStateMachineService },
    ],
})

export class ContractComponent extends PageComponent implements OnInit {
    public static readonly CONTRACT_FILLER = '<div class="wait"><h3>Un momento. Generando tu contrato...</h3></div>';
    public readonly disbursementMethods = StateService.DISBURSEMENT_METHODS;
    private offerKey: string;
    private deviceManufacturer: string;
    private saveSignatureSubcription: Subscription;

    contractHtml = '';
    guidanceOn = false;
    drawStarted = false;
    congrats = false;
    signatureData = '';
    isHuawei = true;

    constructor(
        protected elementRef: ElementRef,
        private state: StateService,
        private help: HelpService,
        private stateMachine: CashStateMachineService,
        private loan: LoanService,
        public language: LanguageService
    ) {
        super(elementRef);
        this.initializeContractHtml();
    }

    @ViewChild(SignaturePad, null) signaturePad: SignaturePad;
    private signaturePadOptions: Object = {
        'minWidth': 5,
        'canvasWidth': 1500, // takes up whole screen, this should be modified to fit design #todo
        'canvasHeight': 100
    };

    public async ngOnInit() {
        super.ngOnInit();
        this.offerKey = await this.loan.getOfferData('partnerOfferKey');
        this.deviceManufacturer = await this.stateData.get(StateDataService.KEYS.MANUFACTURER);

        if (this.state.getTermsAccepted()) {
            this.guidanceOn = true;
            this.configureGuidanceNavFooter();
        } else {
            this.navFooter.setHiddenStyle();
            await this.getContract();
        }
    }

    private async getContract() {
        const data = {
            userId: await this.stateData.get(StateDataService.KEYS.USER_ID),
            deviceTag: await this.stateData.get(StateDataService.KEYS.DEVICE_TAG),
            locale: await this.stateData.get(StateDataService.KEYS.LOCALE),
            offerKey: this.offerKey,
            product: 'cash'
        }
        this.state.apiPost('/get-contract', data).subscribe(
            res => {
                console.log(res);
                const success = res['valid'];
                if (success) {
                    this.state.setContractHtml(res['data']);
                    this.configureSignatureNavFooter();
                } else {
                    this.stateMachine.setError(res['message'], StateDataService.ERROR_CODES.GET_CONTRACT);
                    console.log(res['message']) // logs the error message
                }
            },
            (err: HttpErrorResponse) => {
                this.stateMachine.setError(err.message, StateDataService.ERROR_CODES.POST_GET_CONTRACT);
                console.log('Error occurred on post get-contract: ', err.message);
            }
        );
    }

    private configureSignatureNavFooter() {
        this.stateMachine.disableNext();
        this.navFooter.setNextStyle();
        // ToDo: Add translate JSON string
        this.navFooter.setNextText('Acepto Trminos y Condiciones');
        this.saveSignatureSubcription = this.stateMachine.$nextButton.subscribe(val => {
            this.saveSignature();
        })
        this.subscription.add(this.saveSignatureSubcription);
    }

    private configureGuidanceNavFooter() {
        if (this.saveSignatureSubcription && !this.saveSignatureSubcription.closed) {
            this.saveSignatureSubcription.unsubscribe();
        }
        this.stateMachine.enableNext();
        // ToDo: Add translate JSON string
        this.navFooter.setTextBelowStyle('Cancelar');
        this.navFooter.setNextText('Aceptar');
        this.subscription.add(
            this.stateMachine.$nextButton.subscribe(val => {
                this.sendContractSignature();
            })
        );
        this.subscription.add(
            this.navFooter.$button.subscribe(val => {
                if (val === 'text-below') {
                    this.cancelSubmission();
                }
            })
        );
    }

    public async sendContractSignature() {
        this.navFooter.setOnboardingLoadingStyle();
        this.state.setSubmittingSignatureOn();
        this.state.apiPost('/initiate-loan-processing', {
            signature: this.state.getSignatureData().slice(22),
            offerKey: this.offerKey,
            clabeNumber: this.state.getClabeNumber(),
            paymentType: this.state.getDisbursementMethod()
        }
        ).subscribe(
            res => {
                console.log('loan processing initiated');
                const success = res['valid'];
                if (success) {
                    console.log(res['message']);
                    this.stateData.set(StateDataService.KEYS.LOAN_CONTRACT_IS_SIGNED, true);
                    this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_TRANSITION);
                } else {
                    console.error(res['message']) // logs the error message
                    this.stateMachine.setError(res['message'], StateDataService.ERROR_CODES.INITIATE_LOAN_PROCESSING);
                }
            },
            (err: HttpErrorResponse) => {
                this.state.setSubmittingSignatureOff();
                this.stateMachine.setError(err.message, StateDataService.ERROR_CODES.POST_INITIATE_LOAN_PROCESSING);
                console.error('Error occurred on post save-signatures: ', err.message);
            }
        );
    }

    public getName(): string {
        return 'ContractComponent';
    }

    ngAfterViewInit() {
        // this.signaturePad is now available
        this.signaturePad.set('minWidth', 2); // indicates thickness of pen, can be modified
        this.signaturePad.clear(); // invoke functions from szimek/signature_pad API
    }

    drawStart() {
        this.drawStarted = true;
        this.stateMachine.enableNext();
    }

    showSubmitButton() {
        return (this.contractLoaded() && this.drawStarted && !this.state.getSubmittingSignature());
    }

    async delay(ms: number) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    saveSignature() {
        console.log("saveSignature");
        this.state.setTermsAccepted();
        this.state.setSignatureData(this.signaturePad.toDataURL()); // #todo signatureType; trim base64 image header (data:image/png;base64,) for ease of server side decoding
        this.signaturePad.clear(); // #todo
        if (!this.state.availableDisbursementMethods[this.disbursementMethods.WALMART_GIFT_CARD]) {
            this.runTransition();
        } else {
            this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_WALMART_DISBURSEMENT);
        }
    }

    async runTransition() {
        this.stateMachine.beginTransition();
        this.congrats = true;
        await this.delay(5000);
        this.congrats = false;
        this.stateMachine.endTransition();
        this.guidanceOn = true;
        this.configureGuidanceNavFooter();
    }

    acceptButtonClass() {
        if (this.contractLoaded() && this.drawStarted) {
            return 'fullButtonEnabled';
        }
        return 'fullButtonDisabled';
    }

    cancelSubmission() {
        this.stateMachine.updateCurrentPage(this.stateMachine.states.OFFERS)
        this.guidanceOn = false;
    }

    contractLoaded() {
        if (this.state.getContractHtml() === ContractComponent.CONTRACT_FILLER) {
            return false;
        } else if (this.state.getContractHtml() === '') {
            return false;
        }
        return true;
    }

    initializeContractHtml() {
        // this.contractHtml = this.state.getContractHtml();
        if (!this.state.getContractHtml()) {
            this.state.setContractHtml(ContractComponent.CONTRACT_FILLER);
        }
    }

    getContractHtml() {
        return this.state.getContractHtml();
    }

    clearAndDisableSubmit() {
        this.drawStarted = false;
        this.signaturePad.clear();
        this.state.clearSignatureData();
        this.stateMachine.disableNext();
    }


    showExplain() {
        this.help.openHelp('LOAN_APPLICATION_PERMISSIONS');
    }

    showHuaweiGuidance() {
        return this.deviceManufacturer === 'HUAWEI';
    }

    showGeneralGuidance() {
        return !this.showHuaweiGuidance();
    }

    showPrivacy() {
        this.help.openHelp('LOAN_APPLICATION_PRIVACY');
    }

}
