import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { TranslateLoader, TranslateModule, TranslateService } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { ComponentsModule } from '@app/components/components.module';
import { LockComponent } from './lock.component';
import { MenuComponent } from './menu/menu.component';
import { HomeComponent } from './home/home.component';
import { ProfileComponent } from './profile/profile.component';
import { PayComponent } from './pay/pay.component';
import { HelpComponent } from './help/help.component';
import { LoanComponent } from './loan/loan.component';
import { WithdrawalComponent } from './withdrawal/withdrawal.component';
import { ProgressBarComponent } from './progress-bar/progress-bar.component';
import { IonicModule } from '@ionic/angular';
import { ErrorModule } from '../error/error.module';
import { NoConnectionModule } from '../no-connection/no-connection.module';
import { PageModule } from '@app/pages/_page/page.module';
import { LanguageService } from '../services/language.service';

// this is duplicate code from cash.module.ts  should be refactored
export function createTranslateLoader(http: HttpClient) {
    return new TranslateHttpLoader(http, 'assets/i18n/', '.json');
}

@NgModule({
    declarations: [
        LockComponent,
        HomeComponent,
        MenuComponent,
        ProfileComponent,
        PayComponent,
        HelpComponent,
        LoanComponent,
        ProgressBarComponent,
        WithdrawalComponent
    ],
    imports: [
        CommonModule,
        IonicModule,
        TranslateModule.forRoot({
                loader: {
                    provide: TranslateLoader,
                    useFactory: (createTranslateLoader),
                    deps: [HttpClient]
                }
            }
        ),
        ErrorModule,
        NoConnectionModule,
        PageModule,
        ComponentsModule
    ],
    exports: [
        LockComponent
    ],
    providers: [
        TranslateService
    ],
    bootstrap: [LockComponent],
})
export class LockModule {
    constructor(
        private languageService: LanguageService
    ) {
    }
}