import { Component, OnInit, ElementRef } from '@angular/core';
import { StateService } from '../../loan-application/state.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { CashStateMachineService } from '../../cash/cash-state-machine.service';
import { LoanService } from '../../services/loan.service';

@Component({
    selector: 'app-offer',
    templateUrl: './offer.component.html',
    styleUrls: ['./offer.component.scss']
})
export class OfferComponent extends PageComponent implements OnInit {
    public amount: string;
    public months: string;
    public period: string;
    public payment: string;
    public iva: number;
    public cat: string;
    public totalPayment: string;

    constructor(
        protected elementRef: ElementRef,
        public state: StateService,
        private stateMachine: CashStateMachineService,
        private loan: LoanService,
    ) {
        super(elementRef);
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.amount = await this.loan.getOfferData('amount');
        this.months = await this.loan.getOfferData('months');
        this.period = this.translate(await this.loan.getOfferData('paymentFrequency'));
        this.payment = await this.loan.getOfferData('payment');
        this.iva = Number(await this.loan.getOfferData('iva'));
        this.cat = await this.loan.getOfferData('cat');
        this.totalPayment = await this.loan.getOfferData('totalCost');
        this.navFooter.setNextStyle();
        this.stateMachine.enableNext();
        // ToDo: Add translate JSON string
        this.navFooter.setNextText('Firmar contrato de crdito');
        this.subscription.add(
            this.stateMachine.$nextButton.subscribe(val => {
                this.gotoSignaturePage();
            })
        );
    }

    public getName(): string {
        return 'OfferComponent';
    }

    public getPrefixCurrencyMark() {
        if (!this.stateMachine.isEcuador()) {
            return '$';
        } else {
            return '';
        }
    }

    public getSuffixCurrencyMark() {
        if (this.stateMachine.isEcuador()) {
            return 'USD';
        } else {
            return '';
        }
    }

    gotoSignaturePage() {
        this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_CONTRACT);
    }

    translate(inword: string) {
        if (inword == 'weekly') {
            return 'semanal';
        } else if (inword == 'monthly') {
            return 'mensual'
        } else {
            return inword;
        }
    }

}
