import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { Camera } from '@ionic-native/camera/ngx';

import { IonicModule } from '@ionic/angular';

import { CashComponent } from './cash.component';

import { StoreModule } from '@ngrx/store';
import { metaReducers, reducers } from '../reducers';
import { NativeStorage } from '@ionic-native/native-storage/ngx';

import { HttpClient, HttpClientModule } from '@angular/common/http';
import { HTTP } from '@ionic-native/http/ngx';
import { TranslateLoader, TranslateModule, TranslateService } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { SignaturePadModule } from 'angular2-signaturepad';
import { SignaturePad } from 'angular2-signaturepad/signature-pad';

import { OpenNativeSettings } from '@ionic-native/open-native-settings/ngx';
import { ComponentsModule } from '@app/components/components.module';
// App Pages
import { ConnectModule } from '@app/pages/connect/connect.module';
import { SignCirculoComponent } from '../onboarding/sign-circulo/sign-circulo.component';
import { OfferComponent } from '../offer/offer.component';
import { AwaitDeviceSecureComponent } from '../await-device-secure/await-device-secure.component';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { GetOffersComponentModule } from '../get-offers/get-offers.module';
import { ErrorModule } from '../error/error.module';
import { NoConnectionModule } from '../no-connection/no-connection.module';
import { OnboardingModule } from '../onboarding/onboarding.module';
import { PageModule } from '@app/pages/_page/page.module';
import { LanguageService } from '../services/language.service';
import { LoanApplicationModule } from '../loan-application/loan-application.module';

// XXX 20200925: Native integration disabled until we can get our app in the Play Store
// import { Facebook } from '@ionic-native/facebook/ngx';

export function createTranslateLoader(http: HttpClient) {
    return new TranslateHttpLoader(http, 'assets/i18n/', '.json');
}

@NgModule({
    declarations: [
        CashComponent,
        SignCirculoComponent,
        OfferComponent,
        AwaitDeviceSecureComponent,
    ],
    imports: [
        BrowserModule,
        IonicModule,
        HttpClientModule,
        TranslateModule.forRoot({
                loader: {
                    provide: TranslateLoader,
                    useFactory: (createTranslateLoader),
                    deps: [HttpClient]
                }
            }
        ),
        StoreModule.forRoot(reducers, { metaReducers }),
        CommonModule,
        FormsModule,
        SignaturePadModule,
        GetOffersComponentModule,
        ErrorModule,
        NoConnectionModule,
        OnboardingModule,
        ConnectModule,
        PageModule,
        ComponentsModule,
        LoanApplicationModule,
    ],
    exports: [
        CashComponent,
    ],
    providers: [
        Camera,
        SignaturePad,
        // XXX 20200925: Native integration disabled until we can get our app in the Play Store
        // Facebook,
        TranslateService,
        OpenNativeSettings,
        NativeStorage,
        HTTP
    ],
    bootstrap: [CashComponent],
    schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class CashModule {
    constructor(
        private languageService: LanguageService
    ) {
    }
}