import { ChangeDetectorRef, Component, ElementRef, OnDestroy, OnInit } from '@angular/core';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';
import { StateDataService } from '../../api/state-data.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { IdPhotoService } from '../../services/id-photo.service';
import { HelpService } from '../../services/help.service';
import { rotateImage } from '../../shared/util';

@Component({
    selector: 'app-id-photos',
    templateUrl: './id-photos.component.html',
    styleUrls: ['./id-photos.component.scss']
})
export class IdPhotosComponent extends PageComponent implements OnInit, OnDestroy {
    public postResults = '';
    public frontRotate: number;
    public backRotate: number;
    public idsSubmitted = false;
    public idsProcessed: any;
    public frontImage: any;
    public backImage: any;
    public frontPreview: any;
    public backPreview: any;
    public currentErrorName: string = null;
    public serverErrorNamesMap = {
        "FACE_NOT_FOUND_ON_ID": "CHECK_FACE_ON_ID",
        "FAILED_TO_CROP_FACE_FROM_ID": "CHECK_FACE_ON_ID",
        "UNRELIABLE_ID": "CHECK_IMAGE_QUALITY",
        "DEFAULT_ID_ERROR": "CHECK_IMAGE_QUALITY",
    };

    private rotate = rotateImage;

    constructor(
        public help: HelpService,
        public stateMachine: OnboardingStateMachineService,
        protected elementRef: ElementRef,
        private cdRef: ChangeDetectorRef,
        private idPhoto: IdPhotoService,
    ) {
        super(elementRef);
        if (this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_EXISTS)[0] &&
            this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_EXISTS)[1]) {
            this.stateMachine.enableNext();
        } else {
            this.stateMachine.disableNext();
        }
    }

    public getName(): string {
        return 'IdPhotosComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.stateMachine.endTransition();
        this.navFooter.setNextStyle();
        this.subscription.add(this.stateMachine.$nextButton.subscribe(val => {
            this.idsSubmitted = true;
            this.idsProcessed = false;
            this.submitIds();
        }));
        this.subscription.add(this.idPhoto.$frontUpload.subscribe(val => {
            this.cdRef.markForCheck();
        }));
        this.subscription.add(this.idPhoto.$backUpload.subscribe(val => {
            this.cdRef.markForCheck();
        }));

        this.frontImage = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_ENTRY)[0];
        this.backImage = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_ENTRY)[1];

        this.frontPreview = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_PREVIEW)[0];
        this.backPreview = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_PREVIEW)[1];

        this.frontRotate = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_ROTATE)[0];
        this.backRotate = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_ROTATE)[1];
    }

    public ngOnDestroy() {
        super.ngOnDestroy();
        this.stateMachine.closeLoading();
    }

    public displayImageBackground(tag = 'front') {
        if (tag === 'front') {
            if (this.frontImage) {
                return 'preview preview-nobackground';
            }
        } else if (tag === 'back') {
            if (this.backImage) {
                return 'preview preview-nobackground';
            }
        }
        return 'preview preview-background';
    }

    public retakeRequired() {
        return this.idPhoto.isIdRetakeRequired();
    }

    public handleOpenCamSuccess(side = 'front', imageDataUrl) {
        // imageData is a base64 encoded string
        if (side === 'front') {
            this.setImage(imageDataUrl, null);
            this.setPreview(imageDataUrl, null);
        } else {
            this.setImage(null, imageDataUrl);
            this.setPreview(null, imageDataUrl);
        }
        if (this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_EXISTS)[0] &&
            this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_EXISTS)[1]) {
            this.stateMachine.enableNext();
        }
    }

    public handleOpenCamError(err: string) {
        this.stateMachine.setError(JSON.stringify(err), StateDataService.ERROR_CODES.TAKE_ID_PICTURE);
    }

    public handleRotatePreview(side = 'front', rotateDeg) {
        if (side === 'front') {
            this.setRotate(rotateDeg, null);
        } else {
            this.setRotate(null, rotateDeg);
        }
    }

    private setImage(front = null, back = null) {
        if (front) {
            this.frontImage = front;
            this.idPhoto.setFrontUpload(true);
            this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_EXISTS, true, null, true);
        }
        if (back) {
            this.backImage = back;
            this.idPhoto.setBackUpload(true);
            this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_EXISTS, null, true, true);
        }
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_ENTRY, front, back, true);
    }

    private setPreview(front = null, back = null) {
        if (front) {
            this.frontPreview = front;
            this.setRotate(0, null);
        }
        if (back) {
            this.backPreview = back;
            this.setRotate(null, 0);
        }
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_PREVIEW, front, back, true);
    }


    private submitIds() {
        this.stateMachine.disableNext();
        this.postPhotos();
    }

    private async postPhotos() {
        const data = {
            userId: String(await this.stateData.get(StateDataService.KEYS.USER_ID)),
            deviceTag: String(await this.stateData.get(StateDataService.KEYS.DEVICE_TAG)), // this isn't being used yet
            cashPlatformProfileId: String(await this.stateData.get(StateDataService.KEYS.PROFILE_ID))
        };

        this.doRotate(this.frontRotate, this.backRotate);
        this.currentErrorName = null;
        try {
            const res = await this.api.postImages(
                '/loans/v2/api/upload-id-photo-router.php', data,
                [this.frontImage, this.backImage],
                ['frontId', 'backId']);

            if (res['valid']) {
                //ids successfully uploaded
                console.log('ids successfully uploaded');
                const resultData = res['data'];
                if (resultData['result'] === 'retake required') {
                    console.log('id illegible; retake required');
                    this.currentErrorName = resultData["errorName"] || null;
                    console.log("Error name: "+this.currentErrorName);
                    this.api.logEvent('ids uploaded, retake required', data);
                    //reset screen
                    this.resetIdPhotos();
                    this.idPhoto.requireIdRetake();
                } else {
                    // get userId from upload
                    await this.stateData.set(StateDataService.KEYS.USER_ID, resultData['userId']);
                    this.idPhoto.resetIdRetake();
                    this.stateMachine.incrementPage();
                    this.api.logEvent('id uploaded', data);
                }
            } else {
                this.resetIdPhotos();
                this.stateMachine.setError('Id upload failed: ' + res['message'], StateDataService.ERROR_CODES.UPLOAD_ID_PHOTOS);
            }
        } catch (error) {
            this.resetIdPhotos();
            this.stateMachine.setError(
                'Error occurred on post upload-id-photo: ' + error,
                StateDataService.ERROR_CODES.POST_UPLOAD_ID_PHOTOS);
        }
    }

    public getErrorNameString() {
        const errorNameStringkey = this.serverErrorNamesMap[this.currentErrorName] || this.serverErrorNamesMap.DEFAULT_ID_ERROR;
        return `ID_PHOTOS.${errorNameStringkey}`;
    }

    private resetIdPhotos() {
        this.stateMachine.disableNext();
        this.idsProcessed = true;
        this.setImage('', '');
        this.idPhoto.setFrontUpload(false);
        this.idPhoto.setBackUpload(false);
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_EXISTS, false, false, true);
    }

    private setRotate(frontNum, backNum) {
        if (frontNum !== null) {
            this.frontRotate = frontNum;
        }
        if (backNum !== null) {
            this.backRotate = backNum;
        }
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.ID_ROTATE, frontNum, backNum, true);
    }

    private doRotate(front = 0, back = 0) {
        const frontRotations = front / 90;
        const backRotations = back / 90;
        this.rotateImage('front', frontRotations);
        this.rotateImage('back', backRotations);
    }

    private rotateImage(side = 'front', num = 0) {
        if (side === 'front') {
            this.setImage(this.rotate(this.frontImage, num), null);
        } else {
            this.setImage(null, this.rotate(this.backImage, num));
        }
    }
}