import { Component, ElementRef, OnInit } from '@angular/core';
import { CashStateMachineService } from '../cash/cash-state-machine.service';
import { StateDataService } from '../api/state-data.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { LoanService } from '../services/loan.service';
import { HelpService } from '../services/help.service';

@Component({
    selector: 'app-get-offers',
    templateUrl: './get-offers.component.html',
    styleUrls: ['./get-offers.component.scss'],
})

export class GetOffersComponent extends PageComponent implements OnInit {

    selectedOffer = null;
    offers: any = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS);
    offersStatus: any = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS_STATUS);
    totalOffers: any = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.TOTAL_OFFERS);
    timeLimit = 3600;
    channelType: string;

    constructor(
        public help: HelpService,
        public stateMachine: CashStateMachineService,
        protected elementRef: ElementRef,
        private loan: LoanService,
    ) {
        super(elementRef);
        this.stateMachine.enableNext();
    }

    public getName(): string {
        return 'GetOffersComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.stateMachine.beginTransition();
        this.navFooter.setHiddenStyle();
        this.channelType = await this.stateData.get(StateDataService.KEYS.CHANNEL_TYPE);
        await this.loan.cancelLoanApplication();
        if (this.offersStatus && this.offersStatusStillValid(Math.floor(Date.now() / 1000))) {
            this.stateMachine.endTransition();
        } else {
            this.stateMachine.beginTransition();
            this.resetOffersPage();
        }
    }

    public async goToOffer(offer) {
        await this.loan.setOffer(offer);
        this.stateMachine.updateCurrentPage(this.stateMachine.states.OFFER);
    }

    public handleOfferSliderChange(offer) {
        this.selectedOffer = offer;
    }

    private offersStatusStillValid(time) {
        return (time - this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS_TIMESTAMP) <= this.timeLimit);
    }

    private resetOffersPage() {
        this.stateMachine.beginTransition();
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS_STATUS, '');
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS, false);
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.TOTAL_OFFERS, 0);
        this.offers = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS);
        this.offersStatus = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS_STATUS);
        this.totalOffers = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.TOTAL_OFFERS);
        this.getOffers();
    }

    private resetOffers() {
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS_STATUS, '');
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS, false);
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.TOTAL_OFFERS, 0);
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS_TIMESTAMP, Math.floor(Date.now() / 1000));
    }

    private async getOffers() {
        const payload = {
            userId: await this.stateData.get(StateDataService.KEYS.USER_ID),
            deviceTag: await this.stateData.get(StateDataService.KEYS.DEVICE_TAG),
            country: await this.stateData.get(StateDataService.KEYS.MARKET)
        };
        console.log('posting to get offers', payload);
        try {
            const response = await this.api.post(
                '/loans/v2/api/get-offers.php', payload);
            const success = response['valid'];
            if (success) {
                this.foundOffers();
                this.offers = response['data'];
                this.totalOffers = this.offers.length;
                this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.OFFERS, this.offers);
                this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.TOTAL_OFFERS, this.totalOffers);

            } else {
                const message = response['message'];
                this.stateMachine.endTransition();
                this.resetOffers();
                if (message === 'user not qualified') {
                    const rejectionReasons = response['data'];

                    if (rejectionReasons.includes('reject_finance_history')) {
                        this.offersStatus = 'existing_loan';
                    } else {
                        this.offersStatus = 'no_offers';
                    }
                } else {
                    this.stateMachine.setError(
                        'Error getting offers: ' + message,
                        StateDataService.ERROR_CODES.GET_OFFERS);
                }
            }
            this.stateMachine.endTransition();
            this.stateData.setUserProgress(
                StateDataService.USER_PROGRESS_KEYS.OFFERS_STATUS,
                this.offersStatus);
            this.stateData.setUserProgress(
                StateDataService.USER_PROGRESS_KEYS.OFFERS_TIMESTAMP,
                Math.floor(Date.now() / 1000));
        } catch (error) {
            this.stateMachine.endTransition();
            this.stateMachine.setError(
                'Error occurred on post getOffers: ' + error,
                StateDataService.ERROR_CODES.POST_GET_OFFERS);
        }
    }

    public isWalmartChannelType(): boolean {
        return this.channelType === 'walmart';
    }

    private foundOffers() {
        this.stateMachine.endTransition();
        this.offersStatus = 'offers_found';
        this.stateMachine.foundOffers();
    }
}