import * as Rollbar from 'rollbar'; // When using Typescript < 3.6.0.
// `import Rollbar from 'rollbar';` is the required syntax for Typescript 3.6.x.
// However, it will only work when setting either `allowSyntheticDefaultImports`
// or `esModuleInterop` in your Typescript options.

import {
    Injectable,
    Inject,
    InjectionToken,
    ErrorHandler
} from '@angular/core';

import { environment } from '@app/env';

export const RollbarService = new InjectionToken<Rollbar>('rollbar');

@Injectable()
export class RollbarErrorHandler implements ErrorHandler {
    constructor(@Inject(RollbarService) private rollbar: Rollbar) {}

    handleError(err:any) : void {
        this.rollbar.error(err.originalError || err);
        console.error(err.originalError || err);
    }
}

export function updateIdentity(rollbar, deviceTag) {
    rollbar.configure({payload: {username: deviceTag}});
}

export function rollbarFactory() {
    let env = 'production';
    if (!environment.production) {
        env = 'development';
    }
    const payload = {
        environment: env
    };

    const rollbarConfig = {
        accessToken: 'd4f230f4798d4b6e90ab8fd6457aa905',
        captureUncaught: true,
        captureUnhandledRejections: true,
        payload
    };

    return new Rollbar(rollbarConfig);
}