import { Component, ElementRef, NgZone, OnDestroy, OnInit } from '@angular/core';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';
import { StateDataService } from '../../api/state-data.service';
import { Subscription } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { PageComponent } from '@app/pages/_page/page.component';
import { PluginService } from '../../api/plugin.service';
import { HelpService } from '../../services/help.service';

@Component({
    selector: 'app-android-permissions',
    templateUrl: './android-permissions.component.html',
    styleUrls: ['./android-permissions.component.scss'],
})
export class AndroidPermissionsComponent extends PageComponent implements OnInit, OnDestroy {
    permissionsRejected = false;
    terms;

    constructor(
        public help: HelpService,
        public stateMachine: OnboardingStateMachineService,
        protected elementRef: ElementRef,
        private http: HttpClient,
        private ngZone: NgZone,
        private plugin: PluginService,
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'AndroidPermissionComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();

        // Do early setup of PJB on a Huawei EMUI 10+ (Android 10+) device, on which PJM
        // needs to get IMEI from PJB before the device can be registered:
        let sdkInt = await this.stateData.get(StateDataService.KEYS.SDK_INT);
        if (Number(sdkInt) >= 29) {
            this.api.logEvent('ActivationEarlySetupPjbOnHuaweiStart');
            await this.plugin.setupPjbOnHuawei();
        }

        this.subscription.add(
            this.navFooter.$button.subscribe(val => {
                if (val === 'text-below') {
                    this.rejectPermissions();
                }
            })
        );

        this.stateMachine.endTransition();
        this.navFooter.setTextBelowStyle('NAVIGATION.ALLOW');
        if (await this.stateData.get(StateDataService.KEYS.PERMISSIONS_GIVEN)) {
            this.allowPermissionsClicked();
        } else {
            this.stateMachine.enableNext();
            this.subscription.add(
                this.stateMachine.$nextButton.subscribe(val => {
                    this.allowPermissionsClicked();
                })
            );
        }

        this.getTerms();
    }

    public returnToPermissions() {
        this.permissionsRejected = false;
        this.stateMachine.enableNext();
    }

    public showPopup() {
        return this.permissionsRejected;
    }

    private async getTerms() {
        this.terms = await this.api.get('assets/docs/tos_es.html', {}, false);
    }

    private async rejectPermissions() {
        this.stateMachine.disableNext();
        this.api.logEvent('user rejected permissions');
        this.permissionsRejected = true;
        await this.stateData.set(StateDataService.KEYS.PERMISSIONS_GIVEN, 0);
        this.stateMachine.clearCurrentSubPage();
    }

    private async acceptTermsClicked() {
        this.submitDeviceRegistration();
    }

    private async submitDeviceRegistration() {
        this.stateMachine.markTaskComplete(StateDataService.KEYS.TASK_ACCEPT_TERMS_AND_CONDITIONS);
        this.api.logEvent('user accepted terms');
        if (!this.showTerms()){
            this.stateData.set(StateDataService.KEYS.TERMS_AND_CONDITIONS_ACCEPTED, 1);
        }
        this.stateData.set(StateDataService.KEYS.PRIVACY_POLICY_ACCEPTED, 1);
        this.stateData.set(StateDataService.KEYS.DATA_COLLECTION_AUTHORIZED, 1);
        this.stateMachine.clearCurrentSubPage();
        this.stateMachine.beginTransition();

        try {
            let result = await this.plugin.register();
            this.api.logEvent('register done', result);
            this.api.registerAppInstall();

            if (this.stateMachine.isScoringNeeded()) {
                // Note: if either of these operations fail
                // (authorizeDataCollection or collectData) the user will be
                // prompted to retry, but if they opt not to (or the retry fails
                // too) and instead close and re-open the app, they will be moved
                // forward to the phone verification step without starting data
                // collection. This is because the deviceTag has been set by the
                // register() call above and it's the presence of a deviceTag
                // that determines whether we move to Phone Verify or stay on
                // Permissions.
                //
                // To handle this correctly we probably need the state machine
                // to be more sophisticated about checking for the presence of
                // SMS/Contact/App data on server.
                result = await this.api.authorizeDataCollection();
                this.api.logEvent('data collection authorized', result);

                result = await this.plugin.collectData();
                this.api.logEvent('data collection started', result);
            }

            console.log(await this.stateData.getStatePacket());
            this.stateMachine.incrementPage();
        } catch (error) {
            this.stateMachine.setError(
                'Registration error: ' + error,
                StateDataService.ERROR_CODES.REGISTER_DEVICE);
        }
    }

    private async allowPermissionsClicked() {
        try {
            const result = await this.plugin.requestPermissions();
            console.log('requestPermissions', result);
            this.stateMachine.disableNext();
            this.navFooter.setCheckboxStyle('BUTTON.ACCEPT');
            await this.stateData.set(StateDataService.KEYS.PERMISSIONS_GIVEN, 1);
            this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.PERMISSIONS_GIVEN);
            this.permissionsRejected = false;

            this.api.logEvent('user accepted permissions', result);
            this.subscription.unsubscribe();

            if (!this.showTerms()) {
                await this.submitDeviceRegistration();
            }
            else {

            this.subscription = new Subscription();
            this.subscription.add(
                this.navFooter.$checkbox.subscribe(val => {
                    this.agreeToTerms(val);
                })
            );
            this.subscription.add(
                this.stateMachine.$nextButton.subscribe(val => {
                    this.acceptTermsClicked();
                })
            );
        }
    
        } catch (error) {
            this.stateMachine.setError(
                'DevicePermissions.requestPermissions() error: ' + error,
                StateDataService.ERROR_CODES.DEVICE_PERMISSIONS);
        }
    }

    private showTerms(){
        return (!this.stateMachine.isEcuador());
    }


    private agreeToTerms(e) {
        if (e.currentTarget.checked) {
            this.stateMachine.disableNext();
            this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.TERMS_AND_CONDITIONS_CHECKED, false);
        } else {
            this.stateMachine.enableNext();
            // #todo: figure out how to make box pre-checked if this is 1
            this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.TERMS_AND_CONDITIONS_CHECKED, true);
        }
    }

}