import { Injectable, NgZone } from '@angular/core';
import { Platform } from '@ionic/angular';

declare var cordova: any;

@Injectable({
    providedIn: 'root'
})
export class PluginService {

    constructor(
        private ngZone: NgZone,
        private platform: Platform,
    ) {
    }

    public async requestPermissions() {
        return this.platform.ready().then(() => {
            return new Promise((resolve, reject) => {
                cordova.plugins.DevicePermissions.requestPermissions(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    }
                );
            });
        });
    }

    public async hasPermissions() {
        return this.platform.ready().then(() => {
            return new Promise((resolve, reject) => {
                cordova.plugins.DevicePermissions.hasPermissions(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    }
                );
            });
        });
    }

    public async register() {
        return this.platform.ready().then(() => {
            return new Promise((resolve, reject) => {
                cordova.plugins.DeviceActivation.register(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    }
                );
            });
        });
    }

    public async collectData() {
        return this.platform.ready().then(() => {
            return new Promise((resolve, reject) => {
                cordova.plugins.DeviceActivation.collectData(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    }
                );
            });
        });
    }

    public async isActivated() {
        return this.platform.ready().then(() => {
            return new Promise((resolve, reject) => {
                cordova.plugins.DeviceActivation.isActivated(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    }
                );
            });
        });
    }

    public async activate() {
        return this.platform.ready().then(() => {
            return new Promise((resolve, reject) => {
                cordova.plugins.DeviceActivation.activate(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    }
                );
            });
        });
    }

    public async setupPjbOnHuawei() {
        return this.platform.ready().then(() => {
            return new Promise((resolve, reject) => {
                cordova.plugins.DeviceActivation.setupPjbOnHuawei(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    }
                );
            });
        });
    }

    public async requestDeviceAdmin() {
        return this.platform.ready().then(() => {
            return new Promise((resolve, reject) => {
                cordova.plugins.DeviceActivation.requestDeviceAdmin(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    }
                );
            });
        });
    }

    public async secure(activationParameters) {
        return this.platform.ready().then(() => {
            return new Promise((resolve, reject) => {
                cordova.plugins.DeviceActivation.secure(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    },
                    activationParameters
                );
            });
        });
    }

    public async browse(url) {
        return this.platform.ready().then(() => {
            return new Promise<string>((resolve, reject) => {
                cordova.plugins.AppBrowser.browse(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    },
                    url);
            });
        });
    }

    public async closeBrowser() {
        return this.platform.ready().then(() => {
            return new Promise<boolean>((resolve, reject) => {
                cordova.plugins.AppBrowser.close(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    });
            });
        });
    }

    public async storageSet(key, value) {
        if (value !== null) {
            value = String(value);
        }
        return this.platform.ready().then(() => {
            return new Promise<boolean>((resolve, reject) => {

                cordova.plugins.DeviceStorage.set(
                    result => {
                        this.ngZone.run(() => {
                            resolve(true);
                        });
                    },
                    error => {
                        reject(false);
                    },
                    key, value
                );
            });
        });
    }


    public async storageGet(key) {
        return this.platform.ready().then(() => {
            return new Promise<string>((resolve, reject) => {
                cordova.plugins.DeviceStorage.get(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(error);
                        });
                    },
                    key);
            });
        });
    }


    public async storageRemove(key) {
        return this.platform.ready().then(() => {
            return new Promise<boolean>((resolve, reject) => {
                cordova.plugins.DeviceStorage.remove(
                    result => {
                        this.ngZone.run(() => {
                            resolve(true);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            reject(false);
                        });
                    },
                    key
                );
            });
        });
    }


    public async verifyNumber(phoneNumber) {
        return this.platform.ready().then(() => {
            return new Promise<string>((resolve, reject) => {
                cordova.plugins.DeviceActivation.verifyNumber(
                    result => {
                        this.ngZone.run(() => {
                            resolve(result);
                        });
                    },
                    error => {
                        this.ngZone.run(() => {
                            console.error('DeviceActivation.verifyNumber() error:' + error);
                            reject(error);
                        });
                    },
                    phoneNumber
                );
            });
        });
    }

    public openAndroidSettings() {
        cordova.plugins.settings.open('settings', () => {
                console.log('opened settings');
            },
            () => {
                console.log('failed to open settings');
            }
        );
    }

}
