import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { StateDataService } from '../api/state-data.service';
import { OnboardingStateMachineService } from '../onboarding/onboarding-state-machine.service';
import { ApiService } from '../api/api.service';
import { NavFooterService } from '@app/components/nav-footer/nav-footer.service';


@Injectable()
export class IdPhotoService {

    private retakeId = false;
    private showReviewPopup = false;

    private frontUpload = new Subject<boolean>();
    public $frontUpload = this.frontUpload.asObservable();
    private backUpload = new Subject<boolean>();
    public $backUpload = this.backUpload.asObservable();

    constructor(
        private stateMachine: OnboardingStateMachineService,
        private stateData: StateDataService,
        private api: ApiService,
        private navFooter: NavFooterService
    ) {
        this.frontUpload.next(false);
        this.backUpload.next(false);
    }

    public requireIdRetake() {
        this.retakeId = true;
    }

    public isIdRetakeRequired() {
        return this.retakeId;
    }

    public resetIdRetake() {
        this.retakeId = false;
    }

    public promptConfirmReview() {
        this.stateMachine.disableNext();
        this.setShowReviewPopup(true);
    }

    public undoIncorrectClicked() {
        this.stateMachine.enableNext();
        this.setShowReviewPopup(false);
    }

    public setShowReviewPopup(val) {
        this.showReviewPopup = val;
        this.navFooter.setConfirmDetailsButtonEnabled(!val);
    }

    public isShowReviewPopup() {
        return this.showReviewPopup;
    }

    public setFrontUpload(val) {
        this.frontUpload.next(val);
    }

    public setBackUpload(val) {
        this.backUpload.next(val);
    }

    public async incorrectClicked() {
        this.setShowReviewPopup(false);
        this.stateData.set(StateDataService.KEYS.USER_REQUESTED_ID_REVIEW, 1);
        try {
            const payload = { userId: String(await this.stateData.get(StateDataService.KEYS.USER_ID)) };
            const response = await this.api.post('/loans/v2/api/request-manual-review.php', payload);
            const success = response['valid'];
            if (success) {
                this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.REQUESTING_MANUAL_REVIEW);
                this.stateMachine.enableNext();
            } else {
                const message = response['message'];
                this.stateMachine.setError(
                    'Error requesting manual review: ' + message,
                    StateDataService.ERROR_CODES.MANUAL_REVIEW_REQUEST);
            }
        } catch (error) {
            this.stateMachine.setError(
                'Error occurred on post request-manual-review: ' + error,
                StateDataService.ERROR_CODES.POST_REQUEST_MANUAL_REVIEW);
        }
    }
}
