import { Component, ElementRef, OnInit } from '@angular/core';
import { LockStateMachineService } from '../lock-state-machine.service';
import { MenuController } from '@ionic/angular';
import { Profile } from '../../models/profile.model';
import { PageComponent } from '@app/pages/_page/page.component';
import { TranslateService } from '@ngx-translate/core';
import { PluginService } from '../../api/plugin.service';
import { HelpService } from '../../services/help.service';

@Component({
    selector: 'app-profile',
    templateUrl: './profile.component.html',
    styleUrls: ['./profile.component.scss', '../lock.scss'],
})
export class ProfileComponent extends PageComponent implements OnInit {
    public profileBlocks;

    constructor(
        public translate: TranslateService,
        protected elementRef: ElementRef,
        private stateMachine: LockStateMachineService,
        private menuControl: MenuController,
        private userProfile: Profile,
        private plugin: PluginService,
        private help: HelpService
    ) {
        super(elementRef);

        this.profileBlocks = [{
            subtitle: 'PERSONAL',
            leftColSize: '6',
            rightColSize: '6',
            items: [{
                left: this.translate.instant('LOCK.DEVICE_TAG'),
                right: this.stateData.getDeviceTag(),
            },
                {
                    left: this.translate.instant('LOCK.PHONE_NUMBER'),
                    right: this.stateMachine.userProfile.telephoneNumber,
                },
                {
                    left: 'Email',
                    right: this.stateMachine.userProfile.emailAddress,
                },
            ],
        },
            {
                subtitle: 'GENERAL',
                leftColSize: '12',
                rightColSize: '0',
                items: [{
                    left: '<a>' + this.translate.instant('LOCK.PHONE_SETTINGS') + '</a>',
                    function: this.plugin.openAndroidSettings
                },],
            },
            // {
            //     subtitle: 'Notificaciones',
            //     leftColSize: '9',
            //     rightColSize: '3',
            //     items: [{
            //             left: 'Notificarme antes de la fecha de pago',
            //             toggle: true,
            //         },
            //         {
            //             left: 'Notificarme va mensaje de texto',
            //             toggle: true,
            //         },
            //         {
            //             left: 'Mostrar notificaciones de la app',
            //             toggle: true,
            //         },
            //     ],
            // }
        ]
    }

    public getName(): string {
        return 'ProfileComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.api.logEvent('LockUserProfile', {
            currentPage: this.stateMachine.getCurrentPage(),
            helpType: this.help.getHelpType()
        });
    }

    public openMenu() {
        this.stateMachine.openMenu();
        this.menuControl.open();
    }

    public openHelp() {
        this.help.setHelpBackButton(this.stateMachine.states.MAIN_PROFILE);
        this.stateMachine.updateCurrentPage(this.stateMachine.states.MAIN_HELP);
    }
}