import { Injectable, Renderer2, RendererFactory2 } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as Clabe from 'clabe-validator';
import { environment } from '@app/env';

@Injectable({
    providedIn: 'root'
})


export class StateService {

    private renderer: Renderer2;

    public static readonly STATES = {
        ONBOARDING: 'onboarding',
        TRANSITION: 'transition',
        DISBURSEMENT: 'disbursement'
    };

    public static readonly CHANNEL_TYPES = {
        LANDING_PAGE: 'landingPage',
        WALMART: 'walmart',
        FORCED_UPGRADE: 'forcedUpgrade',
    };

    public static readonly DISBURSEMENT_METHODS = {
        CASH_TRANSFER: 'CashTransfer',
        WINDOW_PICKUP: 'WindowPickup',
        ATM_WITHDRAWAL: 'AtmWithdrawal',
        WALMART_GIFT_CARD: 'WalmartGiftCard',
    };

    public static readonly  PAYMENT_METHOD_AVAILABILITY_GENERAL = {
        [StateService.DISBURSEMENT_METHODS.CASH_TRANSFER]: true,
        [StateService.DISBURSEMENT_METHODS.WINDOW_PICKUP]: true,
        [StateService.DISBURSEMENT_METHODS.ATM_WITHDRAWAL]: false,
        [StateService.DISBURSEMENT_METHODS.WALMART_GIFT_CARD]: false
    };
    
      public static readonly  PAYMENT_METHOD_AVAILABILITY_WALMART = {
        [StateService.DISBURSEMENT_METHODS.CASH_TRANSFER]: false,
        [StateService.DISBURSEMENT_METHODS.WINDOW_PICKUP]: false,
        [StateService.DISBURSEMENT_METHODS.ATM_WITHDRAWAL]: false,
        [StateService.DISBURSEMENT_METHODS.WALMART_GIFT_CARD]: true
    };

    public static readonly DISBURSEMENT_STATES = {
        COMPLETE: 'Completed',
        EXPIRED: 'Expired',
        ERROR: 'Error',
        READY: 'Ready',
        PROCESSING: 'Processing'
    };


    public startUpScreen;
    private signatureData;
    private termsAccepted = false;
    private contractHtml;
    private disbursementMethod = null;
    private submittingSignature = false;
    private readonly apiBaseUrl = environment.API_BASE_URL + '/finance';
    public outsideBusinessHours = false;
    public onHolidayOrWeekend = false;
    public availableDisbursementMethods = null;

    public offer = {};
    public loan = {};

    public clabeNumber = '';
    public clabeCheck = null;

    constructor(
        private http: HttpClient,
        private rendererFactory: RendererFactory2,
    ) {
        this.renderer = this.rendererFactory.createRenderer(null, null);
    }

    getAvailableDisbursmentMethods(channelType: string) {
        switch(channelType) {
            case StateService.CHANNEL_TYPES.WALMART:
                return StateService.PAYMENT_METHOD_AVAILABILITY_WALMART;
            case StateService.CHANNEL_TYPES.FORCED_UPGRADE:
                return StateService.PAYMENT_METHOD_AVAILABILITY_GENERAL;
            case StateService.CHANNEL_TYPES.LANDING_PAGE:
                return StateService.PAYMENT_METHOD_AVAILABILITY_GENERAL;
            default:
                return StateService.PAYMENT_METHOD_AVAILABILITY_GENERAL;
        }
    }

    resetDisbursementMethod() {
        this.disbursementMethod = null;
    }

    isDisbursementMethodAvailable(method) {
        return (this.availableDisbursementMethods && (method in this.availableDisbursementMethods)) ?
            this.availableDisbursementMethods[method] : false;

    }

    setAvailableDisbursementMethods(availableMethods) {
        this.availableDisbursementMethods = availableMethods;
    }

    setDisbursementMethod(method) {
        if (Object.values(StateService.DISBURSEMENT_METHODS).includes(method)) {
            this.disbursementMethod = method;
        } else {
            console.log('unknown payment method', method)
        }
    }

    getDisbursementMethod() {
        return this.disbursementMethod;
    }

    cashTransferSelected() {
        return (this.getDisbursementMethod() === StateService.DISBURSEMENT_METHODS.CASH_TRANSFER);
    }

    windowPickupSelected() {
        return (this.getDisbursementMethod() === StateService.DISBURSEMENT_METHODS.WINDOW_PICKUP);
    }

    atmWithdrawalSelected() {
        return (this.getDisbursementMethod() === StateService.DISBURSEMENT_METHODS.ATM_WITHDRAWAL);
    }

    walmartGiftCardSelected() {
        return (this.getDisbursementMethod() === StateService.DISBURSEMENT_METHODS.WALMART_GIFT_CARD);
    }

    translateBoolean(input) {
        if (input == true || input === 'true' || input === '1' || input === 1) {
            return true;
        } else {
            return false;
        }
    }

    setOutsideBusinessHours(isOutside = true) {
        this.outsideBusinessHours = this.translateBoolean(isOutside);
        console.log('setOutsideBusinessHours', this.outsideBusinessHours);
    }

    isOutsideBusinessHours() {
        return this.outsideBusinessHours;
    }

    setOnHolidayOrWeekend(isHoliday = true) {
        this.onHolidayOrWeekend = this.translateBoolean(isHoliday);
    }

    isHolidayOrWeekend() {
        return this.onHolidayOrWeekend;
    }

    apiPost(endpoint, data) {
        return this.http.post(this.apiBaseUrl + endpoint, data);
    }

    apiGet(endpoint, params) {
        return this.http.get(this.apiBaseUrl + endpoint, { params: params });
    }

    getSubmittingSignature() {
        return this.submittingSignature;
    }

    setSubmittingSignatureOff() {
        this.submittingSignature = false;
    }

    setSubmittingSignatureOn() {
        this.submittingSignature = true;
    }

    setSignatureData(data) {
        this.signatureData = data;
    }

    clearSignatureData() {
        this.signatureData = '';
    }

    getSignatureData() {
        return this.signatureData;
    }

    setContractHtml(data) {
        this.contractHtml = data;
    }

    getContractHtml() {
        return this.contractHtml;
    }

    setTermsAccepted() {
        this.termsAccepted = true;
    }

    getTermsAccepted() {
        return this.termsAccepted;
    }

    getClabeNumber() {
        return this.clabeNumber;
    }

    resetClabeNumber() {
        this.clabeNumber = '';
        this.clabeCheck = null;
    }

    resetClabeCheck() {
        this.clabeCheck = null;
    }

    isClabeNumberValid() {
        this.clabeCheck = Clabe.validate(this.clabeNumber);
        return this.clabeCheck.ok;
    }

    isClabeNumberValidated() {
        return (this.clabeCheck && this.clabeCheck.ok);
    }

    isClabeNumberInvalidated() {
        return (this.clabeCheck && !this.clabeCheck.ok);
    }

    isClabeNumberCorrectLength() {
        return (this.clabeNumber.length == 18);
    }

}
