/**
 * Takes a base64 encoded images and performs a num ammount of 90deg rotations
 * @param base64ImageSrc String with encoded image
 * @param num Number of times the image was rotated (0=0deg, 1=90deg, 2=180deg, 3=270deg)
 * @returns base64 string after performing rotations
 */
export const rotateImage = (base64ImageSrc: string, num = 0): string => {
    const canvas = document.createElement('canvas');
    const img = new Image();
    const context = canvas.getContext('2d');
    img.src = 'data:image/jpeg;base64,' + base64ImageSrc;

    switch (num) {
        case 1:
            canvas.width = img.height;
            canvas.height = img.width;
            context.rotate(Math.PI / 2);
            context.drawImage(img, 0, -img.height);
            break;
        case 2:
            canvas.height = img.height;
            canvas.width = img.width;
            context.rotate(Math.PI);
            context.drawImage(img, -img.width, -img.height);
            break;
        case 3:
            canvas.width = img.height;
            canvas.height = img.width;
            context.rotate((3 * Math.PI) / 2);
            context.drawImage(img, -img.width, 0);
            break;
        default:
            canvas.height = img.height;
            canvas.width = img.width;
            context.drawImage(img, 0, 0);
            break;
    }
    const stringToReturn = canvas.toDataURL('image/jpeg').slice(23);
    context.clearRect(0, 0, canvas.width, canvas.height);
    return stringToReturn;
}

/**
 * Creates image objects and adds a source to a download an image
 * with needing to display it.
 * @param src 
 */
export const preLoadImage = (src: string): void => {
    const image = new Image();
    image.src = src;
}

/**
 * Parses a URL to get the string of content after the last "/" of the given URL
 * @param url 
 * @returns string
 */
export const getTrailingUrlString = (url: string): string => {
    const urlSections = url.split('/');
    if (url.charAt(url.length - 1) === '/') {
        return urlSections[urlSections.length - 2];
    } else {
        return urlSections[urlSections.length - 1];
    }
}
