import { Component, ElementRef, OnInit } from '@angular/core';
import { LockStateMachineService } from '../lock-state-machine.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { TranslateService } from '@ngx-translate/core';
import { LanguageService } from '../../services/language.service'

@Component({
    selector: 'app-loan',
    templateUrl: './loan.component.html',
    styleUrls: ['./loan.component.scss', '../lock.scss'],
})
export class LoanComponent extends PageComponent implements OnInit {

    constructor(
        public language: LanguageService,
        public stateMachine: LockStateMachineService,
        public translate: TranslateService,
        protected elementRef: ElementRef,
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'LoanComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
    }

    public getPaymentHeader() {
        if (this.stateMachine.loanInfo.paymentFrequency == 'weekly') {
            return this.translate.instant('LOCK.DETAILS.HEADER_WEEKLY_PAYMENTS');
        } else if (this.stateMachine.loanInfo.paymentFrequency == 'monthly') {
            return this.translate.instant('LOCK.DETAILS.HEADER_MONTHLY_PAYMENTS');
        } else if (this.stateMachine.loanInfo.paymentFrequency == 'daily') {
            return this.translate.instant('LOCK.DETAILS.HEADER_DAILY_PAYMENTS');
        }
        return this.translate.instant('LOCK.DETAILS.HEADER_PAYMENTS');
    }

    public async goToPage(newState) {
        this.stateMachine.updateCurrentPage(newState);
    }

    public formatPaymentDate() {
        if (this.stateMachine.loanInfo.nextPaymentDatetime) {
            return this.stateMachine.loanInfo.nextPaymentDatetime.toLocaleDateString(
                this.language.languageCode, LanguageService.LONG_DATE_OPTIONS);
        } else {
            return '';
        }
    }

}