import { Component, OnInit, ViewChild, Output, EventEmitter, Input } from '@angular/core';
import { IonSlides } from '@ionic/angular';
import { CashStateMachineService } from '../../../../cash/cash-state-machine.service';

@Component({
    selector: 'app-home-carousel',
    templateUrl: './home-carousel.component.html',
    styleUrls: ['./home-carousel.component.scss'],
})
export class HomeCarouselComponent implements OnInit {
    slideOpts = {
        initialSlide: 0,
        speed: 400,
        autoplay: false
    };
    @ViewChild(IonSlides, { static: true }) carousel: IonSlides;
    @Input() slidesData: Array<any>;
    @Input() customHeight: string | number = 'auto';
    @Output() onSlideChange = new EventEmitter<number>();
    @Output() onLearnMoreClick = new EventEmitter();

    constructor(
        public stateMachine: CashStateMachineService
    ) { }

    ngOnInit() { }

    async handleSlideChange() {
        const currentIndex = await this.carousel.getActiveIndex();
        this.onSlideChange.emit(currentIndex);
    }

    public handleLearnMoreClick() {
        this.onLearnMoreClick.emit();
    }

}
