import { Component, ElementRef, OnInit } from '@angular/core';
import { LockStateMachineService } from '../lock-state-machine.service';
import { MenuController } from '@ionic/angular';
import { PageComponent } from '@app/pages/_page/page.component';
import { TranslateService } from '@ngx-translate/core';
import { LanguageService } from '../../services/language.service'
import { StateDataService } from '../../api/state-data.service';
import { PluginService } from '../../api/plugin.service';
import { HelpService } from '../../services/help.service';

@Component({
    selector: 'app-lock-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.scss', '../lock.scss'],
})
export class HomeComponent extends PageComponent implements OnInit {

    public updateButtonEnabled = true;
    public infoBoxVisible = true;

    constructor(
        public language: LanguageService,
        public stateMachine: LockStateMachineService,
        public stateData: StateDataService,
        public translate: TranslateService,
        protected elementRef: ElementRef,
        private help: HelpService,
        private menuControl: MenuController,
        private plugin: PluginService,
        ) {
        super(elementRef);
    }

    public buildPaymentDueMessage() {
        // switching to the i18n package would make the handling of singular and plurals cleaner
        const daysLeft = this.stateMachine.loanInfo.daysLeft();
        let buffer = this.translate.instant('LOCK.NEXT_PAYMENT_DUE_PRE');
        if (this.stateMachine.loanInfo.isTomorrow()) {
            buffer += this.translate.instant('LOCK.NEXT_PAYMENT_TIME_LEFT_SINGLE');
        } else if (this.stateMachine.loanInfo.isToday()) {
            buffer += this.translate.instant('LOCK.NEXT_PAYMENT_TIME_LEFT_TODAY');
        } else if (daysLeft > 0) {
            buffer += this.translate.instant('LOCK.NEXT_PAYMENT_TIME_LEFT_PLURAL', { days: daysLeft, date: this.formatPaymentDate() });
        } else {
            return '';
        }
        buffer += ' ' + this.translate.instant('LOCK.NEXT_PAYMENT_TIME_PHRASE', { time: this.formatPaymentTime() });
        return buffer;
    }

    public getDisbursementMessage(textType = 'MESSAGE') {
        let text = 'DISBURSEMENT.' + this.stateMachine.loanInfo.disbursementInfo['paymentType'] +
        '.' + this.stateMachine.loanInfo.disbursementInfo['status'].toUpperCase() +
        '.' + textType;
        if (this.stateMachine.loanInfo.disbursementInfo['paymentType'] === 'AtmWithdrawal' && textType === 'MESSAGE') {
            return this.stateMachine.processEcString(text);
        } else {
            return text;
        }
    }

    public getGreeting() {
        if (this.stateMachine.userProfile.userDisplayName) {
            return this.translate.instant('LOCK.HELLO') + ' ' + this.stateMachine.userProfile.userDisplayName + '!';
        } else {
            return this.translate.instant('LOCK.HELLO') + '!';
        }

    }

    public getName(): string {
        return 'HomeComponent';
    }

    public async goToOnboarding() {
        this.stateMachine.setupForOnboardingFlow();
    }

    public async goToOffers() {
        this.stateMachine.setupForOffersReapplyFlow();
    }

    public async goToPage(newState) {
        this.stateMachine.updateCurrentPage(newState);
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.help.setHelpType('general');
        this.api.logEvent('LockHomeLoad', {
            currentPage: this.stateMachine.getCurrentPage(),
        });
    }

    public openHelp() {
        this.help.setHelpBackButton(this.stateMachine.states.MAIN_HOME);
        this.stateMachine.updateCurrentPage(this.stateMachine.states.MAIN_HELP);
    }

    public openMenu() {
        this.stateMachine.openMenu();
        this.menuControl.open();
    }

    public openReferralLink() {
        let cashUrl;
        if (this.stateData.getDeviceTag()) {
            cashUrl = 'https://payjoy.com/es/cash/?deviceTag=' + this.stateData.getDeviceTag();
        } else {
            cashUrl = 'https://payjoy.com/es/cash/';
        }

        this.api.logEvent('LockReferralLinkAction', {
            currentPage: this.stateMachine.getCurrentPage(),
            action: 'openReferralLink'
        });
        window.open(
            'https://api.whatsapp.com/send?text=%20' +
            this.translate.instant('LOCK.REFER_FRIEND_FILLER_TEXT') +
            cashUrl,
            '_system');
    }

    public async openWithdrawalLink() {
        if (this.stateMachine.loanInfo.withdrawalLink) {
            try {
                this.api.logEvent('LockOpenWithdrawalLink', {
                    currentPage: this.stateMachine.getCurrentPage(),
                    action: 'openWithdrawalLink'
                });
                await this.plugin.browse(this.stateMachine.loanInfo.withdrawalLink);
                console.log('closing AppBrowser');
            } catch (error) {
                // this error essentially will be ignored and means the link wont work
                this.api.logError(StateDataService.ERROR_CODES.LOCK_SCREEN_WITHDRAWAL_LINK, error);
            }
        }
    }

    public showProgressBar() {
        if ((this.stateMachine.loanInfo.paymentsSoFar == null) || !this.stateMachine.loanInfo.totalNumOfPayments) {
            return false;
        }
        return true;
    }

    public topBubbleLinkAction() {
        if (this.stateMachine.loanInfo.isWalmartGiftCardDisbursement()) {
            this.openHelp();
            return;
        }
        this.goToPage(this.stateMachine.states.MAIN_DISBURSEMENT)
    }


    private formatPaymentDate() {
        if (this.stateMachine.loanInfo.nextPaymentDatetime) {
            return this.stateMachine.loanInfo.nextPaymentDatetime.toLocaleDateString(
                this.language.languageCode, LanguageService.NUMERIC_DATE_OPTIONS);
        } else {
            return '';
        }
    }

    private formatPaymentTime() {
        if (this.stateMachine.loanInfo.nextPaymentDatetime) {
            return this.stateMachine.loanInfo.nextPaymentDatetime.toLocaleTimeString(
                this.language.languageCode, { hour: '2-digit', minute: '2-digit', hour12: true });
        } else {
            return '';
        }
    }

    public remindRemovable() {
        return !this.stateMachine.loanInfo.loanCompleted && (this.stateMachine.loanInfo.paymentsRemaining() <= 2);
    }

    public hideInfoBox() {
        this.infoBoxVisible = false;
    }
}