import { Component, ElementRef, OnInit } from '@angular/core';
import { PageComponent } from '@app/pages/_page/page.component';
import { StateService } from '../../loan-application/state.service';
import { HttpErrorResponse } from '@angular/common/http';
import { CashStateMachineService } from '../../cash/cash-state-machine.service';
import { StateDataService } from '../../api/state-data.service';

@Component({
    selector: 'app-questionnaire',
    templateUrl: './questionnaire.component.html',
    styleUrls: ['./questionnaire.component.scss']
})
export class QuestionnaireComponent extends PageComponent implements OnInit {
    static readonly OTHER_TEXT = 'Otro';
    private userId: string;

    questionContinuePressed = false;
    submittingAnswers = false;

    question3 = 'Cmo te enteraste de PayJoy?';
    answer3 = '';
    other3 = '';

    options3 = [
        'Facebook',
        'Instagram',
        'Vi publicidad en un autobs o poster en la calle',
        'Lo busqu en Google',
        'Me lo recomend un amigo o familiar',
        'Obtuve un financiamiento de celular previamente con PayJoy',
        'Me enter en una tienda Bodega Aurrer',
        QuestionnaireComponent.OTHER_TEXT
    ];

    question3_ec = 'Cmo te enteraste de PayJoy?';

    options3_ec = [
        'Facebook',
        'Instagram',
        'Lo busqu en Google',
        'Me lo recomend un amigo o familiar',
        'Obtuve un financiamiento de celular previamente con PayJoy',
        QuestionnaireComponent.OTHER_TEXT
    ];

    questionWalmart1 = 'En qu vas a usar tu prstamo?';
    answerWalmart1 = [];
    otherWalmart1 = '';

    optionsWalmart1 = [
        'Alimentos',
        'Electrodomsticos y artculos para el hogar',
        'Celulares, computadoras y otros electrnicos',
        'Productos de beb',
        'Moda,belleza y salud',
        'Vinos y licores',
        QuestionnaireComponent.OTHER_TEXT
    ];


    constructor(
        protected elementRef: ElementRef,
        public state: StateService,
        public stateMachine: CashStateMachineService,
    ) {
        super(elementRef);
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.userId = await this.stateData.get(StateDataService.KEYS.USER_ID);
        this.navFooter.setNextStyle();
        this.stateMachine.disableNext();
        // ToDo: Add translate JSON string
        this.navFooter.setNextText('Continuar');
        this.subscription.add(
            this.stateMachine.$nextButton.subscribe(val => {
                this.submitAnswers();
            })
        );
    }

    public getName(): string {
        return 'QuestionaireComponent';
    }

    handleQuestion3Change(value: string) {
        this.answer3 = value;
        if (this.answer3 !== QuestionnaireComponent.OTHER_TEXT) {
            this.stateMachine.enableNext();
        } else {
            this.stateMachine.disableNext();
        }
    }

    handleQuestion3OtherInputChange(value: string) {
        this.other3 = value;
        if (this.other3) {
            this.stateMachine.enableNext();
        } else {
            this.stateMachine.disableNext()
        }
    }

    handleWalmart1Change(value: string[]) {
        this.answerWalmart1 = value;
        if (this.answer3 !== QuestionnaireComponent.OTHER_TEXT) {
            this.stateMachine.enableNext();
        } else {
            this.stateMachine.disableNext();
        }
    }

    handleWalmart1OtherInputChange(value: string) {
        this.otherWalmart1 = value;
        if (this.other3) {
            this.stateMachine.enableNext();
        } else {
            this.stateMachine.disableNext()
        }
    }

    answerButtonClass() {
        if (!this.answerButtonEnabled()) {
            return 'fullButtonDisabled';
        }
        return 'fullButtonEnabled';
    }

    answerButtonEnabled() {
        if (this.state.walmartGiftCardSelected()) {
            return (this.buildWalmartAnswer1().length);
        } else {
            return (!this.submittingAnswers &&
                (this.buildAnswer3() && this.buildAnswer3() !== QuestionnaireComponent.OTHER_TEXT));
        }
    }

    buildAnswer3() {
        if (this.answer3 === QuestionnaireComponent.OTHER_TEXT && this.other3) {
            return this.answer3 + ': ' + this.other3;
        }
        return this.answer3;
    }


    buildWalmartAnswer1() {

        let answers = [];
        const otherText = this.otherWalmart1;

        this.answerWalmart1.forEach(
            function(answer) {
                // if the user chooses only other and doesn't fill in the text then we want
                // to return an empty list
                if (answer === QuestionnaireComponent.OTHER_TEXT) {
                    if (otherText.length > 0) {
                        answers.push(answer + ': ' + otherText);
                    }
                } else {
                    answers.push(answer);
                }
            }
        );

        return answers;
    }

    submitAnswers() {
        this.navFooter.setOnboardingLoadingStyle();
        this.submittingAnswers = true;

        let fullResponse = {};

        if (this.state.walmartGiftCardSelected()) {

            let responseList = [];
            const currentQuestion = this.questionWalmart1;
            this.buildWalmartAnswer1().forEach(
                function(answer) {
                    responseList.push({
                        name: "question_walmart_1",
                        question: currentQuestion,
                        answer: answer,
                        share: 0
                    });
                });

            fullResponse = {
                userId: this.userId,
                surveyVersion: '1',
                responses: responseList
            }
        } else if (this.stateMachine.isEcuador()) {
            fullResponse = {
                userId: this.userId,
                surveyVersion: '1',
                responses: [{
                    name: "question_3_ec",
                    question: this.question3_ec,
                    answer: this.buildAnswer3(),
                    share: 0
                }]
            }
        } else {
            fullResponse = {
                userId: this.userId,
                surveyVersion: '1',
                responses: [{
                    name: "question_3",
                    question: this.question3,
                    answer: this.buildAnswer3(),
                    share: 0
                }]
            }
        }

        this.state.apiPost('/answer-risk-questions', fullResponse).subscribe(
            res => {
                const success = res['valid'];
                if (success) {
                    this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_OFFER);
                    this.submittingAnswers = false;
                }
            },
            (err: HttpErrorResponse) => {
                this.submittingAnswers = false;
                this.navFooter.setNextStyle();
                console.log(err.error);
                console.log(err.name);
                console.log(err.message);
                console.log(err.status);
            }
        );

    }

    otherChosenWalmart1() {
        return this.answerWalmart1.includes(QuestionnaireComponent.OTHER_TEXT);
    }

    otherChosen() {
        return this.answer3 === QuestionnaireComponent.OTHER_TEXT;
    }

    clearQuestion3() {
        this.answer3 = '';
        this.other3 = '';
    }

    continueQuestions() {
        this.submitAnswers();
    }

}
