import { AfterViewInit, Component, ElementRef, OnDestroy, OnInit } from '@angular/core';
import { ApiService } from '../../api/api.service';
import { AppInjector } from '../../services/app-injector.service';
import { BackButtonService } from '../../services/back-button.service';
import { NativeStorage } from '@ionic-native/native-storage/ngx';
import { Observable, Subscription, timer } from 'rxjs';
import { Platform } from '@ionic/angular';
import { StateDataService } from '../../api/state-data.service';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { NavFooterService } from '@app/components/nav-footer/nav-footer.service';

// Baseclass for any component which represents a full page. Components
// which represent a smaller part of page should not extend this baseclass
@Component({
    selector: 'app-payjoy',
    template: ''
})
export class PageComponent implements OnInit, OnDestroy, AfterViewInit {
    private static readonly SCREEN_SIZE_ADJUST_CLASSES = [
        'bodyTitle',
        'svgIllustration',
        'photoContainer'
    ];

    private static readonly SCREEN_TYPES = {
        LARGE: 'large',
        MEDIUM: 'medium',
        SMALL: 'small'
    };

    protected subscription: Subscription = new Subscription();
    protected timer$: Observable<number> = null;
    protected backButton: BackButtonService;
    protected nativeStorage: NativeStorage;
    protected platform: Platform;
    protected splashscreen: SplashScreen;
    protected stateData: StateDataService;
    protected api: ApiService;
    protected navFooter: NavFooterService;
    private screenType = PageComponent.SCREEN_TYPES.MEDIUM;

    constructor(
        protected elementRef: ElementRef
    ) {
        // *IMPORTANT* Using the static AppInjector is an anti-pattern and should
        // be limited to baseclasses like PageComponent to avoid *all* the child
        // components having to list all of the base component's injections
        this.backButton = AppInjector.injector.get(BackButtonService);
        this.nativeStorage = AppInjector.injector.get(NativeStorage);
        this.platform = AppInjector.injector.get(Platform);
        this.splashscreen = AppInjector.injector.get(SplashScreen);
        this.stateData = AppInjector.injector.get(StateDataService);
        this.api = AppInjector.injector.get(ApiService);
        this.navFooter = AppInjector.injector.get(NavFooterService);
    }

    public getName(): string {
        throw new Error('Not implemented');
    }

    public async ngOnInit() {
        console.log('ngOnInit for ' + this.getName());
        this.backButton.setActivePage(this);
        this.platform.ready().then(() => {
            console.log('height', this.platform.height());
            console.log('width', this.platform.width());
            this.setScreenType(this.platform.height());
            setTimeout(function () {
                this.splashscreen.hide();
            }.bind(this), 500);
        });
        await this.stateData.refreshDeviceTag();
    }

    public ngAfterViewInit(): void {
        this.api.logEvent(this.buildScreenLabel(), {
            componentName: this.getName()
        });  
    }

    public ngOnDestroy() {
        console.log('ngOnDestroy for ' + this.getName());
        this.api.logEvent(this.buildScreenLabel('ScreenClose'), {
            componentName: this.getName()
        }); 
        this.backButton.removeActivePage();
        this.subscription.unsubscribe();
    }

    public handleBackButton() {
        return false;
    }

    protected adjustClassByScreenType(className) {
        if (this.screenType === PageComponent.SCREEN_TYPES.SMALL) {
            if (PageComponent.SCREEN_SIZE_ADJUST_CLASSES.includes(className)) {
                return className + 'Small';
            }
        }
        return className;
    }

    protected async cacheState(key, value) {
        console.log('Caching component state: ' + key + ' = ' + value);
        return this.nativeStorage.setItem(this.cacheKey(key), value);
    }

    protected async loadState(key) {
        console.log('Loading component state: ' + key);
        return this.nativeStorage.getItem(this.cacheKey(key));
    }

    protected timer() {
        if (!this.timer$) {
            this.timer$ = timer(0, 1000);
        }
        return this.timer$;
    }

    private buildScreenLabel(prefix = 'ScreenLoad') {
       return prefix+this.getName().replace(/Component$/,'');
    }

    private cacheKey(key) {
        return this.elementRef.nativeElement.tagName.toLowerCase() + '.' + key;
    }

    private setScreenType(height) {
        if (height <= 600) {
            this.screenType = PageComponent.SCREEN_TYPES.SMALL;
        } else if (height <= 1080) {
            this.screenType = PageComponent.SCREEN_TYPES.MEDIUM;
        } else {
            this.screenType = PageComponent.SCREEN_TYPES.LARGE;
        }
    }

}
