import { Component, ElementRef, OnInit } from '@angular/core';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { HelpService } from '../../services/help.service';

@Component({
    selector: 'app-decline',
    templateUrl: './decline.component.html',
    styleUrls: ['./decline.component.scss'],
})
export class DeclineComponent extends PageComponent implements OnInit {
    public displayManufacturers: any = false;
    public displayDevices: any = false;
    public devices: any = false;
    public manufacturerDevices: any = false;
    public displayDevicesUnavailable: any = false;

    private devicesUnavailable: any = false;

    constructor(
        public stateMachine: OnboardingStateMachineService,
        protected elementRef: ElementRef,
        private help: HelpService,
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'DeclineComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.stateMachine.endTransition();
        this.navFooter.setHiddenStyle();
        this.getSupportedDevices();
    }

    public displayDeviceManufacturers() {
        if (this.devices) {
            this.displayDevices = false;
            this.displayManufacturers = true;
        } else {
            this.displayDevicesUnavailable = true;
        }
    }

    public back(screen) {
        if (screen === 'unsupportedDeviceScreen') {
            this.displayDevices = false;
            this.displayManufacturers = false;
            this.displayDevicesUnavailable = false;
        }
        if (screen === 'manufacturerScreen') {
            this.displayDevices = false;
            this.displayDevicesUnavailable = false;
            this.displayManufacturers = true;
        }
    }

    public selectManufacturer(manufacturer) {
        this.displayManufacturers = false;
        this.displayDevices = true;

        let i;
        for (i = 0; i < this.devices.length; i++) {
            if (this.devices[i]['manufacturer'] === manufacturer) {
                this.manufacturerDevices = this.devices[i]['deviceFamilies'];
            }
        }
    }

    private async getSupportedDevices() {
        try {
            const parameters = {
                country: 'MX' // #todo is this necessary
            };
            const res = await this.api.get('/loans/v2/api/supported-devices.php', parameters);
            if (res['valid']) {
                this.devices = res['data'];
                if (this.devices) {
                    this.devicesUnavailable = false;
                }
            } else {
                console.log('Error returned from supported-devices: ' + res['message']);
                if (!this.devices) {
                    this.devicesUnavailable = true;
                }
            }
        } catch (err) {
            console.log('Error on post to supported-devices: ' + err);
            if (!this.devices) {
                this.devicesUnavailable = true;
            }
        }
    }
}