import { Component, ElementRef } from '@angular/core';
import { PageComponent } from '@app/pages/_page/page.component';
import { CashStateMachineService } from '../../cash/cash-state-machine.service';
import { StateService } from '../../loan-application/state.service';

@Component({
    selector: 'app-select-disbursement-method',
    templateUrl: './select-disbursement-method.component.html',
    styleUrls: ['./select-disbursement-method.component.scss']
})
export class SelectDisbursementMethodComponent extends PageComponent {
    readonly disbursementMethods = StateService.DISBURSEMENT_METHODS;

    constructor(
        protected elementRef: ElementRef,
        public state: StateService,
        public stateMachine: CashStateMachineService,
    ) {
        super(elementRef)
    }

    public async ngOnInit() {
        super.ngOnInit();
        if (this.state.availableDisbursementMethods[this.disbursementMethods.WALMART_GIFT_CARD]) {
            this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_QUESTIONS);
            this.state.setDisbursementMethod(this.disbursementMethods.WALMART_GIFT_CARD);
        }
        this.navFooter.setCancelStyle()
        this.subscription.add(
            this.navFooter.$button.subscribe((val) => {
                if (val === 'cancel') {
                    this.stateMachine.updateCurrentPage(this.stateMachine.states.OFFERS);
                }
            })
        );
    }

    public getName(): string {
        return 'SelectDisbursmentMethodComponent';
    }

    chooseAtmWithdrawal() {
        this.state.setDisbursementMethod(this.disbursementMethods.ATM_WITHDRAWAL);
        this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_QUESTIONS);
    }

    chooseTransfer() {
        this.state.setDisbursementMethod(this.disbursementMethods.CASH_TRANSFER);
        this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_TRANSFER);
    }

    chooseWindowPickup() {
        this.state.setDisbursementMethod(this.disbursementMethods.WINDOW_PICKUP);
        this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_WINDOW_PICKUP);
    }

    chooseWalmartGiftCard() {
        this.state.setDisbursementMethod(this.disbursementMethods.WALMART_GIFT_CARD);
        this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_OFFER);
    }

    ngOnDestroy() {
        this.navFooter.setHiddenStyle();
    }

}
