import { Component, OnDestroy, OnInit } from '@angular/core';
import { MenuController } from '@ionic/angular';

import { LockStateMachineService } from '../lock-state-machine.service';
import { TranslateService } from '@ngx-translate/core';

import { Profile } from '../../models/profile.model';
import { HelpService } from '../../services/help.service';
import { ApiService } from '../../api/api.service';

@Component({
    selector: 'app-lock-menu',
    templateUrl: './menu.component.html',
    styleUrls: ['./menu.component.scss', '../lock.scss'],
})
export class MenuComponent implements OnInit, OnDestroy {

    contactMessage = 'LockHelpAction';

    pages = [{
            title: this.translate.instant('LOCK.MENU.ACTIVE_CREDIT_TITLE'),
            state: this.stateMachine.states.MAIN_HOME,
        },
        {
            title: this.translate.instant('LOCK.MENU.MAKE_PAYMENT_TITLE'),
            state: this.stateMachine.states.MAIN_PAY,
            disabled: !this.stateMachine.loanInfo.isDisbursed()
        },
        {
            title: this.translate.instant('LOCK.MENU.PERSONAL_LOAN_TITLE'),
            subtitle: this.translate.instant('LOCK.MENU.PERSONAL_LOAN_SUBTITLE'),
            state: this.stateMachine.states.CASH_ONBOARDING,
            disabled: this.isOffersDisabled()
        },
        {
            title: this.translate.instant('LOCK.MENU.PREVIOUS_LOANS_TITLE'),
            state: this.stateMachine.states.MAIN_PAY,
            hidden: true
        },
        {
            title: this.translate.instant('LOCK.MENU.HELP_TITLE'),
            subtitle: this.translate.instant('LOCK.MENU.HELP_SUBTITLE'),
            state: this.stateMachine.states.MAIN_HELP,
            help: 'general',
        },
        /* {
             title: this.translate.instant('LOCK.MENU.PRIVACY_TITLE'),
             state: this.stateMachine.states.MAIN_HELP,
             help: 'privacy',
         },
         {
             title: this.translate.instant('LOCK.MENU.HOW_WORKS'),
             state: this.stateMachine.states.MAIN_HELP,
             help: 'function',
         }  */
    ];

    constructor(
        public help: HelpService,
        public stateMachine: LockStateMachineService,
        public translate: TranslateService,
        private api: ApiService,
        private menuControl: MenuController,
        private userProfile: Profile
    ) {}

    public ngOnInit() {
        this.openMenu();
    }

    public ngOnDestroy() {
        this.closeMenu();
        this.stateMachine.closeMenu();
    }

    public async goToPage(newState, help = 'general') {

        this.api.logEvent('LockMenuLink', {
            currentPage: this.stateMachine.getCurrentPage(),
            gotoPage: newState,
            help
        });

        this.closeMenu();
        await this.delay(500);
        this.help.setHelpType(help);

        if (newState === this.stateMachine.states.CASH_ONBOARDING) {
            this.stateMachine.setupForOnboardingFlow();
        } else {
            this.stateMachine.updateCurrentPage(newState);
        }
        this.stateMachine.closeMenu();
    }

    public isOffersDisabled() {
        return (!this.stateMachine.FEATURES_CONTROL.OFFERS_ON ||
            !this.stateMachine.loanInfo.loanCompleted ||
            this.stateMachine.loanInfo.inCooldown);
    }

    public userName() {
        if (this.userProfile.userDisplayName) {
            return this.userProfile.userDisplayName;
        } else {
            return this.translate.instant('NAVIGATION.MENU')
        }
    }

    private async delay(ms: number) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    private openMenu() {
        this.menuControl.open();
        this.stateMachine.openMenu();
    }

    private closeMenu() {
        this.menuControl.close();
    }

}