import { Component, ElementRef, OnInit } from '@angular/core';
import { LockStateMachineService } from '../lock-state-machine.service';
import { MenuController } from '@ionic/angular';
import { environment } from '@app/env';
import { PageComponent } from '@app/pages/_page/page.component';
import { PluginService } from '../../api/plugin.service';
import { StateDataService } from '../../api/state-data.service';

@Component({
    selector: 'app-pay',
    templateUrl: './pay.component.html',
    styleUrls: ['./pay.component.scss', '../lock.scss'],
})
export class PayComponent extends PageComponent implements OnInit {

    constructor(
        protected elementRef: ElementRef,
        private menuControl: MenuController,
        private plugin: PluginService,
        private stateMachine: LockStateMachineService,
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'PayComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();

        this.api.logEvent('LockPaymentScreenLoad', {
            currentPage: this.stateMachine.getCurrentPage(),
        });
        this.openPaymentPage();
    }

    private async openPaymentPage() {
        const paymentLink = environment.API_BASE_URL + '/pay-options.php?deviceTag=' + this.stateData.getDeviceTag();

        try {
            await this.plugin.browse(paymentLink);
            console.log('closing AppBrowser');
            this.stateMachine.updateCurrentPage(this.stateMachine.states.MAIN_HOME);
        } catch (error) {
            this.stateMachine.setError('openPaymentPage error: ' + error, StateDataService.ERROR_CODES.LOCK_SCREEN_PAYMENT_SCREEN);

        }
    }
}