import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { CameraService } from '../../services/camera.service';

@Component({
  selector: 'app-image-input',
  templateUrl: './image-input.component.html',
  styleUrls: ['./image-input.component.scss'],
})
export class ImageInputComponent implements OnInit {
  @Input() pictureId: string;
  @Input() camButtonTagId: string;
  @Input() imageMaxHeight: string = '22vh';
  @Input() placeholderSubtitle: string;
  @Input() placeholderImgURL: string;
  @Input() placeholderImgWidth: string = '75%';
  @Input() controlsPosition: 'right' | 'bottom' | 'left' | 'top' = 'bottom';
  @Input() capturedImage: string;
  @Input() hideRotateButton: boolean;
  @Input() customCaptureButtonLabel: string;

  @Output() onCamOpenSuccess = new EventEmitter<string>();
  @Output() onCamOpenError = new EventEmitter<string>();
  @Output() onRotateClick = new EventEmitter<number>();

  public rotationDeg: number = 0;
  // public capturedImage: string;

  constructor(
    private camera: CameraService,
  ) { }

  ngOnInit() { }
  
  public getWrapperFlexDirection(): string {
    switch(this.controlsPosition) {
      case 'bottom': {
        return 'column';
      }
      case 'left': {
        return 'row-reverse';
      }
      case 'right': {
        return 'row';
      }
      case 'top': {
        return 'column-reverse';
      }
    }
  }

  public getControlsFlexDirection(): string {
    if (this.controlsPosition === 'bottom' || this.controlsPosition === 'top') {
      return 'row'
    }
    if (this.controlsPosition === 'left' || this.controlsPosition === 'right') {
      return 'column'
    }
  }

  public handleTakePictureClick() {
    this.camera.takePicture(this.pictureId).then((imageDataUrl) => {
      // imageData is a base64 encoded string
      this.capturedImage = imageDataUrl;
      this.onCamOpenSuccess.emit(imageDataUrl);
    }, (err) => {
      if (err === 'No Image Selected') {
        return;
      }
      this.onCamOpenError.emit(err);
    });

  }

  public handlerRotateClick(): void {
    this.rotationDeg = (this.rotationDeg + 90) % 360;
    this.onRotateClick.emit(this.rotationDeg);
  }

}
