import { Component, ElementRef, OnInit } from '@angular/core';
import { CashStateMachineService } from '../cash/cash-state-machine.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { TranslateService } from '@ngx-translate/core';
import { HelpService } from '../services/help.service';
import { PluginService } from '../api/plugin.service';
import { StateDataService } from '../api/state-data.service';

@Component({
    selector: 'app-cash-help',
    templateUrl: './help.component.html',
    styleUrls: ['./help.component.scss', '../cash/cash.component.scss'],
})
export class HelpComponent extends PageComponent implements OnInit {

    public backToHelp: any = false;
    public helpType;
    public terms;
    public helpBlocks = [];
    public contactMessage = 'CashHelpAction';
    public appVersion: string;
    public appEnvironment: string;
    public appBuildType: string;
    public appGitHash: string;
    public appTimestamp: string;
    public bundleVersion: string;
    public country: string;

    constructor(
        public help: HelpService,
        public stateMachine: CashStateMachineService,
        public stateData: StateDataService,
        public translate: TranslateService,
        private plugin: PluginService,
        protected elementRef: ElementRef
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'HelpComponent';
    }

    public async getTerms(type) {
        this.terms = await this.api.get('assets/docs/tos_es.html', {}, false);
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.setHelpBlocks();

        const versionName = await this.stateData.get(StateDataService.KEYS.APP_VERSION_NAME);
        this.appTimestamp = versionName.split('-')[0];
        this.appGitHash = versionName.split('-')[1];
        this.appVersion = await this.stateData.get(StateDataService.KEYS.APP_VERSION_CODE);
        this.appEnvironment = await this.stateData.get(StateDataService.KEYS.APP_ENVIRONMENT);
        this.appBuildType = await this.stateData.get(StateDataService.KEYS.APP_BUILD_TYPE);
        this.bundleVersion = await this.stateData.get(StateDataService.KEYS.APP_BUNDLE_VERSION);
    }

    public backToHelpPage() {
        console.log('returning to main help page');
        this.backToHelp = false;
        this.help.setHelpType('general');
    }

    public getHelp(type) {
        this.backToHelp = true;
        this.help.setHelpType(type);
    }

    public openMenu() {}

    public showMenu() {
        return false;
    }

    public toggleHelp(help = '') {
        const active = document.querySelector('.rotate');
        if (active) {
            active.classList.remove('rotate');
        }
        const arrow = document.querySelector('#' + help);
        if (this.helpType === help) {
            this.helpType = '';
        } else {
            this.helpType = help;
            arrow.classList.add('rotate');
        }
    }

    public showFeedbackForm() {
        return false;
    }

    private async openCustomerCharter() {
        const customerCharter = this.translate.instant('HELP.CUSTOMER_CHARTER_LINK');

        try {
            await this.plugin.browse(customerCharter);
        } catch (error) {
            this.stateMachine.setError('Error occurred on openCustomerCharter: ' + error, StateDataService.ERROR_CODES.OPEN_CUSTOMER_CHARTER);
        }
    }

    private setCountry() {
        if (this.stateMachine.isEcuador()) {
            this.country = 'ec';
        } else {
            this.country = 'mx';
        }
    }

    private setHelpBlocks() {
        this.setCountry();

        this.helpBlocks = [];
        if (this.stateMachine.isCashLoan()) {
            this.helpBlocks.push({
                type: 'general',
                title: this.translate.instant('HELP.LINKTEXT'),
                subtitle: this.translate.instant('HELP.FREQUENTLY_ASKED'),
                items: [{
                        toggle: 'receive-money',
                        question: this.translate.instant('HELP.RECEIVING_MONEY_TITLE'),
                        answer: this.translate.instant(this.stateMachine.processEcString('HELP.RECEIVING_MONEY_CONTENT')),
                        country: 'all',
                    },
                    {
                        toggle: 'atm-withdrawal',
                        question: this.translate.instant('HELP.ATM_WITHDRAWAL_TITLE'),
                        answer: this.translate.instant('HELP.ATM_WITHDRAWAL_CONTENT'),
                        country: 'mx',
                    },
                    {
                        toggle: 'bank-withdrawal',
                        question: this.translate.instant('HELP.BANK_TRANSFER_TITLE'),
                        answer: this.translate.instant('HELP.BANK_TRANSFER_CONTENT'),
                        country: 'mx',
                    },
                    {
                        toggle: 'make-payments',
                        question: this.translate.instant('HELP.MAKING_PAYMENTS_TITLE'),
                        answer: this.translate.instant(this.stateMachine.processEcString('HELP.MAKING_PAYMENTS_CONTENT')),
                        country: 'all',
                    },
                    {
                        toggle: 'missed-payment',
                        question: this.translate.instant('HELP.MISSED_PAYMENT_TITLE'),
                        answer: this.translate.instant('HELP.MISSED_PAYMENT_CONTENT'),
                        country: 'all',
                    },
                ],
                links: [{
                    question: this.translate.instant('HELP.PRIVACY.WHY_WE_NEED_SECTION_TITLE'),
                    type: 'PRIVACY_DETAIL',
                    class: 'b3 semibold',
                    country: 'all',
                }],
            });
        } else {
            this.helpBlocks.push({
                type: 'general',
                title: this.translate.instant('HELP.LINKTEXT'),
                subtitle: this.translate.instant('HELP.FREQUENTLY_ASKED'),
                items: [{
                        toggle: 'make-payments',
                        question: this.translate.instant('HELP.MAKING_PAYMENTS_TITLE'),
                        answer: this.translate.instant('HELP.MAKING_PAYMENTS_CONTENT'),
                        country: 'all',
                    },
                    {
                        toggle: 'missed-payment',
                        question: this.translate.instant('HELP.MISSED_PAYMENT_TITLE'),
                        answer: this.translate.instant('HELP.MISSED_PAYMENT_CONTENT'),
                        country: 'all',
                    },
                    {
                        toggle: 'build-credit',
                        question: this.translate.instant('HELP.BUILD_CREDIT_TITLE'),
                        answer: this.translate.instant('HELP.BUILD_CREDIT_CONTENT'),
                        country: 'all',
                    }
                ],
            });
        }

        this.helpBlocks.push({
            type: 'general',
            subtitle: this.translate.instant('HELP.PRIVACY_TITLE'),
            items: [{
                    toggle: 'why-data',
                    question: this.translate.instant('HELP.WHY_DATA_TITLE'),
                    answer: this.translate.instant('HELP.WHY_DATA_CONTENT'),
                    country: 'all',
                },
                {
                    toggle: 'how-protect',
                    question: this.translate.instant('HELP.HOW_PROTECT_TITLE'),
                    answer: this.translate.instant('HELP.HOW_PROTECT_CONTENT'),
                    country: 'all',
                }
            ],
        });
        this.helpBlocks.push({
            type: 'general',
            subtitle: this.translate.instant('HELP.HOW_WORKS_TITLE'),
            items: [{
                toggle: 'our-mission',
                question: this.translate.instant('HELP.OUR_MISSION_TITLE'),
                answer: this.translate.instant('HELP.OUR_MISSION_CONTENT'),
                country: 'all',
            }],
            links: [{
                question: this.translate.instant('HELP.CREDIT_TERMS_TITLE'),
                type: 'TC_CREDIT',
                class: 'b3 semibold',
                country: 'mx',
            }],
        });
    }
}