import { Component, ElementRef, OnInit } from '@angular/core';
import { CashStateMachineService } from '../cash/cash-state-machine.service';
import { StateDataService } from '../api/state-data.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { LoanService } from '../services/loan.service';

@Component({
    selector: 'app-offer',
    templateUrl: './offer.component.html',
    styleUrls: ['./offer.component.scss'],
})
export class OfferComponent extends PageComponent implements OnInit {
    private pageContent;
    private action;
    private pageContentUrl;

    constructor(
        protected elementRef: ElementRef,
        private loan: LoanService,
        private stateMachine: CashStateMachineService
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'OfferComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.onLinkButtonClicked();
    }

    public handleBackButton() {
        this.loan.returnToOffers();
        return true;
    }

    private async onLinkButtonClicked() {
        const payload = { offerId: await this.loan.getOfferData('offerId') };
        try {
            const response = await this.api.post('/loans/v2/api/begin-loan-application.php', payload);
            const success = response['valid'];
            if (success) {
                this.api.logEvent('began loan application', payload);
                await this.loan.goToOffer();
            } else {
                this.stateMachine.setError(
                    'Error getting offers: ' + response['message'],
                    StateDataService.ERROR_CODES.BEGIN_LOAN_APPLICATION);
            }
        } catch (error) {
            this.stateMachine.setError(
                'Error occurred on post getOffers: ' + error,
                StateDataService.ERROR_CODES.POST_BEGIN_LOAN_APPLICATION);
        }
    }
}