import { Injectable } from '@angular/core';
import { Platform } from '@ionic/angular';
import { OnboardingStateMachineService } from '../onboarding/onboarding-state-machine.service';
import { BackButtonService } from '../services/back-button.service';
import { HelpService } from '../services/help.service';

@Injectable()
export class OnboardingBackButtonService extends BackButtonService {

    constructor(
        protected platform: Platform,
        protected stateMachine: OnboardingStateMachineService,
        protected help: HelpService
    ) {
        super(platform, stateMachine, help);
    }

    handleBackButton() {
        if (super.handleBackButton()) {
            return true;
        }

        if (this.stateMachine.isDeclinedState()) {
            // todo: declined state back button
            // for now we will go to default behavior
            console.log('isDeclinedState back button');
        }

        if (this.stateMachine.getCurrentPage() === this.stateMachine.states.COMPLETE) {
            return true;
        }

        this.stateMachine.closeApp();
        return true;
    }
}
