import { Component, ElementRef, OnInit } from '@angular/core';
import { StateService } from '../../loan-application/state.service';
import { HelpService } from '../../services/help.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { CashStateMachineService } from '../../cash/cash-state-machine.service';


@Component({
    selector: 'app-cash-transfer',
    templateUrl: './cash-transfer.component.html',
    styleUrls: ['./cash-transfer.component.scss']
})
export class CashTransferComponent extends PageComponent implements OnInit {

    constructor(
        protected elementRef: ElementRef,
        public state: StateService,
        public stateMachine: CashStateMachineService,
        private help: HelpService,
    ) {
        super(elementRef);
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.navFooter.setCancelStyle();
        this.subscription.add(
            this.navFooter.$button.subscribe(val => {
                if (val === 'text-below' || val === 'cancel') {
                    this.returnToIntro();
                }
            })
        );
    }

    public getName(): string {
        return 'CashTransferComponent';
    }

    returnToIntro() {
        this.state.resetDisbursementMethod();
        this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_INTRO);
    }

    finishInformationTransfer() {
        if (this.state.isClabeNumberValid()) {
            this.stateMachine.updateCurrentSubPage(this.stateMachine.subStates.LOAN_APPLICATION_QUESTIONS);
        }
    }

    validateClabe() {
        if (this.state.isClabeNumberValid()) {
            this.stateMachine.enableNext();
            // ToDo: Add translate JSON string
            this.navFooter.setTextBelowStyle('Continuar')
            this.subscription.add(
                this.stateMachine.$nextButton.subscribe(val => {
                    this.finishInformationTransfer();
                })
            );
        }
    }

    clabeButtonClass() {
        if (!this.state.isClabeNumberCorrectLength()) {
            return 'fullButtonDisabled';
        }
        return 'fullButtonEnabled';
    }

    openHelp() {
        this.help.openHelp('general');
    }

}
