import { Injectable } from '@angular/core';
import { OnboardingStateMachineService } from '../onboarding/onboarding-state-machine.service';
import { StateDataService } from '../api/state-data.service';
import { PluginService } from '../api/plugin.service';
import { ApiService } from '../api/api.service';

@Injectable()
export class ActivationService {

    public static readonly STATUS = {
        ACTIVATED: 'activated',
        CANCELLED: 'cancelled',
        REJECTED: 'rejected'
    };

    constructor(
        private api: ApiService,
        private stateMachine: OnboardingStateMachineService,
        private stateData: StateDataService,
        private plugin: PluginService
    ) {
    }

    public async isActivated() {
        return this.plugin.isActivated();
    }

    public async activate() {
        let activationParameters;
        try {
            this.api.logEvent('ActivationStart');
            activationParameters = await this.plugin.activate();
            if (activationParameters === 'cancelled') {
                this.api.logEvent('ActivationCancelled',
                    { status: ActivationService.STATUS.CANCELLED });
                return ActivationService.STATUS.CANCELLED;
            }
        } catch (error) {
            this.stateMachine.setError(
                'Error occurred while activating: ' + error,
                StateDataService.ERROR_CODES.ACTIVATION);
            return ActivationService.STATUS.CANCELLED;
        }

        // Do late PJB setup on a Huawei EMUI 9.1 (Android 9) device, on which PJM can get
        // IMEI without needing PJB:
        try {
            this.api.logEvent('ActivationLateSetupPjbOnHuaweiStart');
            const result = await this.plugin.setupPjbOnHuawei();
        } catch (error) {
            this.api.logEvent('ActivationSetupPjbOnHuaweiFailed',
                { status: ActivationService.STATUS.REJECTED });
            return ActivationService.STATUS.REJECTED;
        }

        try {
            this.api.logEvent('ActivationDeviceAdminStart');
            const result = await this.plugin.requestDeviceAdmin();
        } catch (error) {
            this.api.logEvent('ActivationDeviceAdminFailed',
                { status: ActivationService.STATUS.REJECTED });
            return ActivationService.STATUS.REJECTED;
        }
        try {
            this.api.logEvent('ActivationSecureStart');
            const result = await this.plugin.secure(activationParameters);
        } catch (error) {
            this.api.logEvent('ActivationSecureFailed',
                { status: ActivationService.STATUS.REJECTED });
            return ActivationService.STATUS.REJECTED;
        }

        this.api.logEvent('ActivationComplete',
            { status: ActivationService.STATUS.ACTIVATED });
        return ActivationService.STATUS.ACTIVATED;
    }

}
