import { Component, ElementRef, OnDestroy, OnInit } from '@angular/core';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';
import { StateDataService } from '../../api/state-data.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { IdPhotoService } from '../../services/id-photo.service';

// get-user-info.php
@Component({
    selector: 'app-confirm-details',
    templateUrl: './confirm-details.component.html',
    styleUrls: ['./confirm-details.component.scss'],
})
export class ConfirmDetailsComponent extends PageComponent implements OnInit, OnDestroy {
    public detailsLoaded = false;

    public customerDetails = {
        address: ''
    };

    constructor(
        public stateMachine: OnboardingStateMachineService,
        protected elementRef: ElementRef,
        private idPhoto: IdPhotoService,
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'ConfirmDetailsComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.stateMachine.disableNext();
        this.detailsLoaded = false;
        this.stateMachine.endTransition();
        this.updateStyle();
        this.getIdDetails();

        this.subscription.add(
            this.stateMachine.$nextButton.subscribe(val => {
                this.idConfirmContinue();
                this.stateMachine.presentLoading();
            })
        );

        this.subscription.add(
            this.navFooter.$button.subscribe(val => {
                if (val === 'text-above') {
                    this.idPhoto.promptConfirmReview();
                }
            })
        );
    }

    public ngOnDestroy() {
        super.ngOnDestroy();
        this.stateMachine.closeLoading();
        this.stateMachine.clearCurrentSubPage();
    }

    public async incorrectClicked() {
        await this.idPhoto.incorrectClicked();
        this.updateStyle();
    }

    public maskId(text) {
        return text.replace(/.(?=.{3,}$)/g, '*');
    }

    private async getIdDetails() {
        try {
            const response = await this.api.getUserInfo();
            const success = response['valid'];
            if (success) {
                const data = response['data'];
                if (data) {
                    this.customerDetails = data;
                    this.detailsLoaded = true;
                    this.stateMachine.enableNext();
                    this.api.logEvent('address loaded');
                }
            } else {
                this.stateMachine.setError(
                    'Error occurred in getIdDetails: ' + response['message'],
                    StateDataService.ERROR_CODES.GET_ID_DETAILS);
            }
        } catch (error) {
            this.stateMachine.setError(
                'Error occurred on post getIdDetails: ' + error,
                StateDataService.ERROR_CODES.POST_GET_ID_DETAILS);
        }
    }

    private async idConfirmContinue() {
        if (this.stateMachine.requestingManualReview()) {
            await this.stateData.set(StateDataService.KEYS.USER_SHOWN_REVIEW_SCREEN, 1);
            this.stateMachine.incrementPage();
            this.stateMachine.closeLoading();
        } else {
            this.stateData.set(StateDataService.KEYS.USER_APPROVED_ID_DATA, 1);
            if (await this.stateData.get(StateDataService.KEYS.SELFIE_ACCEPTED)) {
                this.stateMachine.markTaskComplete(StateDataService.KEYS.TASK_ID_VERIFICATION);
                this.stateMachine.beginTransition();
                setTimeout(function () {
                    this.stateMachine.incrementPage();
                }.bind(this), 2000);
            } else {
                this.stateMachine.incrementPage();
                this.stateMachine.closeLoading();
            }
        }
    }

    private updateStyle() {
        if (this.stateMachine.requestingManualReview()) {
            this.navFooter.setNextStyle('NAVIGATION.GO_TO_NEXT', 'light');
        } else {
            this.navFooter.setTextAboveStyle('BUTTON.DATA_OK');
        }
    }
}