import { Injectable } from '@angular/core';
// XXX 20200925: Native integration disabled until we can get our app in the Play Store
// import { Facebook } from '@ionic-native/facebook/ngx';
import { StateDataService } from '../api/state-data.service';
import { ApiService } from '../api/api.service';
import { Facebook } from 'ng2-cordova-oauth/core';
import { OauthCordova } from 'ng2-cordova-oauth/platform/cordova'

@Injectable({
    providedIn: 'root'
})
export class FacebookService {

    private readyToConnect = false;
    private loggedIn;

    private oauth: OauthCordova = new OauthCordova();
    private facebookProvider: Facebook = new Facebook({
        clientId: '894331790632986', // Note: Facebook App Id is *not* sensitive and can be published
        appScope: ['public_profile', 'email', 'user_friends'],
        redirectUri: 'https://payjoy.com/loans/v2/api/facebook-login-success.html',
    });

    constructor(
        private stateData: StateDataService,
        private api: ApiService
    ) {
        this.loggedIn = false;
    }

    public isReadyToConnect() {
        return this.readyToConnect;
    }

    public setReadyToConnect(readyToConnect) {
        this.readyToConnect = readyToConnect;
    }

    public async isLoggedIn() {
        if (!this.loggedIn) {
            const facebookUserId = await this.stateData.get(StateDataService.KEYS.FACEBOOK_USER_ID);
            if (facebookUserId) {
                this.loggedIn = true;
            } else {
                console.log('not logged in');
                this.loggedIn = false;
            }
        }
        return this.loggedIn;
    }

    // adapted from  https://ionicthemes.com/tutorials/about/ionic-facebook-login
    public async login() {
        console.log('FacebookService.login');

        // XXX 20200925: Native integration disabled until we can get our app in the Play Store
        // const permissions = ['public_profile', 'email', 'user_friends'];
        // const response = await this.fb.login(permissions);
        // const facebookUserId = response.authResponse.userID;
        // const accessToken = response.authResponse.accessToken;

        const response = await this.oauth.logInVia(this.facebookProvider) as any;

        const facebookUser = await this.getUserInfo(response.access_token) as any;
        console.log('RESULT: ' + JSON.stringify(facebookUser));

        this.loggedIn = true;
        await this.stateData.set(StateDataService.KEYS.FACEBOOK_USER_ID, facebookUser.id);
        this.api.logEvent('logged into Facebook', { userId: facebookUser.id });
        return {
            id: facebookUser.id,
            accessToken: response.access_token
        };
    }

    private async getUserInfo(accessToken) {
        const parameters = {
            access_token: accessToken
        };

        return this.api.externalGet(
            'https://graph.facebook.com/me', parameters);
    }

    // when logging in with facebook credentials should always get a userId
    public async registerPayjoyFacebook(credentials) {
        await this.stateData.set(StateDataService.KEYS.IS_FACEBOOK_ENABLED, 1);
        const userId = await this.stateData.get(StateDataService.KEYS.USER_ID);
        const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);
        const response = await this.api.post(
            '/loans/v2/api/register-user-cash.php', {
                facebookId: credentials.id,
                accessToken: credentials.accessToken,
                userId,
                deviceTag
            }
        );

        console.log(response);
        if (response.hasOwnProperty('data')) {
            const data = response['data'];
            await this.stateData.set(StateDataService.KEYS.USER_ID, data.userId);
            await this.stateData.set(StateDataService.KEYS.PROFILE_ID, data.cashPlatformProfileId);
            console.log('registerPayjoyFacebook done', response['data'].userId);
        } else {
            throw new Error(JSON.stringify(response));
        }
    }

}