import { Injectable } from '@angular/core';

const AMOUNT = 'amount';
const CANCELED = 'Canceled';
const COMPLETED = 'Completed';
const COOLDOWN_SECONDS_REMAINING = 'cooldownSecondsRemaining';
const DISPLAY = 'display';
const MILLISECONDS = 1000;
const DAY_MILLISECONDS = 3600 * MILLISECONDS * 24;
const PAYMENT_TYPE = 'paymentType';
const STATUS = 'status';


@Injectable({
    providedIn: 'root'
})

export class LoanInfo {
    nextPaymentDatetime: Date = null;
    validThrough: number = null;
    cooldownEndDatetime: Date = null;
    nextPaymentAmount: number = null;
    loanCurrency: string = null;
    loanTotalAmount: number = null;
    loanRemainingAmount: number = null;
    loanInProgress: boolean = null;
    loanCompleted: boolean = null;
    noLoanHistory: boolean = null;
    inCooldown: boolean = null;
    paymentsSoFar: number = null;
    totalNumOfPayments: number = null;
    paymentFrequency: string = null;
    term: number = null;
    termUnits: string = null;
    withdrawalLink: string = null;
    disbursementInfo: Object = null;
    financeStatus: string = null;
    financeProduct: string = null;
    financeOrderId: number = null;
    feedbackSurveyCompleted: boolean = null;

    public static readonly DISBURSEMENT_TYPES = {
        CASH_TRANSFER: 'CashTransfer',
        WALMART_GIFT_CARD: 'WalmartGiftCard',
        WINDOW_PICKUP: 'WindowPickup'
    };

    public static readonly DISBURSEMENT_STATUS_TYPES = {
        COMPLETED: 'Completed',
        CANCELED: 'Canceled',
        READY: 'Ready',
        OPEN: 'Open'
    };

    public initialize(loanStatusResponse, validThrough = null) {
        this.disbursementInfo = loanStatusResponse.disbursementInfo ? loanStatusResponse.disbursementInfo : null;

        if (loanStatusResponse.fail || !loanStatusResponse.hasPaymentProgress) {
            this.loanInProgress = false;
            this.noLoanHistory = true;
            return;
        }

        this.loanInProgress = loanStatusResponse.loanInProgress;
        this.financeProduct = loanStatusResponse.financeProduct;
        this.financeOrderId = loanStatusResponse.financeOrderId;
        this.loanCurrency = loanStatusResponse.currency;
        this.paymentFrequency = loanStatusResponse.paymentFrequency;
        this.term = this.intValue(loanStatusResponse.term);
        this.termUnits = loanStatusResponse.termUnits;
        this.financeOrderId = loanStatusResponse.financeOrderId;
        this.feedbackSurveyCompleted = loanStatusResponse.feedbackSurveyCompleted;

        this.withdrawalLink = (loanStatusResponse.disbursementInfo && loanStatusResponse.disbursementInfo.helpLink) ?
            loanStatusResponse.disbursementInfo.helpLink :
            null;

        if (this.loanInProgress) {
            this.nextPaymentAmount = this.floatValue(loanStatusResponse.nextPaymentAmount);
        }

        this.loanTotalAmount = this.floatValue(loanStatusResponse.totalCost);
        this.loanRemainingAmount = this.floatValue(loanStatusResponse.remainingBalance);
        this.loanCompleted = (this.loanRemainingAmount === 0);
        this.paymentsSoFar = this.intValue(loanStatusResponse.paymentsSoFar);
        this.totalNumOfPayments = this.intValue(loanStatusResponse.totalNumOfPayments);

        this.updateValidThrough(validThrough);

        if (loanStatusResponse[COOLDOWN_SECONDS_REMAINING]) {
            const cooldownSecondsRemaining = this.intValue(loanStatusResponse.cooldownSecondsRemaining);
            if (cooldownSecondsRemaining !== null && cooldownSecondsRemaining > 0) {
                this.inCooldown = true;
                this.cooldownEndDatetime = new Date((new Date().getTime() / 1000 + cooldownSecondsRemaining) * 1000);
            }
        } else {
            this.inCooldown = false;
        }
    }

    private floatValue(val) {
        if (val !== undefined) {
            return parseFloat(val);
        }
        return null;
    }

    private intValue(val) {
        if (val !== undefined) {
            return parseInt(val);
        }
        return null;
    }

    public formatPaymentAmount(payment) {
        if (Math.round(payment) !== payment) {
            return payment.toFixed(2);
        } else {
            return payment;
        }
    }

    public updateValidThrough(timestamp) {
        if ((timestamp !== undefined) && (timestamp !== null) && !isNaN(timestamp)) {
            this.validThrough = timestamp;
            this.nextPaymentDatetime = new Date(timestamp * MILLISECONDS);
        }
    }

    public loanTotal() {
        if (this.loanTotalAmount) {
            return this.formatPaymentAmount(this.loanTotalAmount);
        }
        return 0;
    }

    public paidSoFar() {
        if (this.loanTotalAmount && this.loanRemainingAmount) {
            return this.formatPaymentAmount(this.loanTotalAmount - this.loanRemainingAmount);
        }
        return null;
    }

    public getDisbursementAmount() {
        if (this.disbursementInfo) {
            return this.disbursementInfo[AMOUNT];
        }
        return null;
    }

    public isCanceled() {
        return (this.financeStatus === LoanInfo.DISBURSEMENT_STATUS_TYPES.CANCELED);
    }

    public isCashTransfer() {
        return (this.disbursementInfo && (this.disbursementInfo[PAYMENT_TYPE] === LoanInfo.DISBURSEMENT_TYPES.CASH_TRANSFER));
    }

    public isToday() {
        return (this.nextPaymentDatetime && (this.nextPaymentDatetime.toDateString() === new Date().toDateString()));
    }

    public isTomorrow() {
        const tomorrow = new Date();
        tomorrow.setDate(new Date().getDate() + 1);

        return (this.nextPaymentDatetime && (this.nextPaymentDatetime.toDateString() === tomorrow.toDateString()));
    }


    public isPastDue() {
        return (this.loanInProgress && this.validThrough * MILLISECONDS < Date.now());
    }

    public isWalmartGiftCardDisbursement() {
        return (this.disbursementInfo && (this.disbursementInfo[PAYMENT_TYPE] === LoanInfo.DISBURSEMENT_TYPES.WALMART_GIFT_CARD));
    }

    public isWindowPickUpDisbursement() {
        return (this.disbursementInfo && (this.disbursementInfo[PAYMENT_TYPE] === LoanInfo.DISBURSEMENT_TYPES.WINDOW_PICKUP));
    }

    public daysLeft() {
        if (this.nextPaymentDatetime) {
            return Math.round((this.nextPaymentDatetime.getTime() - new Date().getTime()) / (DAY_MILLISECONDS));
        }
        return '';
    }

    public nextPaymentDue() {
        if (this.nextPaymentAmount) {
            return this.formatPaymentAmount(this.nextPaymentAmount);
        }
        return 0;
    }

    public paymentAmountLeft() {
        if (this.loanRemainingAmount) {
            return this.formatPaymentAmount(this.loanRemainingAmount);
        }
        return 0;
    }

    public paymentsRemaining() {
        return (this.totalNumOfPayments - this.paymentsSoFar);
    }

    public isDisbursed() {
        return (
            this.disbursementInfo &&
            this.disbursementInfo[STATUS] === LoanInfo.DISBURSEMENT_STATUS_TYPES.COMPLETED
        );
    }

    public isLoanInProgress() {
        return this.loanInProgress;
    }

    public isLoanCompleted() {
        if (this.isLoanInProgress()) {
            return false;
        } else {
            return this.loanCompleted;
        }
    }

    public displayDisbursementInfo() {
        if (!this.isDisbursed() && this.withdrawalLink && this.disbursementInfo.hasOwnProperty(DISPLAY)) {
            return (this.disbursementInfo[DISPLAY]);
        }
        return false;
    }

    public disbursementIsReady() {
        return [
            LoanInfo.DISBURSEMENT_STATUS_TYPES.READY,
            LoanInfo.DISBURSEMENT_STATUS_TYPES.OPEN
        ].includes(this.disbursementInfo[STATUS])
    }


}