import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import { Appsflyer } from '@ionic-native/appsflyer/ngx';
import { StateDataService } from './state-data.service';

import { environment } from '@app/env';

const APPSFLYER_DEV_KEY = 'Q8KY6xQaJwjBm7Q8iGU2Db';


@Injectable({
    providedIn: 'root'
})

export class AppsFlyerService {

    constructor(private appsFlyer: Appsflyer,
        private stateData: StateDataService,
        private api: ApiService
    ) {}


    public async initialize() {

        var appsFlyerUID = await this.stateData.get(StateDataService.KEYS.APPSFLYER_UID);

        if (appsFlyerUID) {
            console.log('appsflyer stored id is: ' + appsFlyerUID);
            return false;
        }

        var response = await this.appsFlyer.initSdk({
            devKey: APPSFLYER_DEV_KEY,
            isDebug: !environment.production
        });

        appsFlyerUID = await this.appsFlyer.getAppsFlyerUID();

        if (appsFlyerUID) {
            this.stateData.set(StateDataService.KEYS.APPSFLYER_UID, appsFlyerUID);
        } else {
            this.api.logError(StateDataService.ERROR_CODES.APPSFLYER_UUID_GET_ERROR, 'appsflyer could not be initialized.  No appsflyer uid saved.');
        }
        console.log('received id is: ' + appsFlyerUID);
        return true;
    }

}