import { Component } from '@angular/core';
import { LockStateMachineService } from '../lock-state-machine.service';

@Component({
    selector: 'app-progress-bar',
    templateUrl: './progress-bar.component.html',
    styleUrls: ['./progress-bar.component.scss', '../lock.scss'],
})
export class ProgressBarComponent {

    constructor(
        public stateMachine: LockStateMachineService,
    ) {
    }

    public getProgressPercentage() {
        return ((this.stateMachine.loanInfo.loanTotalAmount - this.stateMachine.loanInfo.loanRemainingAmount) / this.stateMachine.loanInfo.loanTotalAmount) * 100;
    }

    public getProgressCircle() {
        return Array.from(Array(this.stateMachine.loanInfo.totalNumOfPayments).keys()).slice(1);
    }

}