import { Injectable } from '@angular/core';
import { StateMachineService } from '../api/state-machine.service';
import { TranslateService } from '@ngx-translate/core';
import { StateDataService } from '../api/state-data.service';
import { PluginService } from '../api/plugin.service';
import { ApiService } from '../api/api.service';

@Injectable()
export class HelpService {
    private helpType;
    private lastPageCached;
    private lastSubPageCached;

    private whatsAppNumber = '525561046033';
    private whatsAppNumber_EC = '+5930994282220';
    private supportPhone = '+525553518341';

    constructor(
        private api: ApiService,
        private plugin: PluginService,
        private stateData: StateDataService,
        private stateMachine: StateMachineService,
        private translate: TranslateService
    ) {
    }

    public initialize() {
        this.stateData.set(StateDataService.KEYS.SUPPORT_NUMBER, this.supportPhone);
    }

    public getHelpType(): string {
        return this.helpType ? this.helpType : 'general';
    }

    public setHelpType(type): void {
        this.helpType = type;
    }

    public exitHelp(): void {
        this.stateMachine.updateCurrentPage(this.returnToPageFromHelp());
        this.stateMachine.updateCurrentSubPage(this.returnToSubPageFromHelp());
        this.setHelpBackButton(null);
        this.setHelpType('general');
        this.stateMachine.setHelpNeeded(false);
    }

    public openHelp(helpType): void {
        this.setHelpBackButton(this.stateMachine.getCurrentPage(), this.stateMachine.getCurrentSubPage());
        this.setHelpType(helpType);
        this.stateMachine.setHelpNeeded(true);
    }

    public returnToPageFromHelp(): string {
        return this.lastPageCached;
    }

    public returnToSubPageFromHelp(): string {
        return this.lastSubPageCached;
    }

    public setHelpBackButton(page, subpage = null): void {
        this.lastPageCached = page;
        this.lastSubPageCached = subpage;
    }


    public contactSupportTel(message) {
        this.api.logEvent(message, {
            currentPage: this.stateMachine.getCurrentPage(),
            action: 'contactSupportTel'
        });
        window.open('tel:' + this.supportPhone);
    }

    public async contactSupportWhatsApp(logMessage) {
        this.api.logEvent(logMessage, {
            currentPage: this.stateMachine.getCurrentPage(),
            action: 'contactSupportWhatsApp'
        });
        let deviceTagMessage = '';
        let errorMessage = '';
        const deviceTag = await this.stateData.get(StateDataService.KEYS.DEVICE_TAG);
        if (deviceTag) {
            deviceTagMessage = this.translate.instant('LOCK.DEVICE_TAG') + ': ' + deviceTag;
        }
        if (this.stateMachine.hasError()) {
            errorMessage = this.translate.instant('MESSAGE.ERROR_CODE') + ': ' + this.stateMachine.getCurrentErrorCode();
        }
        window.open('https://wa.me/' + this.stateMachine.processEcString(this.whatsAppNumber) + '?text=' + deviceTagMessage + '%0A' + errorMessage + '%0A', '_system');
    }


}
