import { Component, ElementRef, OnInit } from '@angular/core';
import { CashStateMachineService } from '../cash/cash-state-machine.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { LoanService } from '../services/loan.service';

@Component({
    selector: 'app-await-device-secure',
    templateUrl: './await-device-secure.component.html',
    styleUrls: ['./await-device-secure.component.scss'],
})
export class AwaitDeviceSecureComponent extends PageComponent implements OnInit {

    constructor(
        protected elementRef: ElementRef,
        private loan: LoanService,
        private stateMachine: CashStateMachineService
    ) {
        super(elementRef);
    }

    public getName(): string {
        return 'AwaitDeviceSecureComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.subscription.add(this.stateMachine.pollIncrementPage().subscribe());
        this.loan.goToOffer();
    }

    public handleBackButton() {
        return true;
    }

}