import { Component, ElementRef, OnInit } from '@angular/core';
import { OnboardingStateMachineService } from '../onboarding/onboarding-state-machine.service';
import { PageComponent } from '@app/pages/_page/page.component';

@Component({
    selector: 'app-error',
    templateUrl: './error.component.html',
    styleUrls: ['./error.component.scss'],
})
export class ErrorComponent extends PageComponent implements OnInit {
    public errorCode;

    constructor(
        protected elementRef: ElementRef,
        private stateMachine: OnboardingStateMachineService
    ) {
        super(elementRef);
        this.stateMachine.enableNext();
    }

    public getName(): string {
        return 'ErrorComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.stateMachine.endTransition();
        console.log('error component');
        this.subscription.add(
            this.navFooter.$button.subscribe(val => {
                if (val === 'try-again') {
                    this.retryError();
                }
            })
        );
        this.navFooter.setTryAgainStyle();
        this.errorCode = this.stateMachine.getCurrentErrorCode();
    }

    public async retryError() {
        this.stateMachine.clearError();
        if (await this.stateMachine.isLoggedIn()) {
            this.stateMachine.disableNext();
        } else {
            this.stateMachine.goToBeginning();
        }
    }
}
