import { AfterViewInit, Component, Injector, OnInit } from '@angular/core';
import { CashStateMachineService } from './cash-state-machine.service';
import { BackButtonService } from '../services/back-button.service';
import { NavFooterService } from '@app/components/nav-footer/nav-footer.service';
import { OnboardingStateMachineService } from '../onboarding/onboarding-state-machine.service';
import { IdPhotoService } from '../services/id-photo.service';
import { LoanService } from '../services/loan.service';
import { ActivationService } from '../services/activation.service';
import { StateMachineService } from '../api/state-machine.service';
import { OnboardingBackButtonService } from '../onboarding/onboarding-back-button-service';
import { HelpService } from '../services/help.service';
import { AppInjector } from '../services/app-injector.service';
import { StateDataService } from '../api/state-data.service';

@Component({
    selector: 'cash-root',
    templateUrl: 'cash.component.html',
    // Any services that are specific to app mode should be injected in
    // that mode's component to avoid ambiguity about what flavor of
    // the service should be injected. The default of using
    // `providedIn: root` for these services means they are created before
    // we know what app mode we're in.
    providers: [
        CashStateMachineService,
        OnboardingBackButtonService,
        { provide: StateMachineService, useExisting: CashStateMachineService },
        { provide: OnboardingStateMachineService, useExisting: CashStateMachineService },
        { provide: BackButtonService, useExisting: OnboardingBackButtonService },
        IdPhotoService,
        LoanService,
        ActivationService,
        HelpService]
})

export class CashComponent implements OnInit, AfterViewInit {
    constructor(
        public injector: Injector,
        public stateMachine: CashStateMachineService,
        public help: HelpService,
        private backButton: BackButtonService,
        private navFooter: NavFooterService,
        private stateData: StateDataService,
        private loan: LoanService,
    ) {
        AppInjector.injector = injector;
    }

    async ngOnInit() {
        this.navFooter.setHiddenStyle();
        this.stateMachine.initialize();
        this.backButton.initialize();
        this.help.initialize();
    }

    async ngAfterViewInit() {
        await this.stateMachine.determineAndSetMarket();
        this.stateMachine.checkDeviceSupported();
        this.stateMachine.checkLoanAvailability();
        this.checkProceedToPermissionsScreen();
    }

    async checkProceedToPermissionsScreen() {
        const loanContractIsSigned = await this.stateData.get(StateDataService.KEYS.LOAN_CONTRACT_IS_SIGNED);
        if (loanContractIsSigned) {
          await this.loan.goToOffer();
        }
      }
}