import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class NavFooterService {

    public static readonly STYLES = {
        HIDDEN: 'hidden',
        NEXT: 'next',
        FACEBOOK: 'facebook',
        START_ONBOARDING_BUTTON: 'start-onboarding-button',
        START_ONBOARDING_OFF: 'start-onboarding-off',
        ONBOARDING_LOADING: 'onboarding-loading',
        CHECKBOX: 'checkbox',
        TEXT_ABOVE: 'text-above',
        TEXT_BELOW: 'text-below',
        TRY_AGAIN: 'try-again',
        CANCEL_ACCEPT: 'cancel-accept',
        CANCEL: 'cancel',
        RETURN_TO_LOCK: 'return-to-lock',
    };

    public styles = NavFooterService.STYLES;
    public footerStyle = NavFooterService.STYLES.HIDDEN;
    public footerStyleChange = new Subject<string>();
    public nextText;
    public nextColor;
    public confirmDetailsButtonEnabled = true;
    private submitButtonEnabled = false;

    private checkbox = new Subject < any > ();
    public $checkbox = this.checkbox.asObservable();
    private button = new Subject < string > ();
    public $button = this.button.asObservable();

    constructor() {
        this.resetNextText();
        this.resetNextColor();
    }

    resetNextText() {
        this.setNextText(null);
    }

    setNextText(nextText) {
        if (nextText) {
            this.nextText = nextText;
        } else {
            this.nextText = 'NAVIGATION.CONTINUE';
        }
    }

    resetNextColor() {
        this.setNextColor(null);
    }

    setNextColor(nextColor) {
        if (nextColor) {
            this.nextColor = nextColor;
        } else {
            this.nextColor = 'enabled';
        }
    }

    public isConfirmDetailsButtonEnabled(): boolean {
        return this.confirmDetailsButtonEnabled;
    }

    public setConfirmDetailsButtonEnabled(val): void {
        this.confirmDetailsButtonEnabled = val;
    }

    public isSubmitButtonEnabled(): boolean {
        return this.submitButtonEnabled;
    }

    public setSubmitButtonEnabled(enabled): void {
        this.submitButtonEnabled = enabled;
    }

    setHiddenStyle() {
        this.setStyle(NavFooterService.STYLES.HIDDEN);
    }

    setNextStyle(overrideNextText = null, overrideNextColor = null) {
        this.setStyle(NavFooterService.STYLES.NEXT);
        this.setNextText(overrideNextText);
        this.setNextColor(overrideNextColor);
    }

    setFacebookStyle() {
        this.setStyle(NavFooterService.STYLES.FACEBOOK);
    }

    setConnectWithoutFacebookStyle() {
        this.setStyle(NavFooterService.STYLES.START_ONBOARDING_BUTTON);
    }

    setStartOnboardingOff() {
        this.setStyle(NavFooterService.STYLES.START_ONBOARDING_OFF);
    }

    setOnboardingLoadingStyle() {
        this.setStyle(NavFooterService.STYLES.ONBOARDING_LOADING);
    }

    setCheckboxStyle(overrideNextText = null) {
        this.setStyle(NavFooterService.STYLES.CHECKBOX);
        this.setNextText(overrideNextText);
    }

    setTextAboveStyle(overrideNextText = null) {
        this.setStyle(NavFooterService.STYLES.TEXT_ABOVE);
        this.setNextText(overrideNextText);
    }

    setTextBelowStyle(overrideNextText = null) {
        this.setStyle(NavFooterService.STYLES.TEXT_BELOW);
        this.setNextText(overrideNextText);
    }

    setTryAgainStyle() {
        this.setStyle(NavFooterService.STYLES.TRY_AGAIN);
    }

    setCancelAcceptStyle() {
        this.setStyle(NavFooterService.STYLES.CANCEL_ACCEPT);
    }

    setReturnToLockStyle() {
        this.setStyle(NavFooterService.STYLES.RETURN_TO_LOCK);
    }

    setCancelStyle() {
        this.setStyle(NavFooterService.STYLES.CANCEL);
    }

    fireCheckbox(e) {
        this.checkbox.next(e);
    }

    fireButton(destination) {
        this.button.next(destination);
    }

    private setStyle(style) {
        this.footerStyle = style;
        this.footerStyleChange.next(style);
    }


}