import { Injectable } from '@angular/core';
import { Platform } from '@ionic/angular';
import { StateMachineService } from '../api/state-machine.service';
import { HelpService } from './help.service';

@Injectable()
export class BackButtonService {

    private activeComponent = null;

    constructor(
        protected platform: Platform,
        protected stateMachine: StateMachineService,
        protected help: HelpService
    ) {
    }

    public initialize() {
        this.platform.ready().then(async () => {
            this.platform.backButton.subscribe(() => {
                this.handleBackButton();
            });
        });
    }

    public setActivePage(component) {
        this.activeComponent = component;
    }

    public removeActivePage() {
        this.activeComponent = null;
    }

    public handleBackButton() {
        if (this.stateMachine.isHelpNeeded()) {
            this.stateMachine.setHelpNeeded(false);
            this.help.exitHelp();
            return true;
        }

        if (this.stateMachine.hasError()) {
            this.stateMachine.closeApp();
            return true;
        }

        if (!this.stateMachine.hasInternetConnection()) {
            return true;
        }

        if (this.activeComponent && this.activeComponent.handleBackButton()) {
            return true;
        }

        return false;
    }
}
