import { Component, ElementRef, OnInit } from '@angular/core';
import { OnboardingStateMachineService } from '../../onboarding/onboarding-state-machine.service';
import { StateDataService } from '../../api/state-data.service';
import { PageComponent } from '@app/pages/_page/page.component';
import { HelpService } from '../../services/help.service';

@Component({
    selector: 'app-selfie',
    templateUrl: './selfie.component.html',
    styleUrls: ['./selfie.component.scss']
})
export class SelfieComponent extends PageComponent implements OnInit {
    public selfieSubmitted = false;
    public selfieProcessed: any;
    public image: any = this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.SELFIE_ENTRY);
    public retakeModal: any = false;
    public svgIllustrationClass = this.adjustClassByScreenType('svgIllustration');
    public photoContainerClass = this.adjustClassByScreenType('photoContainer');
    public currentErrorName: string = null;
    public serverErrorNamesMap = {
        "MULTIPLE_FACES_ON_SELFIE": "MULTIPLE_FACES_FOUND",
        "FACE_NOT_FOUND_ON_SELFIE": "CHECK_IMAGE_QUALITY",
        "FAILED_TO_CROP_FACE_FROM_SELFIE": "FACE_TOO_CLOSE",
        "FACE_OUT_OF_BOUNDS_ON_SELFIE": "FACE_TOO_CLOSE",
        "DEFAULT_SELFIE_ERROR": "CHECK_IMAGE_QUALITY",
    };

    constructor(
        public help: HelpService,
        public stateMachine: OnboardingStateMachineService,
        protected elementRef: ElementRef,
    ) {
        super(elementRef);
        if (this.stateData.getUserProgress(StateDataService.USER_PROGRESS_KEYS.SELFIE_ENTRY)) {
            this.stateMachine.enableNext();
        } else {
            this.stateMachine.disableNext();
        }
    }

    public getName(): string {
        return 'SelfieComponent';
    }

    public async ngOnInit() {
        super.ngOnInit();
        this.stateMachine.endTransition();
        this.navFooter.setNextStyle('NAVIGATION.CONTINUE');
        this.subscription.add(this.stateMachine.$nextButton.subscribe(val => {
            this.selfieSubmitted = true;
            this.selfieProcessed = false;
            this.stateMachine.disableNext();
            this.postPhoto()
        }));
    }

    public handleOpenCamSuccess(imageDataUrl: string) {
        // imageData is a base64 encoded string
        this.setImage(imageDataUrl);
        this.stateMachine.enableNext();

    }

    public handleOpenCamError(err: string) {
        console.log('error ' + err);
    }

    public retakePhoto() {
        this.setImage('');
        this.selfieProcessed = true;
        this.retakeModal = false;
    }

    private setImage(selfie) {
        this.image = selfie;
        this.stateData.setUserProgress(StateDataService.USER_PROGRESS_KEYS.SELFIE_ENTRY, selfie);
    }

    private async postPhoto() {
        const data = {
            userId: String(await this.stateData.get(StateDataService.KEYS.USER_ID)),
            cashPlatformProfileId: String(await this.stateData.get(StateDataService.KEYS.PROFILE_ID)),
        };
        console.log('selfie to send');
        this.stateMachine.disableNext();
        try {
            const response = await this.api.postImages(
                '/camera/upload-selfie-handler-router.php', data, [this.image], ['selfie']
            );
            console.log('selfie sent');
            const success = response['valid'];
            if (success) {
                const outcome = response['outcome'];
                if (outcome === 'Retake') {
                    this.retakeModal = true;
                    console.log("Selfie retake required");
                    this.currentErrorName = response["errorName"] || null;
                    console.log("Error name: "+this.currentErrorName);
                } else {
                    this.stateData.set(StateDataService.KEYS.SELFIE_ACCEPTED, 1);
                    if (this.getUserApprovedData()) {
                        this.stateMachine.markTaskComplete(StateDataService.KEYS.TASK_ID_VERIFICATION);
                        this.stateMachine.beginTransition();
                    }
                    this.stateMachine.incrementPage();
                }
            } else {
                this.stateMachine.setError(
                    'selfie upload failed: ' + response['message'],
                    StateDataService.ERROR_CODES.UPLOAD_SELFIE_HANDLER);
                this.retakePhoto();
            }
        } catch (error) {
            this.stateMachine.setError(
                'Error occurred on post upload-selfie-handler: ' + error,
                StateDataService.ERROR_CODES.POST_UPLOAD_SELFIE_HANDLER);
            this.retakePhoto();
        }
    }

    public getErrorNameString() {
        const errorNameStringkey = this.serverErrorNamesMap[this.currentErrorName] || this.serverErrorNamesMap.DEFAULT_SELFIE_ERROR;
        return `SELFIE.${errorNameStringkey}`;
    }

    private async getUserApprovedData() {
        return this.stateData.get(StateDataService.KEYS.USER_APPROVED_ID_DATA);
    }
}